<template>
    <VueFileAgent
        id="vuefileagent"
        class="mx-auto"
        ref="profilePicRef"
        uploadWithCredentials
        :averageColor="false"
        :multiple="false"
        :disabled="isLoading"
        :deletable="true"
        :meta="false"
        :compact="true"
        :accept="'image/*'"
        :helpText="$t('help_texts.dragndrop_here')"
        v-model="profilePic"
        @mouseenter.native="hoverOn=true"
        @mouseleave.native="hoverOn=false"
        @select="onSelect($event)" >

        <template v-slot:before-outer>
        </template >
        <template #file-preview-new v-if="avatar">
            <div key="1" class="relative">
                <img class="rounded-lg shadow object-cover" width="154" height="154" :src="avatar" />
                <div :class="hoverOn ? 'opacity-75' : 'opacity-0'" class="transition-opacity duration-200 absolute top-0 bg-primary rounded-full" style="width: 154px; height: 154px;">
                    <div class="flex justify-center items-center h-full z-40">
                        <svg class="h-12 text-white" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M3 9a2 2 0 012-2h.93a2 2 0 001.664-.89l.812-1.22A2 2 0 0110.07 4h3.86a2 2 0 011.664.89l.812 1.22A2 2 0 0018.07 7H19a2 2 0 012 2v9a2 2 0 01-2 2H5a2 2 0 01-2-2V9z"></path><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 13a3 3 0 11-6 0 3 3 0 016 0z"></path></svg>
                    </div>
                </div>
            </div>
        </template>
        <template v-slot:after-inner>
            <a
                v-if="!profilePic"
                class="text-sm"
                color="primary"
                type="border">
                {{ selectBtn }}
            </a>
            <vs-button
                v-else
                type="filled"
                class="mx-0 my-0 text-md w-full p-2"
                color="primary"
                style="z-index: 999"
                :disabled="isLoading"
                @click="upload">
                <div class='w-full'>
                    <span :class='isLoading && "opacity-0"'>{{ $t('buttons.upload') }}</span>
                    <div class="w-full absolute h-full top-0">
                        <div class="flex justify-center  h-full">
                            <svg v-if="isLoading" class="circle__svg w-6" viewBox="0 0 100 100">
                                <circle class="circle__svg-circle" cx="50" cy="50" r="45"/>
                            </svg>
                        </div>
                    </div>
                </div>
            </vs-button>
        </template >
    </VueFileAgent>
</template>

<script>
import Vue from 'vue'
import VueFileAgent from 'vue-file-agent'
import 'vue-file-agent/dist/vue-file-agent.css'

Vue.use(VueFileAgent)

export default {
    data: () => ({
        uploaded: false,
        hoverOn: false,
        profilePic: null,
        isLoading: false
    }),
    props: ['avatartop', 'urltop'],


    computed: {
        avatar () {
            return this.avatartop || this.$store.state.auth.userContext.avatar
        },
        fingerprint () {
            return this.$root.syncManager.fingerprint
        },
        selectBtn () {
            return this.avatar ? this.$t('buttons.change_avatar') : this.$t('buttons.select_avatar')
        },
        uploadHeaders () {
            return {
                Range: 'post',
                Pragma: this.fingerprint
            }
        },
        uploadUrl () {
            return this.urltop || this.$root.spec.post_uploadAvatar.url
        },
    },

    mounted () {
        window.ii = this
    },

    methods: {
        removePic () {
            this.profilePic = null
            this.uploaded = false
        },
        async upload () {
            this.isLoading = true
            const uploadToCDNResp = (await this.$refs.profilePicRef.upload(this.uploadUrl, this.uploadHeaders, [this.profilePic]))[0]
            if (uploadToCDNResp.status === 200) {
                const avatar = uploadToCDNResp.data.cdn_url
                const payload = { avatar }
                const updateAvatarURLResp = await this.$parent.onUpdatedPersonal([payload, ()=>{}])
                if (updateAvatarURLResp.status === 200) {
                    this.notifySuccess(this.$t('notifs.avatar_updated'))
                }
            }
            this.isLoading = false
            this.uploaded = true
        },
        onSelect () {
            this.uploaded = false
            document.querySelector('.file-delete').addEventListener('click', this.removePic)
        }
    }
    
}
</script>

