  <div id="data-list-list-view" class="data-list-container">


    <vs-table ref="table" multiple v-model="selected" pagination :max-items="itemsPerPage" search :data="products">

      <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">

        <div class="flex flex-wrap-reverse items-center data-list-btn-container">

          <!-- ACTION - DROPDOWN -->
          
          <vs-dropdown vs-trigger-click class="dd-actions cursor-pointer ml-6 mr-4 mb-4">
            
            <vs-button class="p-3 shadow-drop rounded-lg cursor-pointer flex items-center justify-center text-lg font-medium w-32 w-full">
              <span class="mr-2">Actions</span>
              <feather-icon icon="ChevronDownIcon" svgClasses="h-4 w-4" />
            </vs-button>

            <vs-dropdown-menu>

