                </span>
              </vs-dropdown-item>

              <vs-dropdown-item>
                <span class="flex items-center">
                  <feather-icon icon="FileIcon" svgClasses="h-4 w-4" class="mr-2" />
                  <span>Print</span>
                </span>
              </vs-dropdown-item>

              
            </vs-dropdown-menu>
          </vs-dropdown>

          <!-- ADD NEW -->
          <vs-button color="danger" class="p-3 mb-4 mr-4 cursor-pointer flex items-center justify-center text-lg font-medium" @click="addNewData">
              <feather-icon icon="PlusIcon" svgClasses="h-4 w-4" />
              <span class="ml-2 text-base">Add New</span>
              
          </vs-button>
        </div>

        
      </div>

      <template slot="thead">
        <vs-th sort-key="name">Date</vs-th>
        <vs-th sort-key="id">ID</vs-th>
        <vs-th sort-key="client">Client</vs-th>
        <vs-th sort-key="value">Value</vs-th>
        <vs-th sort-key="value-tax">Value TAX</vs-th>
        <vs-th>Action</vs-th>
      </template>

        <template slot-scope="{data}">
          <tbody>
            <vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">

              <vs-td>
                <p class="product-name font-medium truncate">{{ tr.name }}</p>
              </vs-td>

              <vs-td>
                <p class="product-category">{{ tr.id | title }}</p>
              </vs-td>

              <vs-td>
                <p class="product-price">{{ tr.client }}</p>
              </vs-td>

              <vs-td>
                <p class="product-price">{{ tr.value }}</p>
              </vs-td>

              <vs-td>
                <p class="product-price">{{ tr.valueTax }}</p>
              </vs-td>
              
              <vs-td class="whitespace-no-wrap">
                <feather-icon icon="EditIcon" svgClasses="w-5 h-5 hover:text-warning stroke-current" @click.stop="editData(tr)" />
                <feather-icon icon="MailIcon" svgClasses="w-5 h-5 hover:text-primary stroke-current" class="ml-2" @click.stop="smsData(tr.id)" />
                <feather-icon icon="AtSignIcon" svgClasses="w-5 h-5 hover:text-primary stroke-current" class="ml-2" @click.stop="emailData(tr.id)" />
                <feather-icon icon="PrinterIcon" svgClasses="w-5 h-5 hover:text-primary stroke-current" class="ml-2" @click.stop="printData(tr.id)" />
                <feather-icon icon="XIcon" svgClasses="w-5 h-5 hover:text-danger stroke-current" class="ml-2" @click.stop="printData(tr.id)" />
              </vs-td>

            </vs-tr>
          </tbody>
        </template>
    </vs-table>
  </div>
</template>

<script>
export default {
  data () {
    return {
      selected: [],
      // products: [],
      itemsPerPage: 10,
      isMounted: false,

      // Data Sidebar
      addNewDataSidebar: false,
      sidebarData: {}
    }
  },
  computed: {
    currentPage () {
      if (this.isMounted) {
        return this.$refs.table.currentx
      }
      return 0
    },
    products () {
      return this.$store.state.dataList.products
    },
    queriedItems () {
      return this.$refs.table ? this.$refs.table.queriedResults.length : this.products.length
    }
  },
  methods: {
    addNewData () {
      this.sidebarData = {}
      this.toggleDataSidebar(true)
    },
    deleteData (id) {
      this.$store.dispatch('dataList/removeItem', id).catch(err => { console.error(err) })
    },
    editData (data) {
      // this.sidebarData = JSON.parse(JSON.stringify(this.blankData))
      this.sidebarData = data
      this.toggleDataSidebar(true)
    },
    toggleDataSidebar (val = false) {
      this.addNewDataSidebar = val
    }
  },
  created () {
  },
  mounted () {
    this.isMounted = true
  }
}
</script>

