<template>
<vs-sidebar
    ref="sidebar"
    position-right
    vs-custom-content
    click-not-close
    parent="body"
    default-index="1"
    color="primary"
    class="sidebarx add-new-procedure"
    spacer
    v-model="sidebarActive">
    
    <div class="header-sidebar" slot="header">
        <div class="flex justify-between w-full h-full bg-primary text-white">
            <h5 class="pl-4 flex self-center text-white font-light">{{ $t('headers.add_proc') }} </h5>
            <feather-icon class="pr-4 text-white cursor-pointer" svgClasses="w-6 h-6" @click="sidebarActive=false" icon="XIcon"/>
        </div>
    </div>
    <div id="laima-form-container">
        <laima-form
            ref="lf"
            size="normal"
            :key="laimaFormKey"
            :modelsContainer="initialData"
            :formFields="slotFields"
            :onSubmit="submitProcedure">
            <template #footer="{callback}">
                <vs-button @click="callback" class="w-full rounded-none" size="large" type="filled" color="primary">{{ $t('buttons.save') }}</vs-button>
            </template>
        </laima-form>
    </div>
</vs-sidebar>
</template>


<script>

export default {
    props: {
        addNewProcedure: {
            default: false
        }
    },
    methods: {
        closeSideBar () {
            this.sidebarActive = false
            this.laimaFormKey++
        },
        submitProcedure (payload) {
            if (payload.maxprice != 0 && parseInt(payload.minprice) > parseInt(payload.maxprice)) {
                return this.$refs.lf.$validator.errors.add({
                    scope: 'step-1',
                    field: 'maxprice',
                    msg: this.$t(
                        'misc.must_be_gte', {
                            field: this.$t('headers.max_price').toLowerCase(),
                            otherField: this.$t('headers.min_price').toLowerCase()
                        }
                    )
                })
            }
            this.$store.dispatch('core/addNewProcedure', [payload, this])
        }
    },

    computed: {
        initialData () {
            return {
                duration: this.default_visit_interval
            }
        },
        slotFields () {
            return [
                {
                    name: 'category',
                    label: this.$t('fields.procedure_category'),
                    options: this.categories,
                    type: 'select',
                    validators: {required: true}
                },
                {
                    name: 'name',
                    label: this.$t('fields.proc_name'),
                    placeholder: this.$t('misc.proc_example'),
                    type: 'text',
                    validators: {required: true}
                },
                {
                    name: 'minprice',
                    label: this.$t('headers.min_price') + ` (${this.currency})`,
                    type: 'number',
                    placeholder: '0',
                    validators: {required: true, numeric: true, min_value: 5, max_value: 10000}
                },
                {
                    name: 'maxprice',
                    helpText: this.$t('misc.maxprice_note'),
                    label: this.$t('headers.max_price') + ` (${this.currency})`,
                    type: 'number',
                    placeholder: '0',
                    validators: {required: true, numeric: true, min_value: 0, max_value: 10000}
                },
                {
                    name: 'duration',
                    label: this.$t('fields.duration'),
                    helpText: this.$t('headers.est_proc_duration'),
                    type: 'fancyNumber',
                    classes: ['duration-input'],
                    step: this.default_visit_interval,
                    min: this.default_visit_interval,
                    validators: {required: true}
                },
                {
                    name: 'tax',
                    label: this.$t('headers.tax_rate'),
                    placeholder: '0',
                    type: 'number',
                    validators: {required: true, numeric: true, min_value: 0, max_value: 90}
                },

            ]
        },

        sidebarActive: {
            get () {
                return this.addNewProcedure
            },
            set (val) {
                this.$emit('update:addNewProcedure', val)
            }
        },

        categories () {
            return Object.keys(this.$store.state.core.procedureTemplates).map(i => ({text: i, value: i}))
        },

        clinicID () {
            return this.$store.state.auth.selectedWorkspace.id
        },

        clinic () {
            return this.$store.state.core.clinics.find(item => item.id == this.clinicID)
        },

        currency () {
            return this.clinic.currency || this.$store.state.auth.currency
        },

        default_visit_interval () {
            return this.clinic.default_visit_interval
        },

    },
    data () {
        return {
            laimaFormKey: 0
        }
    },
    mounted () {
        window.yy = this
    },
}
</script>


