<template>
<div class="scheduler-searchbar">
    <div class="flex patient-searchbar h-full">
        <!-- <feather-icon class="cursor-pointer pr-2" :icon="selectedPatient ? 'FilterIcon' : 'SearchIcon'" svgClasses="h-5 w-5" /> -->
        <feather-icon class="cursor-pointer pr-2" icon="SearchIcon" svgClasses="h-5 w-5" />
        <div class="selected-patient-mask absolute h-full top-0 bg-white ml-6" style="width: 240px; z-index: 50000" v-if="selectedPatient">
            <div class="selected-patient flex h-full">
                <h3 class="self-center pl-4" style="font-weight: 300; font-size: 1.2rem;"> {{ selectedPatient.name }} {{ selectedPatient.last_name}}</h3>
                <div class='closure absolute right-0 h-full'>
                    <div class="flex h-full">
                        <feather-icon icon="XIcon" svgClasses="w-5 h-5 hover:text-danger pointer-cursor" class="self-center" @click="closeSelectedPatient"/>
                    </div>
                </div>
            </div>
        </div>
        <input 
            autocomplete="false"
            class="border-none w-full pl-3"
            :class="selectedPatient && 'opacity-0'"
            type="text"
            :placeholder="$t('placeholders.patient_searchbar_prompt')"
            v-model="query"
            v-debounce:300ms="submitPhrase"
            id="patient-searchbar-input"
            @blur="onBlur"
            @input="query && query.length >= 2 && $store.commit('core/SET_PATIENT_SEARCHBAR_LOADING', true)"
            @click="$emit('on')" />
        <div class="relative h-full" style="width: 50px" v-if="isLoadingPatients">
            <div class="loading default">
                <div class="effect-1 effects"></div>
                <div class="effect-2 effects"></div>
                <div class="effect-3 effects"></div>
            </div>
        </div>
    </div>
    
    <div class="results absolute bg-white rounded-lg mt-6 shadow-md" v-if="query.length >= 2 && searchedPatients[0]">
        <div class="flex font-light p-2 cursor-pointer patient-line" v-for="patient in searchedPatients" :key="patient.id" @click="selectPatient(patient.id)">
            <feather-icon icon="UserIcon" svgClasses="h-5 w-5" class="pl-1 pr-3"/>
            <ul>
                <li class="patient-name" v-html="embolden(patient.name + ' ' + patient.last_name)"></li>
                <li class="patient-id" v-html="embolden(patient.patient_id)"></li>
            </ul>
        </div>
    </div>

</div>
</template>

<script>
export default {
    data () {
        return {
            query: '',
            isActive: false,
        }
    },
    computed: {
        isDisabled () {
            return false
        },
        isLoadingPatients () {
            return this.$store.state.core.patientSearchbarLoading
        },
        searchedPatients () {
            return this.$store.state.core.searchedPatients || []
        },
        selectedPatient () {
            return this.$store.state.core.filteredPatient
        }

    },
    methods: {
        embolden (string) {
            let query_len = this.query.length
            let first_idx = string.toLowerCase().search(this.query.toLowerCase())
            let last_idx = first_idx + query_len
            if (first_idx + 1) {
                const split = string.split(' ')
                let prefix = ''
                if (first_idx && split.length == 2) prefix = split[0] 
                return prefix + ' <b class="text-primary">' + string.substring(first_idx, last_idx) + '</b>' + string.slice(last_idx)
            }
            return string
        },
        submitPhrase () {
            const query = {
                query: this.query
            }
            if (this.query && this.query.length >= 2) this.$store.dispatch('core/findPatient', {query, vm: this})
        },
        onBlur (ev) {
            setTimeout(() => {
                this.$emit('off') 
                this.query = ''
            }, 500)
        },
        selectPatient (patient_id) {
            const patient = this.searchedPatients.find(i => i.id === patient_id)
            const msg = `Showing appointments for <b class='font-medium'>${patient.name} ${patient.last_name}</b>`
            this.$store.commit('SET_GLOBAL_MESSAGE', [msg , 'primary'])
            this.$store.dispatch('core/listSlotsByPatient', {vm: this, patient_id})
            this.$parent.$parent.$refs.vuecal.switchView('month')
        },
        closeSelectedPatient () {
            this.$store.commit('SET_GLOBAL_MESSAGE', null)
            this.$store.commit('core/RESELECT_FILTERED_PATIENT')
            this.$store.commit('core/RESET_SEARCHED_PATIENT_SLOTS')
        }
    }
}
</script>

<style lang="scss">
