$barwidth: 390px;
.scheduler-searchbar {
    .patient-searchbar {
        width: $barwidth;
        input {
            font-size: 1.2rem;
            font-weight: 300;
            &::placeholder {
                color: rgb(172, 172, 172) !important;
            }
        }
        .loading {
            top: 50%;
            transform: translateY(-50%);
            left: 0;
            border-width: 1px;
            width: 23px;
            height: 23px;
            .effects {
                border-width: 1px;
            }
        }
    }
    .results {
        width: $barwidth;
        .patient-line {
            .patient-name {
                color: rgb(50, 50, 50);
                font-weight: 400;
                font-size: 15px;
            }
            .patient-id {
                color: rgb(150, 150, 150);
                font-size: 13px;
            }
            &:hover {
                background-color: #f7f7f7;
            }
            &:first-child {
                border-top-right-radius: .5rem;
                border-top-left-radius: .5rem;
            }
            &:last-child {
                border-bottom-right-radius: .5rem;
                border-bottom-left-radius: .5rem;
            }
        }
    }
}
</style>