<template>
  <div class="relative">
    <div class="vx-navbar-wrapper" :class="classObj">
      <vs-navbar class="vx-navbar navbar-custom navbar-skelton" :color="navbarColorLocal" :class="textColor">
        <div class="absolute h-full left-0 global-msg pl-6 w-full" :class="[globalMessage && 'active', globalMessage && globalMessage[1]]">
          <div class="flex content-center align-middle h-full ">
              <feather-icon :class='`text-${globalMessage && globalMessage[1]}`' :icon="globalMessage && icons[globalMessage[1]]" svgClasses="w-6" />
              <div class="self-center flex">
                  <p class='tracking-normal font-light text-xl text-dark pl-6' v-html="globalMessage && globalMessage[0]"></p>
              </div>
          </div>
        </div>
        <!-- SM - OPEN SIDEBAR BUTTON -->
        <feather-icon class="sm:inline-flex xl:hidden cursor-pointer p-2" icon="MenuIcon" @click.stop="showSidebar" />

        <vs-spacer />

        <!-- <search-bar /> -->

        <!-- <notification-drop-down /> -->

        <profile-drop-down />

      </vs-navbar>
    </div>
  </div>
</template>


<script>
import SearchBar            from "./components/SearchBar.vue"
import NotificationDropDown from "./components/NotificationDropDown.vue"
import ProfileDropDown      from "./components/ProfileDropDown.vue"

export default {
  name: "the-navbar-vertical",
  props: {
    navbarColor: {
      type: String,
      default: "#fff",
    },
  },
  components: {
    SearchBar,
    NotificationDropDown,
    ProfileDropDown
  },
  data () {
    return {
      icons: {
        primary: 'InfoIcon',
        warning: 'AlertTriangleIcon',
        success: 'CheckIcon',
        danger: 'AlertOctagonIcon'
      }
    }
  },
  computed: {
    globalMessage () {
      return this.$store.state.globalMessage
    },
    navbarColorLocal() {
      return this.$store.state.theme === "dark" && this.navbarColor === "#fff" ? "#10163a" : this.navbarColor
    },
    verticalNavMenuWidth() {
      return this.$store.state.verticalNavMenuWidth
    },
    textColor() {
      return {'text-white': (this.navbarColor != '#10163a' && this.$store.state.theme === 'dark') || (this.navbarColor != '#fff' && this.$store.state.theme !== 'dark')}
    },
    windowWidth() {
      return this.$store.state.windowWidth
    },

    // NAVBAR STYLE
    classObj() {
      if (this.verticalNavMenuWidth == "default")      return "navbar-default"
      else if (this.verticalNavMenuWidth == "reduced") return "navbar-reduced"
      else if (this.verticalNavMenuWidth)              return "navbar-full"
    },
  },
  methods: {
    showSidebar() {
      this.$store.commit('TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE', true);
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/vuexy/_variables.scss";

.global-msg {
    // width: 60rem;
    transition: transform .4s;
    transform: translateX(-10rem);
    &.active {
      transform: translateX(0rem);
    }
    &.primary {
        background: linear-gradient(140deg, lighten($primary, 30), rgba(#fff, 0));
    }
    &.warning {
        background: linear-gradient(140deg, #fff3e0, rgba(#fff, 0));
    }
    &.success {
        background: linear-gradient(140deg, lighten($success, 45), rgba(#fff, 0));
    }
    &.danger {
        background: linear-gradient(140deg, lighten($danger, 30), rgba(#fff, 0));
    }
}

</style>
