<template>
    <vx-card class="laima-form select-none shadow" :class="sizeCls" :title="title" style="max-width: 1440px;" :key="key">
        <form :data-vv-scope="formScope" onsubmit="return false;">
            <slot name="before-fields" :errors="$validator.errors.all()" />
            <div v-for="(item, idx) in formFieldsParsed" :key="idx" class='fields-box' v-show="!item.hidden">
                <slot name="beforefield" :error="$validator.errors.first(item.name, formScope)" :item="item"/>
                <div class="field-wrapper flex items-center">
                    <div class="field-desc relative">
                        <div class="field-label relative">
                            <slot name="label" :item="item">
                                <span v-if="item.validators && item.validators.required && !item.disabled" class="required-dot"></span>
                                {{ item.label }}
                            </slot>
                        </div>
                        <div class="field-help" v-if="item.helpText">
                            <slot name="help-text" :item="item">
                                {{ item.helpText }}
                            </slot>
                        </div>
                    </div>
                    <div class="field-value-wrap">
                        <div class="field-control items-center">
                            <slot name="fieldcontrol" :item="item">
                                <span class="field-validation">
                                    <v-popover
                                        v-if="showErrorsInTooltip"
                                        v-show="$validator.errors.has(item.name, formScope)"
                                        popoverBaseClass="tooltip"
                                        popoverArrowClass="border-danger tooltip-arrow popover-arrow"
                                        popoverInnerClass="bg-danger rounded-full text-white tracking-normal text-sm p-1 px-2"
                                        :id="item.name"
                                        ref="tooltip"
                                        trigger="click">
                                            <feather-icon 
                                                class="text-danger animated pulse delay-1s infinite"
                                                icon="AlertCircleIcon" />
                                            <template slot="popover">
                                                {{ $validator.errors.first(item.name, formScope) }}
                                            </template>
                                    </v-popover>
                                </span>
                                <div class="field-value" :class="item.type === 'switch' && 'lf-switch'">
                                    <slot :name="`${item.name}BeforeValue`" />
                                    <slot :name="item.name"
                                        :item="item"
                                        :successCallback="partial(success, item.name)"
                                        :onFieldUpdated="updatedField"
                                        :hasErrors="$validator.errors.has(item.name, formScope)">

                                        <lm-input
                                            v-if="laimaInputTypes.includes(item.type)"
                                            :autocomplete="item.autocomplete || 'on'"
                                            :class="item.classes || {}"
                                            :danger="$validator.errors.has(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :loading="item.loading || false"
                                            :name="item.name"
                                            :placeholder="item.placeholder || ''"
                                            :type="item.type"
                                            v-model="item.model"
                                            v-debounce:1100ms="partial(backupSave, item)"
                                            v-validate="item.validators || {}"
                                            @change="$emit('changed', [item.name, item.model])"
                                            @blur="item.submitters.includes('blur') && updatedField(item.name, item.model) || voidFn()"
                                            @keypress.enter="item.submitters.includes('enter') && updatedField(item.name, item.model) || voidFn()"
                                        />

                                        <vue-timepicker 
                                            v-else-if="item.type === 'time'"
                                            close-on-complete
                                            format="HH:mm"
                                            hide-disabled-hours
                                            :hour-label="item.hourLabel || 'hour'"
                                            input-class="vs-inputx vs-input--input normal"
                                            input-width="100%"
                                            :minute-label="item.minuteLabel || 'minute'"
                                            :class="item.classes || {}"
                                            :danger="$validator.errors.has(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :hide-clear-button="!item.clearable || false"
                                            :hour-range="item.hourRange || [[0, 23]]"
                                            :minute-range="item.minuteRange || [[0, 59]]"
                                            :minute-interval="item.interval || 1"
                                            :name="item.name"
                                            :placeholder="item.placeholder || ''"
                                            :type="item.type"
                                            v-model="item.model"
                                            v-validate="item.validators || {}"
                                            @input="$emit('changed', [item.name, item.model]) && updatedField(item.name, item.model) || voidFn()"
                                        />
                                        
                                        <vs-input-number
                                            v-else-if="item.type === 'fancyNumber'"
                                            :class="item.classes || {}"
                                            :danger="$validator.errors.has(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :min="item.min || 0"
                                            :max="item.max"
                                            :name="item.name"
                                            :placeholder="item.placeholder || ''"
                                            :step="item.step || 1"
                                            v-model="item.model"
                                            v-validate="item.validators || {numeric: true}"
                                            v-debounce:1100ms="partial(backupSave, item)"
                                            @input="$emit('changed', [item.name, item.model]) && updatedField(item.name, item.model) || voidFn()"
                                        />

                                        <vs-textarea
                                            v-else-if="item.type === 'textarea'"
                                            :class="item.classes || {}"
                                            :danger="$validator.errors.has(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :name="item.name"
                                            :placeholder="item.placeholder || ''"
                                            v-model="item.model"
                                            v-debounce:1100ms="partial(backupSave, item)"
                                            v-validate="item.validators || {}"
                                            @blur="item.submitters.includes('blur') && updatedField(item.name, item.model) || voidFn()"
                                        />

                                        <vs-select
                                            v-else-if="item.type === 'select'"
                                            class="w-full"
                                            :autocomplete="item.autocomplete || false"
                                            :class="item.classes || {}"
                                            :danger="$validator.errors.has(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :multiple="item.multiple || false"
                                            :name="item.name"
                                            :placeholder="item.placeholder"
                                            style="height: 42px;"
                                            v-validate="item.validators || {}"
                                            v-model="item.model"
                                            @input="val => $emit('changed', [item.name, val]) && updatedField(item.name, val) || voidFn()" >
                                            <vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in item.options">
                                            </vs-select-item>
                                        </vs-select>
                                        
                                        <datepicker
                                            v-else-if="item.type === 'date'"
                                            input-class='vs-inputx vs-input--input normal'
                                            :class="{
                                                'input-icon-validate-danger': $validator.errors.has(item.name, formScope),
                                                ...(item.classes || {})
                                            }"
                                            :danger="$validator.errors.has(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :name="item.name"
                                            :placeholder="item.placeholder || 'e.g. 1970-01-01'"
                                            v-model="item.model"
                                            v-validate="item.validators || {}"
                                            @input="$emit('changed', [item.name, item.model])"
                                            @selected="item.submitters.includes('change') && updatedField(item.name, item.model) || voidFn()"
                                        />

                                        <vs-switch
                                            v-else-if="item.type === 'switch'"
                                            :class="item.classes || {}"
                                            :color="item.color || 'success'"
                                            :danger="$validator.errors.has(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :name="item.name"
                                            v-model="item.model"
                                            v-validate="item.validators || {}"
                                            @input="$emit('changed', [item.name, item.model]) && item.submitters.includes('change') && updatedField(item.name, item.model) || voidFn()"
                                        />

                                        <time-range
                                            v-else-if="item.type === 'timerange'"
                                            :class="item.classes || {}"
                                            :errorsArray="$validator.errors.first(item.name, formScope)"
                                            :data-vv-as="item.label || item.name"
                                            :disabled="item.disabled || false"
                                            :name="item.name"
                                            v-validate="{range: true}"
                                            v-model="item.model"
                                            @input="item.submitters.includes('change') && updatedField(item.name, item.model) || voidFn()"
                                        />

                                    </slot>
                                </div>
                            </slot>
                        </div>
                    </div>
                </div>
                <div>
                    <slot name="afterfield" :item="item"></slot>
                </div>
            </div>
            <slot name="after-fields" />
        </form>
        <slot name="footer" v-if="onSubmit" :callback="onSubmitWrap">
            <vs-divider class="mt-0 mb-0"/>
            <div class="">
                <div class="justify-start flex items-center h-full">
                    <vs-button
                        class="mx-6 my-4"
                        type="filled"
                        color="primary"
                        :disabled="$store.state.stateLoading || isLoading"
                        @click="onSubmitWrap">
                        <div class='w-full'>
                            <span :class='($store.state.stateLoading || isLoading) && "opacity-0"'>{{ $t('buttons.save') }}</span>
                            <div class="w-full absolute h-full top-0">
                                <div class="flex justify-center  h-full">
                                    <svg v-if="$store.state.stateLoading || isLoading" class="circle__svg w-6" viewBox="0 0 100 100">
                                        <circle class="circle__svg-circle" cx="50" cy="50" r="45"/>
                                    </svg>
                                </div>
                            </div>
                        </div>
                        <!-- <span v-else></span> -->
                    </vs-button>
                </div>
            </div>
        </slot>
    </vx-card>
</template>

<script>
import TimeRange from './TimeRange.vue'
import Datepicker from 'vuejs-datepicker'
import VueTimepicker from 'vue2-timepicker'
import 'vue2-timepicker/dist/VueTimepicker.css'

const SIZE_CLASSES = {
    normal: 'normal-size',
    small: 'small-size',
    large: 'large-size'
}

export default {

    name: 'laima-form',

    components: {
        Datepicker,
        TimeRange,
        VueTimepicker
    },

    props: {
        modelsContainer: {
            type: Object,
            required: true
        },
        size: {
            type: String,
            default: 'normal',
        },
        title: {
            type: String
        },
        formFields: {
            type: Array,
            required: true,
            validator (item) {
                const required = ['type', 'name']
                return item.every(fieldObj => {
                    for (let rfield of required) {
                        if (!fieldObj[rfield]) {
                            console.warn(`In ${JSON.stringify(fieldObj)}, '${rfield}' is required`)
                            return false
                        }
                        if (fieldObj.type === 'select' && !fieldObj.options) {
                            console.warn(`In ${JSON.stringify(fieldObj)}, 'options' is required`)
                            return false
                        }
                    }
                    return true
                })
            }
        },
        formScope: {
            type: String,
            default: 'step-1'
        },
        onSubmit: {
            type: [Function, Boolean],
            default: false
        },
        cleanForm: {
            type: Function,
            default: val => val
        },
        showErrorsInTooltip: {
            type: Boolean,
            default: true
        },
        readonly: {
            type: [Boolean, Array],
            default: false
        },
        isLoading: {
            type: Boolean,
            default: false
        }
    },

    computed: {
        sizeCls () {
            return SIZE_CLASSES[this.size]
        },
        localModels () {
            return this.$clone(this.modelsContainer)
        },
        formFieldsParsed () {
            const disabled = this.readonly
            return this.formFields.map(field => {
                if (field.model) {
                    field.model = this.$parent[field.model]
                }
                if (!field.model) {
                    field.model = this.localModels[field.name]
                }
                else {
                    field.model = this.modelsContainer[field.name]
                }
                if (!field.submitters) {
                    field.submitters = this.type2Submitters[field.type] || this.type2Submitters.default
                }
                if (field.type === 'timerange' && !field.procureKeyFn) {
                    field.procureKeyFn = val => {
                        if (!val) return val
                        return `from ${val.join(" to ")}`
                    }
                }
                if (disabled) {
                    if (disabled === true) {
                        field.disabled = true
                    }
                    else if (disabled.includes(field.name)) {
                        field.disabled = true
                    }
                }
                return field
            })
        },
        formFieldsKeyed () {
            return Object.fromEntries(this.formFieldsParsed.slice(0).map(field => {
                const fieldName = field.name
                return [
                    fieldName, field
                ]
            }))
        },
        dynamicModel () {
            return {
                ...this.localModels,
                ...this.modelsContainer
            }
        }
    },

    data () {
        return {
            type2Submitters: {
                text: ['blur', 'enter'],
                password: ['blur', 'enter'],
                number: ['blur', 'enter'],
                email: ['blur', 'enter'],
                tel: ['blur', 'enter'],
                time: ['blur', 'enter'],
                default: ['change']
            },
            laimaInputTypes: ['text', 'password', 'number', 'email', 'tel'],
            oldFieldVals: {},
            key: 0
        }
    },

    methods: {
        execCleanForm (payload) {
            try {
                return this.cleanForm.call(this.$parent, payload)
            }
            catch (e) {
                if (e instanceof this.ValidationError) {
                    const errPayload = {
                        scope: this.formScope,
                        field: e.field,
                        msg: e.msg
                    }
                    this.$validator.errors.add(errPayload)
                    return false
                }
                else {
                    throw e
                }
            }
        },

        async onSubmitWrap () {
            const valid = await this.$validator.validateAll(this.formScope)
            if (!valid) return

            let payload = Object.fromEntries(Object.entries(this.formFieldsKeyed).map(([fieldname, field]) => {
                return [fieldname, field.model]
            }))

            payload = this.execCleanForm(payload)
            if (!payload) return

            await this.onSubmit(payload)
        },

        async backupSave (fieldInst) {
            await this.updatedField(fieldInst.name, fieldInst.model, 'backup')
        },

        voidFn () {},

        async updatedField (field, newvalue, event='updated') {
            // this is tantamount to this form being used to *post* a resource, so update makes no sense
            if (this.onSubmit) return
            const valid = await this.$validator.validateAll(this.formScope)
            if (!valid) return

            const oldvalue = this.oldFieldVals[field]
            if (oldvalue && oldvalue == newvalue) return

            this.$set(this.localModels, field, newvalue)

            const payload = this.execCleanForm({[field]: newvalue})
            if (!payload) return

            if (event === 'backup') {
                this.$emit(event, [payload])
            }
            else {
                this.$emit(event, [payload, this.partial(this.success, field, newvalue)])
            }
        },

        executeNotification (field, newvalue) {
            newvalue = newvalue || field.model
            const fieldname = field.name
            const procureKeyFn = field.procureKeyFn
            const oldvalue = this.oldFieldVals[fieldname]

            const transformedNewValue = procureKeyFn && procureKeyFn(newvalue) || newvalue
            const transformedOldValue = procureKeyFn && procureKeyFn(oldvalue) || oldvalue

            const fieldlabel = field.label
            if (!transformedNewValue || transformedNewValue == oldvalue) return
            let msg
            
            if (!transformedOldValue || transformedOldValue === fieldname) {
                msg = this.$t('misc.set_field', {field: fieldlabel, to: transformedNewValue})
            }
            else {
                msg = this.$t('misc.changed_field', {field: fieldlabel, from: transformedOldValue, to: transformedNewValue})
            }
            this.oldFieldVals[fieldname] = newvalue
            this.notifySuccess(msg)
        },

        success (field, newvalue) {
            const fieldObj = this.formFieldsKeyed[field]
            if (!fieldObj) return
            this.executeNotification(fieldObj, newvalue)
        }
    },

    mounted () {
        const parent = this.$parent

        const unwatch = this.$watch(() => {
            return this.formFieldsParsed[0].model
        }, () => {
            this.formFieldsParsed.forEach(field => {
                const model = field.model
                if (model === undefined || model === null) return
                if (typeof model === String) {
                    this.$set(this.oldFieldVals, field.name, parent[model])
                }
                else {
                    this.$set(this.oldFieldVals, field.name, model)
                }
            })
            
        })
        setTimeout(unwatch, 1000)
    },

    watch: {
        '$validator.errors.items' () {
            if (!this.showErrorsInTooltip) return
            this.$nextTick(() => {
                this.$validator.fields.items.forEach(field => {
                    const tooltip = this.$refs.tooltip.find(ttip => ttip.$attrs.id === field.name)
                    if (!tooltip) return
                    if (this.$validator.errors.has(field.name, this.formScope)) {
                        tooltip.show()
                    }
                    else {
                        tooltip.hide()
                    }
                })
            })
        }
    },

    beforeDestroy () {
        document.querySelectorAll('.vs-tooltip').forEach(el => el.remove())
    }
}
</script>

<style lang="scss">
@import "@/assets/scss/vuexy/_variables.scss";

