        position: absolute !important;
    }

    .border-danger {
        .vs-input--input {
            border-color: $danger;
        }
    }
    .vx-card__header {
        border-radius: .375rem .375rem 0 0;
        background: #dfeaef;
        padding: 15px;
        .vx-card__title {
            h4 {
                text-transform: uppercase;
                font-weight: 500 !important;
                color: #025a7b;
            }
        }
    }
    .vx-card__body {
        padding: 0 !important;
    }
    .fields-box {
        &:not(:last-child) {
            border-bottom: 1px solid #e7e7e7;
        }
        .field-wrapper {
            padding: 15px 20px 15px 20px;
            .field-desc {
                width: 50%;
