                    color: rgb(48, 40, 57);
                    line-height: 1;
                    flex: 1 1 0%;
                }
                .field-help {
                    color: #628c9c;
                    font-size: 13px;
                    margin-top: 8px;
                    line-height: 1.4;
                }
            }
            .field-value-wrap {
                width: 50%;
                .field-control {
                    display: flex;
                    flex-shrink: 0;
                    .field-validation {
                        height: 23px;
                        width: 26px;
                    }
                    .field-value {
                        display: flex;
                        flex-direction: column;
                        position: relative;
                        flex: 1 1 0%;
