import removeActions from './removeActions.js'
import createActions from './createActions.js'
import editActions from './editActions.js'
import listActions from './listActions.js'

export default {
    ...createActions,
    ...removeActions,
    ...editActions,
    ...listActions,

    async verifyDomain ({commit}, [clinicID, url, vm, callback]) {
        const resp = await vm.requestResource('post_verifyDomain', {
            vm,
            no_loading: true,
            data: { url }
        })
        if (resp.status === 200) {
            const status = resp.data.status
            commit('PATCH_CLINIC', {clinicID, payload: {
                widget_installed: status
            }})
            callback(resp)
        }
    },

    async viewSlotDetails ({commit}, [slotID, vm]) {
        const data = {
            id: slotID
        }
        const query = {
            select: 'during,status,patient,schedule,procedures'
        }
        const resp = await vm.requestResource('get_scheduleslot', {
            data,
            query,
            vm
        })
        if (resp.status === 200) {
            let slotData = resp.data
            let {status} = slotData
            const patientResp = await vm.requestResource('get_patientresource', {
                data: {
                    id: slotData.patient.id,
                    patient_id: slotData.patient.patient_id
                }
            })
            if (patientResp.status === 200) {
                delete slotData.patient
                slotData.doctor = slotData.schedule.doctorID
                Object.assign(slotData, patientResp.data)
                Object.assign(slotData, slotData.actor)
                delete slotData.actor
            }
            slotData.status = status
            commit('SET_SLOT_DETAILS', slotData)
        }
        commit('SET_VIEW_SLOT_PROMPT', slotID)
    },

    async confirmAppointment ({commit}, {vm, slotID, doctorID}) {
        const resp = await vm.requestResource('post_confirmAppointment', {
            vm,
            format: {
                slot_id: slotID
            }
        })
        if (resp.status === 200) {
            document.querySelector('.form-alert').style.height = 'auto'
            commit('UPDATE_SLOT_DETAILS', {status: 'confirmed'})
            commit('CONFIRM_SLOT_STATUS', [doctorID, slotID])
            vm.notifySuccess(vm.$t('misc.visit_confirmed'))
        }
    }
}