<template>
    <div class="h-screen flex w-full bg-img">
        <div class="vx-col sm:w-3/5 md:w-3/5 lg:w-3/4 xl:w-3/5 mx-auto self-center">
            <vx-card>
                <div slot="no-body" class="full-page-bg-color">
                    <div class="vx-row">
                        <div class="vx-col hidden sm:hidden md:hidden lg:block lg:w-1/2 mx-auto self-center">
                            <!-- <img src="@/assets/images/pages/reset-password.png" alt="login" class="mx-auto"> -->
                        </div>
                        <div class="vx-col sm:w-full md:w-full lg:w-1/2 mx-auto self-center  d-theme-dark-bg">
                            <div class="p-8">
                                <div class="vx-card__title mb-8">
                                    <h4 class="mb-4">{{ $t('headers.reset_pass') }}</h4>
                                    <p>{{ $t('headers.enter_new_pass') }}</p>
                                </div>
                                <div class="vx-col mb-8">
                                    <vs-input type="password"
                                        name="password"
                                        v-validate="'required|min:6'"
                                        v-on:keyup.enter="reset"
                                        :data-vv-as="$t('fields.password').toLowerCase()" 
                                        :label-placeholder="$t('fields.password')" 
                                        v-model="pass1"
                                        ref="password"
                                        class="w-full mb-6" />
                                    <span class="text-danger text-sm" v-show="errors.has('password')">{{ errors.first('password') }}</span>
                                    <vs-input type="password"
                                        name="confirm_password"
                                        :data-vv-as="$t('fields.password').toLowerCase()" 
                                        v-validate="'required|min:6|confirmed:password'"
                                        v-on:keyup.enter="reset"
                                        :label-placeholder="$t('fields.confirm_pass')" 
                                        v-model="pass2" 
                                        class="w-full" />
                                    <span class="text-danger text-sm" v-show="errors.has('confirm_password')">{{ errors.first('confirm_password') }}</span>
                                </div>

                                <div class="flex flex-wrap justify-between flex-col-reverse sm:flex-row">
                                    <vs-button type="border" to="/pages/account/login" class="w-full sm:w-auto mb-8 sm:mb-auto mt-3 sm:mt-auto">{{ $t('headers.goto_login') }}</vs-button>
                                    <vs-button @click="reset" class="w-full sm:w-auto">{{ $t('headers.reset_pass') }}</vs-button>
                                </div>

                            </div>
                        </div>
                    </div>
                </div>
            </vx-card>
        </div>
    </div>
</template>

<script>
import EventBus from '@/EventBus.js'

export default {
    data () {
        return {
            pass1: '',
            pass2: ''
        }
    },

    methods: {
        async reset () {
            const valid = await this.$validator.validate()
            if (!valid) return
            
            const checkcode = this.$route.params.checkcode

            // Get the swapcode first
            this.$store.commit('STATE_LOADING_ON')
            const swapcode_url = `/api/reset/pass/${checkcode}/`
            const swapcode_resp = await this.$http.get(swapcode_url)
            if (swapcode_resp.status != 200) {
                return this.notifyError(
                    this.$t('notifs.failed_set_pass')
                )
            }
            const swapcode = swapcode_resp.data.checkcode

            // Proceed to pass reset with the swapcode
            const reset_url = `/api/actor/pass/change/${swapcode}/`
            const bodyFormData = new FormData()
            bodyFormData.set('password1', this.pass1)
            bodyFormData.set('password2', this.pass2)

            const resp = await this.$http({
                url: reset_url,
                method: 'POST',
                data: bodyFormData,
                headers: {
                    Range: 'post',
                    'Content-Type': 'multipart/form-data'
                }
            })
            this.$store.commit('STATE_LOADING_OFF')

            if (resp.status === 422) {
                const errors = this.errors
                return Object.entries(resp.data).forEach(item => {
                    let [keyname, errs] = item
                    if (keyname === 'password1') keyname = 'password'
                    else if (keyname === 'password2') keyname = 'confirm_password'
                    else {
                        return this.notifyError( this.$t('notifs.failed_set_pass') )
                    }
                    errs.forEach(errmsg => {
                        errors.add({
                            field: keyname,
                            msg: errmsg
                        })
                    })
                })
            }
            else if (resp.status !== 200) {
                return this.notifyError( this.$t('notifs.failed_set_pass') )
            }
            EventBus.$emit('login', {})
            // this.$router.push({
            //     name: 'page-login', 
            //     query: {source: 'resetPass'}
            // })
        }
    }
}
</script>