import Vue from 'vue'

export default {
    async removeBreak ({state, commit}, [event, vm]) {
        const {doctorID, id, scheduleID} = event
        const breaks = state.schedulerBreakSlots[doctorID]
        const afterRemovalScheduleBreaksRaw = breaks.filter(item => item.scheduleID === scheduleID && item.id !== id)
        const afterRemovalScheduleBreaks = afterRemovalScheduleBreaksRaw.map(item => {
            return [ item.start, item.end ].map(datestr => (new Date(datestr).format('HH:mm')))
        })
        const data = {
            select: { id: scheduleID },
            payload: { gaps: afterRemovalScheduleBreaks }
        }
        const resp = await vm.requestResource('put_schedule', { vm, data })
        if (resp.status === 200) {
            vm.notifySuccess(vm.$t('notifs.break_removed'))
            commit('REMOVE_SCHEDULE_BREAK', [doctorID, id])
        }
        else {
            vm.notifyError(vm.$t('notifs.break_remove_failed'))
        }
    },

    async removeProcedure ({commit}, [vm, tr]) {
        const resp = await vm.requestResource('delete_clinicprocedure', {
            data: {
                id: tr.id
            },
            vm
        })
        let success = false
        if (resp.status === 422) {
            if (resp.data.id[0].match('still referenced')) {
                // change hidden flag to true instead
                const updateProcResp = await vm.requestResource('patch_clinicprocedure', {
                    data: {
                        select: {id: tr.id},
                        payload: {hidden: true}
                    },
                    vm
                })
                if (updateProcResp.status === 200) {
                    commit('UPDATE_CLINIC_PROCEDURE', [tr.id, 'hidden', true])
                    success = true
                }
            }
            else {
                return vm.notifyError(vm.$t('notifs.procedure_deleted_failed'))
            }
        }

        if (resp.status === 200) {
            success = true
        }

        if (success) {
            vm.notifySuccess(vm.$t('notifs.procedure_deleted', {
                name: tr.name
            }))
            commit('REMOVE_CLINIC_PROCEDURE', [tr.id])
        }
    },

    async removeSchedule ({state, commit}, [event, vm, hasSlots]) {
        const {doctorID, id} = event
        const scheduleIndex = state.plannerSlots[doctorID].findIndex(item => item.id === id)
        const schedule = state.plannerSlots[doctorID][scheduleIndex]
        if (schedule.start < new Date()) {
            return vm.notifyError(vm.$t('notifs.no_expired_schedules_removed'))
        }
        const data = { id }
        const resp = await vm.requestResource('delete_schedule', {data})
        if (resp.status === 204) {
            vm.emitSchedulerUpdate()
            vm.notifySuccess(vm.$t('notifs.schedule_deleted'))
            commit('REMOVE_SCHEDULE', [doctorID, scheduleIndex, hasSlots])
        }
        else {
            vm.notifyError(vm.$t('notifs.schedule_removal_failed'))
        }
    },

    async cancelAppointment ({commit}, [event, vm]) {
        const {doctorID, id, start} = event
        if (start < new Date()) {
            return vm.notifyError(vm.$t('notifs.no_cancel_expired_visits'))
        }
        const data = {id}
        const resp = await vm.requestResource('delete_scheduleslot', {data})
        if (resp.status === 200) {
            vm.notifySuccess(vm.$t('notifs.visit_canceled'))
            commit('REMOVE_SCHEDULE_SLOT', [doctorID, id])
        }
        else {
            vm.notifyError(vm.$t('notifs.visit_cancel_failed'))
        }
    }
}