import db from '@/db.js'
const onSyncOk = table_name => async () => {
    await db.sync.put({
        table_name,
        hashsum: 'full',
        timestamp: new Date().getTime()
    })
}

function default_presync_fn (resource, headers) {
    return {
        headers,
        data: {}
    }
}

const PRESYNC_FNS = {
    presync_visit (resource, headers) {
        return {
            headers,
            data: {},
            query: {
                select: 'id,doctor,comments,workspace,procedures,date,patient,schedule_slot'
            }
        }
    }
}
export default {
    async fullSync({dispatch, commit, rootState}, {vm, toSync}) {
        // TODO: break up larger downloads into blocks
        commit('CLEAR_SYNCING_STACK')
        commit('TOGGLE_SYNC', true)
        const fixWorkspaceFor = ['doctors']
        for (let table of toSync) {
            console.info(`Syncing ${table}...`)
            commit('ADD_TO_SYNCING_STACK', table)

            // in case of dealing aux table, this is onle available through `get
            let headers = {}
            let resource = `list_${table}`
            resource = vm.spec[resource] ? resource : `get_${table}`
            if (fixWorkspaceFor.includes(table)) {
                headers['Overwrite'] = rootState.auth.selectedWorkspace.id
            }
            
            // download the resource
            const presync_fn_name = `presync_${table}`
            const payload = Object.assign(
                {no_loading: true},
                (PRESYNC_FNS[presync_fn_name] || default_presync_fn)(resource, headers)
            )
            if (!payload.query) {
                payload.query = {}
            }
            payload.query['limit'] = 500
            const resp = await vm.requestResource(resource, payload)
            if (resp.status === 200) {
                // TODO: Read `total_count`. If > 500, fetch-iterate over the remaining part
                dispatch(`sync_${table}`, [vm, table, resp.data.data || resp.data, onSyncOk(table)])
            }
        }
        commit('TOGGLE_SYNC', false)
    },

    async sync_clinic ({ commit, rootState }, [vm, tablename, data, callback]) {
        const table = db[tablename]
        const clinics = data.map(item => {
            let clinicID = item.id
            let workspaces = rootState.auth.actorContext.workspaces
            item['name'] = workspaces[clinicID]
            return item
        })
        await table.bulkPut(clinics)
        commit('core/SET_CLINICS', clinics)
        callback()
    },
    
    async sync_doctors ({commit}, [vm, tablename, data, callback]) {
        const table = db[tablename]
        await table.bulkPut(data)
        commit("core/SAVE_DOCTORS", {payload: data})
        callback()
    },

    async sync_schedule ({commit}, [vm, tablename, data, callback]) {
        // console.log(data);
    }
}