import Vue from 'vue'

function addDays(date, days) {
    date.setDate(date.getDate() + days)
    return date
}

function sanitizeEmptyBusinessHours(item) {
    const bh = Object.entries(item.business_hours).filter(([k, v]) => {
        return v[0]
    })
    item.business_hours = Object.fromEntries(bh)
    return item
}

export default {
    /*
     *  Scheduler
     */

    ADD_LOADING_SLOT (state, [start, end, split]) {
        const payload = {
            start,
            end,
            split,
            draggable: false,
            resizable: false,
            class: 'slot-loading',
            background: true
        }
        state.loadingSlots.push(payload)
    },

    SET_HEATMAP_LOADING (state, flag) {
        state.heatMapLoading = flag
    },

    SET_MONTHLY_HEATMAP (state, [date, payload]) {
        Vue.set(state.monthlyHeatmapSlots, date, payload)
    },

    REMOVE_LOADING_SLOT (state) {
        state.loadingSlots = []
    },

    ADD_PLANNER_SLOT (state, payload) {
        const {doctorID} = payload
        if (!state.plannerSlots[doctorID]) {
            Vue.set(state.plannerSlots, doctorID, [])
        }
        state.plannerSlots[doctorID].push(payload)
    },

    ADD_PLANNER_OUTLIER (state, payload) {
        const {doctorID} = payload
        if (!state.plannerOutliers[doctorID]) {
            Vue.set(state.plannerOutliers, doctorID, [])
        }
        payload.draggable = false
        payload.resizable = false
        state.plannerOutliers[doctorID].push(payload)
    },

    ADD_SCHEDULER_OUTLIER (state, payload) {
        const {doctorID} = payload
        if (!state.schedulerOutliers[doctorID]) {
            Vue.set(state.schedulerOutliers, doctorID, [])
        }
        payload.draggable = false
        payload.resizable = false
        state.schedulerOutliers[doctorID].push(payload)
    },
    
    ADD_SCHEDULER_SLOT (state, payload) {
        const {doctorID} = payload
        if (!state.schedulerSlots[doctorID]) {
            Vue.set(state.schedulerSlots, doctorID, [])
        }
        payload.draggable = true
        payload.class = 'main'
        state.schedulerSlots[doctorID].push(payload)
    },

    ADD_SCHEDULER_BREAK_SLOT (state, payload) {
        const {doctorID} = payload
        if (!state.schedulerBreakSlots[doctorID]) {
            Vue.set(state.schedulerBreakSlots, doctorID, [])
        }
        payload.resizable = false
        state.schedulerBreakSlots[doctorID].push(payload)
    },

    UPDATE_OUTLIERS_PER_DOCTOR (state, [doctorID, payload]) {
        Vue.set(state.schedulerOutliers, doctorID, payload)
    },

    UPDATE_OUTLIERS (state, payload) {
        state.schedulerOutliers = payload
    },

    CLEAR_ALL_SLOTS (state) {
        state.schedulerSlots = {}
        state.schedulerBreakSlots = {}
        state.schedulerOutliers = {}
        state.scheduleMetaMap = {}
        state.plannerSlots = {}
        state.plannerOutliers = {}
    },

    MAKE_SCHEDULE (state, payload) {
        const {doctorID} = payload
        if (!state.plannerSlots[doctorID]) {
            Vue.set(state.plannerSlots, doctorID, [])
        }
        state.plannerSlots[doctorID].push(payload)
    },

    UPDATE_CURRENT_DATE (state, payload) {
        state.currentDate = payload
    },

    REMOVE_SCHEDULE (state, [doctorID, scheduleIndex, hasSlots]) {
        const scheduleIDToRemove = state.plannerSlots[doctorID][scheduleIndex].id
        // const deletedDay = state.plannerSlots[doctorID][scheduleIndex].start

        // remove the schedule itself
        Vue.delete(state.plannerSlots[doctorID], scheduleIndex)

        // remove the schedule map
        const scheduleMapIndex = state.scheduleMetaMap[doctorID].findIndex(i => i.scheduleID === scheduleIDToRemove)
        if (scheduleMapIndex) Vue.delete(state.scheduleMetaMap[doctorID], scheduleMapIndex)

        // remove the slots, if present
        if (hasSlots) {
            const newSlots = state.schedulerSlots[doctorID].filter(item => {
                return item.scheduleID !== scheduleIDToRemove
            })
            Vue.set(state.schedulerSlots, doctorID, newSlots)

        }

        // remove breaks, if present
        const schedulerBreakSlots = state.schedulerBreakSlots[doctorID] || []
        const newBreaks = schedulerBreakSlots.filter(item => {
            return item.scheduleID !== scheduleIDToRemove
        })
        Vue.set(state.schedulerBreakSlots, doctorID, newBreaks)
        
        // remove the scheduler's outliers
        const schedulerOutliers = state.schedulerOutliers[doctorID] || []
        const newOutliers = schedulerOutliers.filter(item => {
            return item.scheduleID !== scheduleIDToRemove
        })
        Vue.set(state.schedulerOutliers, doctorID, newOutliers)
    },

    REMOVE_SCHEDULE_BREAK (state, [doctorID, id]) {
        state.schedulerBreakSlots[doctorID].forEach((item, index) => {
            if (item.id === id) {
                return Vue.delete(state.schedulerBreakSlots[doctorID], index)
            }
        })
    },

    REMOVE_SCHEDULE_SLOT (state, [doctorID, slot_id, holder]) {
        if (!doctorID) {
            Object.entries(state.schedulerSlots).forEach(([doctorID, slots]) => {
                slots.forEach((item, index) => {
                    if (item.id === slot_id) {
                        if (holder) holder.slot = item
                        return Vue.delete(state.schedulerSlots[doctorID], index)
                    }
                })
            })
        }
        else {
            state.schedulerSlots[doctorID].forEach((item, index) => {
                if (item.id === slot_id) {
                    if (holder) holder.slot = item
                    return Vue.delete(state.schedulerSlots[doctorID], index)
                }
            })
        }
    },

    SET_BUSINESS_HOURS (state, [bh, firstDate]) {
        // bh is clinics' business_hours object, where each key denotes a day index
        state.businessHours = Object.fromEntries(Object.entries(bh).map(([dayIndex, bhs]) => {
            let date = firstDate.clone()
            addDays(date, parseInt(dayIndex))
            let [start, end] = bhs.map(hour => {
                const [hrs, mins] = hour.split(':')
                let base = date.clone()
                base.setHours(hrs)
                base.setMinutes(mins)
                return base
            })
            return [date, [start, end]]
        }))
    },

    UPDATE_SCHEDULE_META_MAP(state, {
            date,
            doctorID,
            scheduleID,
            scheduleDuring,
            businessHours
        }) {
            
        if (!state.scheduleMetaMap[doctorID]) {
            Vue.set(state.scheduleMetaMap, doctorID, [])
        }
        state.scheduleMetaMap[doctorID].push({
            date,
            scheduleID,
            scheduleDuring,
            businessHours
        })
    },

    SAVE_DOCTORS (state, {payload}) {
        payload.forEach(doctor => {
            const doctorID = doctor.actor
            Vue.set(state.docs, doctorID, doctor)
        })        
    },

    UPDATE_DOCTOR (state, [actorID, payload]) {
        const newPayload = Object.assign(state.docs[actorID], payload)
        Vue.set(state.docs, actorID, newPayload)
    },

    SET_ACTIVE_DOCTOR (state, payload) {
        state.calendarActiveDoctor = payload
    },

    SET_CALENDAR_MODE (state, val) {
        state.calendarMode = val
    },

    SET_INTERIM_SLOT (state, val) {
        state.interimEvents.push(val)
    },

    SET_PATIENT_SEARCHBAR_LOADING (state, flag) {
        state.patientSearchbarLoading = flag
    },

    RESET_INTERIM_SLOT (state) {
        state.interimEvents = []
    },

    SET_SCHEDULE_LOADING (state, flag) {
        state.scheduleLoading = flag
    },

    CONFIRM_SLOT_STATUS (state, [doctorID, slotID]) {
        const index = state.schedulerSlots[doctorID].findIndex(item => item.id == slotID)
        const payload = state.schedulerSlots[doctorID][index]
        payload.status = 'confirmed'
        Vue.set(state.schedulerSlots[doctorID], index, payload)
    },

    SET_ADD_SLOT_PROMPT (state, flag) {
        state.addSlotPrompt = !!flag
        if (!flag) state.interimEvents = []
    },

    SET_VIEW_SLOT_PROMPT (state, slotID) {
        state.viewSlotPrompt = slotID
    },

    SET_SLOT_DETAILS (state, payload) {
        payload.during = payload.during.map(i => new Date(i))
        state.selectedSlotDetails = payload
    },

    UPDATE_SLOT_DETAILS (state, payload) {
        Object.assign(state.selectedSlotDetails, payload)
    },

    UPDATE_INTERIM_SLOT (state, payload) {
        const newEvent = Object.assign(state.interimEvents[0] || {}, payload)
        state.interimEvents = [newEvent]
    },

    UPDATE_SCHEDULE_DURATION (state, [doctorID, scheduleID, endTime]) {
        const index = state.plannerSlots[doctorID].findIndex(item => item.id === scheduleID)
        Vue.set(state.plannerSlots[doctorID][index], 'end', endTime)
    },

    /*
     *  Clinics
     */

    ADD_CLINIC (state, payload) {
        state.clinics.push(sanitizeEmptyBusinessHours(payload))
    },

    SET_CLINICS(state, payload) {
        // sanitize empty business hours
        payload = payload.map(sanitizeEmptyBusinessHours)
        state.clinics = payload
    },

	SHOW_CLINIC_PROMPT(state) {
        state.addClinicPrompt = true
    },

    HIDE_CLINIC_PROMPT(state) {
        state.addClinicPrompt = false
    },

    PATCH_CLINIC(state, {clinicID, payload}) {
        let idx = state.clinics.findIndex(clinic => clinic.id == clinicID)
        const editedClinic = state.clinics[idx]
        Vue.set(state.clinics, idx, Object.assign(editedClinic, payload))
    },

    DELETE_CLINIC(state, name) {
        state.clinics = state.clinics.filter(clinic => clinic.name !== name)
    },

    /*
     *  Procedures
     */

    SET_PROCEDURE_TEMPLATES(state, payload) {
        // divide into categories before commiting
        state.procedureTemplates = payload
        localStorage.setItem('app.laima.procedureTemplates', JSON.stringify(state.procedureTemplates))
    },

    SET_CLINIC_PROCEDURES(state, [payload]) {
        // divide into categories before commiting
        state.clinicProcedures = payload
    },

    APPEND_CLINIC_PROCEDURE(state, [payload]) {
        state.clinicProcedures.push(payload)
    },

    UPDATE_CLINIC_PROCEDURE(state, [id, fieldName, newValue]) {
        const index = state.clinicProcedures.findIndex(i => i.id === id)
        Vue.set(state.clinicProcedures[index], fieldName, newValue)
    },

    REMOVE_CLINIC_PROCEDURE(state, [procID]) {
        const index = state.clinicProcedures.findIndex(i => i.id === procID)
        Vue.delete(state.clinicProcedures, index)
    },

    /*
        Patients
    */
    SET_SEARCHED_PATIENTS(state, payload) {
        state.searchedPatients = payload
    },

    SET_SEARCHED_PATIENT_SLOTS (state, payload) {
        state.searchedPatientSlots = payload
    },

    RESET_SEARCHED_PATIENT_SLOTS (state) {
        state.searchedPatientSlots = []
    },

    SELECT_FILTERED_PATIENT (state, patient_id) {
        state.filteredPatient = state.searchedPatients.find(i => i.id == patient_id)
    },

    SET_FILTERED_PATIENT (state, payload) {
        state.filteredPatient = payload
    },

    RESELECT_FILTERED_PATIENT (state) {
        state.filteredPatient = null
    },

    RESET_INTERIM_PATIENT (state) {
        state.interimPatient = null
    },

    SET_EMPTY_TIMEFRAME(state, flag) {
        state.emptyTimeframe = flag
    },

    SET_PATIENTS (state, patients) {
        state.patients = patients
    },

    SET_INTERIM_PATIENT (state, patient) {
        // holds patient state pending visit addition
        state.interimPatient = patient
    },

    ADD_PATIENT (state, payload) {
        state.patients.push(payload)
    },

    UPDATE_PATIENT (state, [id, payload]) {
        const index = state.patients.findIndex(i => i.id === id)
        const updatee = Object.assign(state.patients[index], payload)
        Vue.set(state.patients, index, updatee)
    },

    SAVE_SMS_STATUSES (state, payload) {
        state.textStatuses = payload.map(i => ({
            ...i,
            date: new Date(i.date)
        }))
    }
}