export default {
    partial_patch_doctors ({commit, rootState}, [vm, select, payload]) {
        const actorID = select.actor
        commit('core/UPDATE_DOCTOR', [
            actorID,
            payload
        ])
        vm.notifySuccess(`Updated details of ${actorID}`)
    },

    partial_post_doctors ({commit}, [vm, empty, payload]) {
        commit('core/SAVE_DOCTORS', {
            payload: [payload]
        })
    }
}