<template>
    <div class="h-screen flex w-full bg-img vx-row no-gutter items-center justify-center">
        <div v-if="!$store.state.auth.passResetEmail" class="vx-col w-4/5 sm:w-4/5 md:w-3/5 lg:w-3/4 xl:w-3/5 mx-auto self-center">
            <vx-card>
                <div slot="no-body" class="full-page-bg-color">
                    <div class="vx-row">
                        <div class="vx-col hidden sm:hidden md:hidden lg:block lg:w-1/2 mx-auto self-center">
                            <!-- <img src="@/assets/images/pages/forgot-password.png" alt="login" class="mx-auto"> -->
                        </div>
                        <div class="vx-col sm:w-full md:w-full lg:w-1/2 mx-auto self-center d-theme-dark-bg">
                            <div class="p-8">
                                <div class="vx-card__title mb-8">
                                    <h4 class="mb-4">{{ $t('headers.reset_pass') }} </h4>
                                    <p>{{ $t('headers.email_prompt_pass_reset') }}</p>
                                </div>
                                <div class="vx-col mb-8">
                                    <vs-input 
                                        name="email"
                                        type="email" 
                                        :label-placeholder="$t('fields.email')" 
                                        v-validate="'required|email'"
                                        v-model="email" 
                                        v-on:keyup.enter="reset"
                                        class="w-full" />
                                    <span class="text-danger text-sm">{{ errors.first('email') }}</span>
                                </div>
                                <vs-button type="border" to="/pages/account/login" class="px-4 w-full md:w-auto">{{ $t('headers.back_to_login') }}</vs-button>
                                <vs-button class="float-right px-4 w-full md:w-auto mt-3 mb-8 md:mt-0 md:mb-0" @click="reset">{{ $t('headers.reset_pass') }} </vs-button>
                            </div>
                        </div>
                    </div>
                </div>
            </vx-card>
        </div>
        <pass-reset-sent-dialog v-else/>
    </div>
</template>

<script>
import PassResetSentDialog from './PassResetSentDialog.vue'
export default {
    data () {
        return {
            email: ''
        }
    },

    components: {
        PassResetSentDialog
    },

    methods: {
        async reset () {
            const valid = await this.$validator.validate()
            if (!valid) return

            const resp = await this.$http.post(
                '/api/actor/pass/reset/',
                JSON.stringify({email: this.email}), 
                {headers: {Range: 'post'}}
            )

            if (resp.status === 422) {
                const errors = this.errors
                return Object.entries(resp.data).forEach(item => {
                    let [keyname, errs] = item
                    errs.forEach(errmsg => {
                        errors.add({
                            field: keyname,
                            msg: errmsg
                        })
                    })
                })
            }
            else if (resp.status !== 204) {
                return this.notifyWarning(
                    this.$t('notifs.failed_reset_pass')
                )
            }

            this.$store.commit('auth/PASSWORD_RESET_LINK_SENT', this.email)

        }
    }
}
</script>
