import { lastDayOfDecade } from "date-fns"

export default {
    doctors: state => {
        return Object.fromEntries(
            Object.entries(state.docs).map(([id, {name, last_name}]) => {
                return [id, name + ' ' + last_name]
            })
        )
	},
	
	docToDateToScheduleMap: state => {
		const res = {}
		Object.entries(state.scheduleMetaMap).forEach(([doctorID, obj]) => {
			res[doctorID] = {}
			obj.forEach(({date, scheduleID}) => {
				res[doctorID][date.format('DD/MM')] = scheduleID
			})
		})
		return res
	},
    
    templatedProcedureCategories: state => {
		const out = {}
		Object.entries(state.procedureTemplates).forEach(([cat, items]) => {
			out[cat] = new Set()
			items.forEach(row => {
				if (row.subgroup) out[cat].add(row.subgroup)
			})
		})
		return out
	},

	activeClinic: (state, _, rootState) => () => {
		const selectedWorkspaceID = rootState.auth.selectedWorkspace.id
		return state.clinics[0] && state.clinics.find(clinic => clinic.id == selectedWorkspaceID)
	},

	activeClinicsTimeLimits: (state, getters) => {
		const clinic = getters.activeClinic()
		const map = {}
		const series = Object.values(clinic.business_hours).filter(i => i[0]).map(item => item.map(i => {
			const tt = i.split(':').map(y => parseInt(y))
			const val = tt[0] * 60 + tt[1]
			map[val] = item
			return val
		}))
		const [min, max] = [Math.min(...series.map(i => i[0])), Math.max(...series.map(i => i[1]))]
		
		return {
			min: [map[min][0], min],
			max: [map[max][1], max]
		}
	},

	scheduleDurationsPerDoctor: state => {
		return Object.fromEntries(Object.entries(state.scheduleMetaMap).map(([doctorID, items]) => {
			return [doctorID, items.map(item => {
				return [item.scheduleID, item.scheduleDuring.map(i => new Date(i))]
			})]
		}))
	},

	scheduleDurationsPerDate: state => {
		const datesToMinLims = {}
		const datesToMaxLims = {}
		Object.values(state.scheduleMetaMap).forEach(docsArrays => {
			docsArrays.forEach(arr => {
				let {date, scheduleDuring} = arr
				date = date.format('DD/MM')
				
				let [scheduleLowerBound, scheduleUpperBound] = scheduleDuring.map(i => (new Date(i)).format('HH:mm'))
				if (!datesToMinLims[date]) {
					datesToMinLims[date] = []
				}
				if (!datesToMaxLims[date]) {
					datesToMaxLims[date] = []
				}
				datesToMinLims[date].push(scheduleLowerBound)
				datesToMaxLims[date].push(scheduleUpperBound)
			})
		})
		return Object.fromEntries(
			Object.entries(datesToMinLims).map(([date, bounds]) => [
				date, [bounds.sort()[0], datesToMaxLims[date].sort().reverse()[0]]
			])
		)
	},

	/*
		Return the list of all events(appointments, breaks and closures),
		for all doctors, by date
	*/
	aggregateAllSchedulerEvents: (state, getters) => (date, vm) => {
		date = date.format('MM/DD')
		const filteredPatientID = (state.filteredPatient || {}).id
		const filterFn = event => event.start.format('MM/DD') === date
		let slots = Object.values(state.schedulerSlots).flatMap(event => event).filter(filterFn)
		if (filteredPatientID) {
			slots = slots.filter(i => i.patientID === filteredPatientID)
			if (!slots[0]) {
			    vm.$store.commit('core/SET_EMPTY_TIMEFRAME', true)
			} else {
			    vm.$store.commit('core/SET_EMPTY_TIMEFRAME', false)
			}
		}
		
		return [
			...slots || [],
			...Object.values(state.schedulerBreakSlots).flatMap(event => event).filter(filterFn) || [],
			...state.interimEvents || [],
			...state.loadingSlots || []
		]
	},

	/*
		Given a doctorID, return all scheduler slots (appointments, breaks and closures)
	*/
	allSchedulerSlotsByDoctor: (state, getters) => (doctorID, vm) => {
		const filteredPatientID = (state.filteredPatient || {}).id
		let slots = state.schedulerSlots[doctorID] || []
		if (filteredPatientID) {
			slots = slots.filter(i => i.patientID === filteredPatientID)
			if (!slots[0]) {
				vm.$store.commit('core/SET_EMPTY_TIMEFRAME', true)
			}
			else {
				vm.$store.commit('core/SET_EMPTY_TIMEFRAME', false)
			}
		}
		else {
			vm.$store.commit('core/SET_EMPTY_TIMEFRAME', false)
		}
		return [
			...slots,
			...(state.schedulerBreakSlots[doctorID] || []),
			...state.interimEvents || [],
			...state.loadingSlots || []
		]
	},

	/*
		Given a doctorID, return all schedule planner slots
	*/
	allPlannerSlotsByDoctorPlannerSlots: state => doctorID => {
		return [
			...(state.schedulerBreakSlots[doctorID] || []).map(item => {
				item = Object.assign({}, item)
				item.resizable = true
				return item
			}),
			...state.plannerSlots[doctorID] || [],
			...state.plannerOutliers[doctorID] || [],
			...(state.schedulerSlots[doctorID] || []).map(item => ({
				...item,
				class: 'appointment-shadow',
				background: true
			}))
		]
	},
}