const actions = {

	async setupScreenLock ( {commit}) {
		const isSet = window.localStorage.getItem('app.laima.screenLock.isSet')
		const ttl = window.localStorage.getItem('app.laima.screenLock.ttl')
		const lockOn = window.localStorage.getItem('app.laima.screenLock.on')
		
		if (!isSet || !JSON.parse(isSet)) {
			return commit('DISABLE_SCREEN_LOCK')
		}
		else if (JSON.parse(isSet)) {
			commit('ENABLE_SCREEN_LOCK')
		}
		if (lockOn && JSON.parse(lockOn)) {
			commit('LOCK_SCREEN')
		}
		if (ttl) {
			commit('SET_LOCK_SCREEN_IDLE_TIME', JSON.parse(ttl))
		}
	},

	toggleNavbar ({commit}, payload) {
		commit('TOGGLE_REDUCE_BUTTON', payload && true || false)
		commit('TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE', payload && true || false)
		commit('UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN', !!payload)
		// commit('UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN', payload && false || true)
		commit('UPDATE_VERTICAL_NAV_MENU_WIDTH', payload && 'reduced' || 'default')
	},

	// Vertical NavMenu
	updateVerticalNavMenuWidth({ commit }, width) {
		commit('UPDATE_VERTICAL_NAV_MENU_WIDTH', width)
	}

}

export default actions
