<template>
<vs-sidebar vs-custom-content position-right parent="body" default-index="-1" color="primary" class="add-edit-slot slot-details" :value="slotID">
    <div class="header-sidebar" slot="header">
        <div class="flex justify-between w-full h-full bg-primary text-white">
            <h5 class="pl-4 flex self-center text-white font-light">{{ slot.name }} {{ slot.last_name }}</h5>
            <feather-icon class="pr-4 text-white cursor-pointer" svgClasses="w-6 h-6" @click="closeSidebar()" icon="XIcon"/>
        </div>
    </div>
    <laima-form
        title="Appointment"
        v-if="slotID"
        ref="visitForm"
        size="small"
        formScope="step-1"
        @updated="changedField"
        @changed="([field, val]) => {field.match('duration') && changedField([{[field]: val}])}"
        :formFields="slotFields"
        :showErrorsInTooltip="false"
        :onSubmit="false"
        :modelsContainer="initModelsContainer">

        <template #before-fields="{errors}">
            <vs-alert class="form-alert flex warning" v-if="readonly" icon-pack="feather" icon="icon-archive">
                <div class="flex items-center">
                    <ul>
                        <li>
                            <h4 class="w-full inline-flex text-black shadow-none"> {{ $t('headers.archived_visit') }} </h4>
                        </li>
                        <li>
                            <span class="w-full inline-flex alert-text font-normal pt-2">
                                {{ $t('headers.archived_visit_desc') }}
                            </span>
                        </li>
                    </ul>
                </div>
            </vs-alert>
            <vs-alert class="form-alert flex danger" v-if="errors[0]"
                        active="true" icon-pack="feather"
                        icon="icon-alert-octagon">
                <div class="flex h-100">
                    <span class="alert-text font-normal">
                        {{ errors[0] }}
                    </span>
                </div>
            </vs-alert>
        </template>

        <template #beforefield="{item}">
            <vs-alert class="form-alert flex" :class="statuses[slot.status].class" v-if="item.name === 'status'"
                        active="true" icon-pack="feather"
                        :icon="statuses[slot.status].icon">
                <div class="flex items-center">
                    <ul>
                        <li>
                            <h5 class="w-full inline-flex text-black shadow-none"> {{ statusHeadlines[slot.status] }} </h5>
                        </li>
                        <li>
                            <span class="w-full inline-flex alert-text font-normal text-sm pt-2" v-html="statuses[slot.status].msg"> </span>
                        </li>
                    </ul>
                </div>
            </vs-alert>
        </template>

        <template #date="{item, hasErrors, successCallback, onFieldUpdated}">
            <vs-dropdown vs-custom-content vs-trigger-click class="heatmap-dropdown cursor-pointer hover:underline">
                <lm-input
                    :danger="hasErrors"
                    :data-vv-as="item.label || item.name"
                    :disabled="item.disabled || false"
                    :name="item.name"
                    :placeholder="item.placeholder || ''"
                    :value="initModelsContainer.date.format('YYYY-MM-DD')"
                />
                <vs-dropdown-menu class="notification-dropdown dropdown-custom" style="z-index: 60004" v-show="!item.disabled" >
                    <monthly-slots-heatmap
                        @dateSelected="date => changedField([{date}])"
                        :date="initModelsContainer.date.format('YYYY-MM')" 
                        :disabled="item.disabled"
                        :doctor="$parent.selectedDoctor.actor"
                        :minDate="new Date()"
                        :set="$parent.currentView === 'week' ? 'docs' : 'all'" />
                </vs-dropdown-menu>
            </vs-dropdown>
        </template>

        <template #footer>
            <div></div>
        </template>
    </laima-form>
    <add-edit-patient :initialData="slot" :readonly="true" ref="patient" size="small" />
</vs-sidebar>
</template>

<script>
import MonthlySlotsHeatmap from './MonthlySlotsHeatmap.vue'

export default {
    components: {
        MonthlySlotsHeatmap
    },
    data () {
        return {
            initModelsContainer: {},
            overridingSchedule: null,
            overridingDoctor: null,
            oldSlot: null
        }
    },
    computed: {
        default_visit_interval () {
            const activeClinic = this.$store.getters['core/activeClinic']()
            return activeClinic && activeClinic.default_visit_interval
        },
        during () {
            const during = this.slot.during || []
            return during.map(dt => dt.format('HH:mm')).join(' - ')
        },
        hourRange () {
            const during = this.slot.during
            const doctorID = this.slot.schedule.doctor
            if (during) {
                const dayIndex = during[0].getDay() - 1
                const [minHour, maxHour] = this.$store.state.core._schedulerClosureHoursEachDoc[dayIndex].doctors[doctorID].scheduleDuring.map(i => (new Date(i).format('HH')))
                return [
                    parseInt(minHour), 
                    parseInt(maxHour)
                ]
            }
            return [8, 18]
        },
        notifOffset () {
            return this.$parent.clinic.send_confirmation_before
        },
        readonly () {
            return this.slot.during[0] < new Date()
        },
        slot () {
            return this.$store.state.core.selectedSlotDetails
        },
        slotID () {
            return this.$store.state.core.viewSlotPrompt
        },
        slotDate () {
            const during = this.slot.during || [new Date(0)]
            return during[0].format('DD/MM/YY')
        },
        slotFields () {
            return [
                {
                    name: 'status',
                    disabled: this.readonly || this.slot.status === 'confirmed',
                    label: this.$t('fields.confirmed'),
                    type: 'switch'
                },
                {
                    name: 'doctor',
                    label: this.$t('fields.doctor'),
                    disabled: this.readonly,
                    type: 'select',
                    options: Object.values(this.$parent.docs).map(i=>({value: i.actor, text: i.name + ' ' + i.last_name})),
                    validators: {required: true}
                },
                {
                    name: 'date',
                    label: this.$t('fields.visit_date'),
                    disabled: this.readonly,
                    type: 'date',
                    validators: {required: true}
                },
                {
                    name: 'startTime',
                    label: this.$t('fields.startTime'),
                    disabled: this.readonly,
                    type: 'time',
                    interval: this.default_visit_interval,
                    hourRange: [this.hourRange],
                    validators: {required: true}
                },
                {
                    name: 'duration',
                    helpText: this.$t('help_texts.duration'),
                    label: this.$t('fields.duration'),
                    disabled: this.readonly,
                    classes: ['duration-input'],
                    type: 'fancyNumber',
                    step: this.default_visit_interval,
                    min: this.default_visit_interval,
                    max: 180
                },
                {
                    name: 'category',
                    label: this.$t('fields.procedure_category'),
                    disabled: this.readonly,
                    type: 'select',
                    options: this.procCategories
                },
            ]
        },
        statusHeadlines () {
            return {
                booked: this.$t('misc.visit_booked'),
                confirmation_pending: this.$t('misc.confirmation_pending'),
                confirmed: this.$t('misc.visit_confirmed')
            }
        },
        statuses () {
            return {
                booked: {
                    icon: 'icon-info',
                    class: 'primary',  
                    msg: this.$t('misc.booked_msg', {notifOffset: this.notifOffset * 24})
                },
                confirmation_pending: {
                    icon: 'icon-alert-triangle',
                    class: 'warning',
                    msg: this.$t('misc.pending_msg')
                },
                confirmed: {
                    icon: 'icon-check',
                    class: 'success',
                    msg: this.$t('misc.confirmed_msg')
                }
            }
        }
    },

    methods: {
        async changedField ([{startTime, duration, date, category, doctor, status}]) {
            document.body.click()
            this.prepareShadowInterimSlot()
            let successMsg = []
            let forUpdate = {}
            let [start, end] = this.slot.during
            let [oldStart, oldEnd] = this.slot.during.map(i => i.clone())
            let [hours, minutes] = start.format('HH:mm').split(':')
            const origDoctorID = this.slot.schedule.doctor
            const delta = (oldEnd - oldStart) / 6e+4

            if (date) {
                this.$set(this.initModelsContainer, 'date', date)
                let [year, month, day] = date.format('YYYY:MM:DD').split(':')
                month = parseInt(month) - 1
                start.setDate(day)
                start.setMonth(month)
                start.setYear(year)
                start.setHours(hours)
                start.setMinutes(minutes)

                end = start.addMinutes(delta)
                end.setDate(day)
                end.setMonth(month)
                end.setYear(year)

                this.$store.commit('core/UPDATE_SLOT_DETAILS', {during: [start, end]})
                forUpdate = {
                    during: [start, end]
                }
                // change active date
                this.$parent.currentDate = date

                // TODO: don't fetch the slots if we're still in the same week
                const data = {
                    min_date: date.format('YYYY-MM-DD')
                }
                this.$store.dispatch('core/fetchSlots', {
                    data,
                    currentDate: date,
                    bh: this.$parent.clinic.business_hours,
                    vm: this.$parent
                })

                if (day != oldStart.getDate()) {
                    await this.sleep(1)
                    const scheduleMap = this.$store.getters['core/docToDateToScheduleMap'][origDoctorID]
                    const mth = start.format('DD/MM')
                    while (!scheduleMap[mth]) {
                        await this.sleep(.1)
                    }
                    let newScheduleID = scheduleMap[mth]
                    this.overridingSchedule = newScheduleID
                    this.$store.commit('core/UPDATE_SLOT_DETAILS', {schedule: {id: newScheduleID, doctor: origDoctorID}})
                }
                successMsg = ['date', start]
            }

            else if (startTime) {
                this.$set(this.initModelsContainer, 'startTime', startTime)
                let [hrs, mins] = startTime.split(':')
                start.setHours(hrs)
                start.setMinutes(mins)
                end = start.addMinutes(this.initModelsContainer.duration || delta)
                this.$store.commit('core/UPDATE_SLOT_DETAILS', {during: [start, end]})
                forUpdate = {during: [start, end]}
                successMsg = ['start', start]
            }

            else if (duration) {
                this.$set(this.initModelsContainer, 'duration', duration)
                end = start.clone().addMinutes(duration)
                this.$store.commit('core/UPDATE_SLOT_DETAILS', {during: [start, end]})
                forUpdate = {during: [start, end]}
                successMsg = ['during', (end - start) / 1000]
            }

            else if (category) {
                this.$set(this.initModelsContainer, 'category', category)
                forUpdate = {procedure_category: category}
                successMsg = ['category', category]
            }

            else if (doctor) {
                // update the selected doctor globally
                const newDoctorID = doctor
                const doctorDetails = this.$parent.docs[newDoctorID]
                const scheduleMap = this.$store.getters['core/docToDateToScheduleMap'][newDoctorID]
                const mth = start.format('DD/MM')
                let newScheduleID = scheduleMap[mth]
                this.overridingSchedule = newScheduleID
                this.overridingDoctor = newDoctorID
                this.$store.commit("core/SET_ACTIVE_DOCTOR", doctorDetails)
                this.$store.commit('core/UPDATE_SLOT_DETAILS', {schedule: {id: newScheduleID, doctor: newDoctorID}})
                successMsg = ['doctor', doctorDetails]
            }

            else if (status) {
                return this.$store.dispatch('core/confirmAppointment', {vm: this, slotID: this.slotID, doctorID: origDoctorID})
            }
            
            // set the interim event
            const interimPayload = {
                id: this.slotID,
				start,
                end,
				class: 'interim-slot',
				title: this.slot.name + ' ' + this.slot.last_name,
				split: parseInt(this.slot.schedule.doctor)
            }
            if (doctor) {
                interimPayload.split = doctor
            }

            if (start.getTime() != oldStart.getTime() || end.getTime() != oldEnd.getTime() || category || doctor) {
                this.$store.commit('core/UPDATE_INTERIM_SLOT', interimPayload)
                 if (!this.oldSlot) {
                    let slot = this.$store.state.core.schedulerSlots[origDoctorID].find(i => i.id === this.slotID)
                    this.oldSlot = slot
                }
                this.$store.commit('core/REMOVE_SCHEDULE_SLOT', [null, this.slotID])
                
                if (this.overridingSchedule) {
                    forUpdate = {
                        during: [start, end],
                        schedule: this.overridingSchedule
                    }
                    interimPayload['scheduleID'] = this.overridingSchedule
                }

                this.$store.dispatch('core/editSlot', [this.slotID, interimPayload, forUpdate, successMsg, this])
            }
        },

        closeSidebar () {
            // this.clearForm()
            if (this.oldSlot) {
                this.$store.commit('core/ADD_SCHEDULER_SLOT', this.oldSlot)
                this.oldSlot = null
            }
            this.toggleBodyOverflow()
            this.overridingSchedule = null
            this.overridingDoctor = null
            this.$store.commit('core/RESET_INTERIM_SLOT')
            this.$store.commit('core/SET_VIEW_SLOT_PROMPT', false)
        },

        prepareShadowInterimSlot () {
            if (this.$store.state.core.interimEvents.length) return
            let [start, end] = this.slot.during
            const interimPayload = {
                start,
                end,
                class: 'interim-slot',
                title: this.slot.name + ' ' + this.slot.last_name,
                split: parseInt(this.slot.schedule.doctor)
            }
            this.$store.commit('core/UPDATE_INTERIM_SLOT', interimPayload)
        }
    },
    mounted () {
        window.slot = this
    },
    watch: {
        slot (slot) {
            let delta, startHour, category, date, doctorID
            const during = slot.during
            if (during[0]) {
                date = new Date(during[0])
                delta = during[1] - during[0]
                startHour = during[0]
                category = slot.procedure_category
                doctorID = slot.schedule.doctor
            }
            const doctorDetails = this.$parent.docs[doctorID]
            
            this.initModelsContainer = {
                category,
                date,
                status: slot.status === 'confirmed',
                doctor: doctorID,
                duration: delta / 6e+4,
                startTime: startHour.format('HH:mm')
            }
        },
        slotID (val) {
            if (val) {
                window.addEventListener('keydown', (e) => {
                    if (e.key == 'Escape') {
                        this.closeSidebar ()
                    }
                }, {once: true})
            }
        }
    }
}
</script>

<style lang="scss">
.slot-details {
    .vs__dropdown-toggle {
        max-width: 229px;
    }
    .field-desc {
        width: 40% !important;
        .field-help {
            min-width: 250px;
        }
    }
    .field-value-wrap {
        width: 60% !important;
    }
}
</style>