<template>
<div style="height: inherit; z-index: 110; width: inherit" :id="`s-${event.id}`" v-show="active">
    <div :id="`line-${event.id}`" style="width: inherit" class="time-mark absolute bg-dark" >
        <span class="absolute left-0 text-dark" style="top: -16px"><small>{{ text }}</small></span>
        <div class="w-full" style="height: 15px; cursor: copy;">
            <span class='current-clock text-black'>{{ (timemark || event.start).format('HH:mm') }}</span>
        </div>
    </div>
</div>
</template>

<script>
export default {
    props: ['event', 'roundTo'],
    data () {
        return {
            active: false,
            timemark: null,
            text: '',
            scrollBias: 0
        }
    },
    mounted () {
        this.$emit('mounted')
    },
    methods: {
        mouseMoveTimemark (sel, timeStart, minutesPerPixel, topOffset) {
            const self = this
            return e => {
                const offsetY = (e.clientY - topOffset) + self.scrollBias
                sel.style.top = `${offsetY}px`
                let minutes = Math.round(offsetY * minutesPerPixel)
                const reminder = minutes % this.roundTo
                this.timemark = timeStart.addMinutes(minutes - reminder)
            }
        },
        startTimemarker (text) {
            this.active = true
            this.text = text
            const event = this.event
            const $timemark = document.querySelector(`#line-${event.id}`)
            const $weekday = document.querySelector(`#s-${event.id}`).parentElement
            const parentID = $weekday.id || Math.random().toString(36).slice(2)
            if (!$weekday.id) $weekday.id = parentID
            
            const topOffset = $weekday.getBoundingClientRect().top
            const totalMinutes = (event.end - event.start) / 6e+4
            
            const minutesPerPixel = totalMinutes / $weekday.offsetHeight
            const eventFn = this.mouseMoveTimemark($timemark, event.start.clone(), minutesPerPixel, topOffset)
            const self = this
            const scrollUpdater = () => {
                self.scrollBias = window.scrollY
            }
            window.addEventListener('scroll', scrollUpdater)
            const selfListener = () => {
                this.$emit('eventcreated', [event, this.timemark])
                this.active = false
                this.scrollBias = 0
                this.timemark = null
                $weekday.removeEventListener('mousemove', eventFn)
                window.removeEventListener('scroll', scrollUpdater)
            }
            $weekday.addEventListener('mousemove', eventFn, {passive: true, capture: true})
            document.body.addEventListener('click', function bodyBlurListener (e) {
                if (!e.path.find(el => el.id == parentID)) {
                    // blur, destruct
                    document.body.removeEventListener('click', bodyBlurListener)
                    self.timemark = null
                    self.active = false
                    self.scrollBias = 0
                    window.removeEventListener('scroll', scrollUpdater)
                    $weekday.removeEventListener('click', selfListener)
                    $weekday.removeEventListener('mousemove', eventFn)
                }   
            })
            
            setTimeout(() => {
                $weekday.addEventListener('click', selfListener, {once: true})
            }, 1000)
        },
    },
    watch: {
        active (val) {
            if (!val) this.$emit('closed')
        }
    }
}
</script>