<template>
    <div class="clearfix">
        <vs-input
            autocomplete="off"
            v-validate="'required|alpha_dash|min:3'"
            data-vv-validate-on="blur"
            name="username"
            :data-vv-as="$t('fields.username').toLowerCase()"
            :label-placeholder="$t('fields.name')"
            :placeholder="$t('fields.username')"
            v-model="username"
            class="w-full" />
        <span class="text-danger text-sm">{{ errors.first('username') }}</span>

        <vs-input
            autocomplete="off"
            v-if="!isInvited"
            v-validate="'required|email'"
            data-vv-validate-on="blur"
            name="email"
            type="email"
            :data-vv-as="$t('fields.email').toLowerCase()"
            :label-placeholder="$t('fields.email')"
            :placeholder="$t('fields.email')"
            v-model="email"
            class="w-full mt-6" />
        <span class="text-danger text-sm">{{ errors.first('email') }}</span>

        <vs-input
            autocomplete="off"
            v-if="isInvited"
            v-validate="'required'"
            data-vv-validate-on="blur"
            name="phone"
            type="text"
            :data-vv-as="$t('fields.phone').toLowerCase()"
            :label-placeholder="$t('fields.phone')"
            :placeholder="$t('fields.phone')"
            v-model="phone"
            class="w-full mt-6" />
        <span class="text-danger text-sm">{{ errors.first('phone') }}</span>

        <vs-input
            ref="password"
            type="password"
            data-vv-validate-on="blur"
            v-validate="'required|min:6|max:10'"
            name="password"
            :data-vv-as="$t('fields.password').toLowerCase()"
            :label-placeholder="$t('fields.password')"
            :placeholder="$t('fields.password')"
            v-model="password"
            class="w-full mt-6" />
        <span class="text-danger text-sm">{{ errors.first('password') }}</span>

        <vs-input
            type="password"
            v-validate="'required|min:6|max:10|confirmed:password'"
            data-vv-validate-on="blur"
            :data-vv-as="$t('fields.password').toLowerCase()"
            name="confirm_password"
            :label-placeholder="$t('fields.confirm_pass')"
            :placeholder="$t('fields.confirm_pass')"
            v-model="confirm_password"
            v-on:keyup.enter="registerUser"
            class="w-full mt-6" />
        <span class="text-danger text-sm">{{ errors.first('confirm_password') }}</span>

        <vs-checkbox v-model="isTermsConditionAccepted" class="mt-6">{{ $t('headers.tc_accepted') }}</vs-checkbox>
        <vs-button type="border" to="/pages/account/login" class="mt-6">{{ $t('headers.login') }}</vs-button>
        <vs-button class="float-right mt-6" @click="registerUser" :disabled="!validateForm">{{ $t('headers.register')  }}</vs-button>
    </div>
</template>

<script>
export default {
    data () {
        return {
            username: '',
            email: '',
            password: '',
            phone: '',
            confirm_password: '',
            isTermsConditionAccepted: true
        }
    },
    computed: {
        validateForm () {
            return !this.errors.any()
        },
        isInvited () {
            return this.$route.query.inv
        }
    },
    methods: {
        async checkLogin () {
          // If user is already logged in notify
            // if(await this.$store.state.auth.isUserLoggedIn()) {
            //     this.notifyWarning(this.$t('notifs.already_logged'))
            //     return false
            // }
            return true
        },
        async registerUser () {
            // If form is not validated or user is already login return
            await this.$validator.validate()
            if (!this.validateForm || !(await this.checkLogin())) return

            const payload = {
                userDetails: {
                    username: this.username,
                    email: this.email,
                    password: this.password
                },
                errors: this.errors
            }
            if (this.phone) {
                payload.userDetails.phone = this.phone
            }
            if (this.isInvited) payload['isInvited'] = this.isInvited
            this.$store.dispatch('auth/registerUser', [payload, this])
        }
    }
}
</script>
