let parser = document.createElement('a')
parser.href = ''
const HOST = `https://${parser.host}`

export default {
	SET_NEEDED_PHONE_VERIF (state, flag) {
		state.phoneVerificationNeeded = flag
	},
	SET_SHIELDED_RESPONSE (state, payload) {
		state.shieldedResponse = payload
	},
	RESOURCE_LOCKED (state, payload) {
		state.resourceLocked = true
		state.pendingShieldedRequest = payload
	},
	RESOURCE_UNLOCK (state) {
		state.resourceLocked = false
		state.pendingShieldedRequest = {}
	},
	SAVE_SCOPES (state, scopes) {
		state.scopes = scopes
	},
	SAVE_ROLES (state, roles) {
		state.roles = roles
	},
	SAVE_LOCALE (state, payload) {
		state.langs = (payload.langs || '').split(',')
		state.stateCode = payload.state
		state.currency = payload.currency
	},
	ADD_NEW_WORKSPACE (state, payload) {
		state.availableWorkspaces.push(payload)
		const actorInfo = JSON.parse(localStorage.getItem('actorInfo'))
		actorInfo.workspaces[payload.id] = payload.name
		localStorage.setItem('app.laima.actorInfo', JSON.stringify(actorInfo))
	},
    SET_AVAILABLE_WORKSPACES (state, payload) {
        // invert keys with values
        state.availableWorkspaces = Object.entries(payload).map(([k, v]) => {
            return {
                name: v,
                id: k
            }
        })
    },
    SET_CURRENT_WORKSPACE(state, workspace_obj) {
        state.selectedWorkspace = workspace_obj
	},
	SET_USER_CONTEXT (state, ctxt) {
		state.userContext = ctxt
	},
    SET_ACTOR_CONTEXT (state, ctxt) {
        state.actorContext = ctxt
    },
	SET_ACTOR_LOADING (state, flag) {
		state.actorContextLoading = flag
	},
	SET_META_LOADING (state, flag) {
		state.metaContextLoading = flag
	},
    SET_CURRENT_SPEC_HASHSUM (state, hashsum) {
        state.localSpecHashsum = hashsum
	},
	SET_CURRENT_SPEC (state, spec) {
		const new_spec = Object.fromEntries(Object.entries(spec).map(item => {
			let [urlmethod, obj] = item
			let [url, method] = urlmethod.split(':')
			return [
				`${method}_${url}`, {
					method,
					url: HOST + obj.url,
					authed: obj.authed,
					schema: obj.schema.properties,
					required: obj.schema.required || []
				}
			]
		}))
		state.spec = new_spec
	},
	EMAIL_VERIFICATION_SENT (state, payload) {
		if (payload) {
			state.pendingEmail = payload.email
		}
		state.emailVerifRetryIn = 60
	},
	EMAIL_VERIF_BACKOFF_DECR (state) {
		if (state.emailVerifRetryIn >= 0 || !state.pendingEmail) return
		state.emailVerifRetryIn--
	},
	PASSWORD_RESET_LINK_SENT (state, email) {
		if (email) {
			state.passResetEmail = email
		}
	},
	PASS_RESET_BACKOFF_DECR (state) {
		if (state.passResetRetryIn >= 0 || !state.passResetEmail) return
		state.passResetRetryIn--
	},

	USER_MISSING(state, flag) {
		state.userMissing = flag
	},

	UPDATE_ACTOR_CONTEXT (state, payload) {
		const new_ctxt = Object.assign(state.actorContext, payload)
		state.actorContext = new_ctxt
		localStorage.setItem('app.laima.actorInfo', JSON.stringify(new_ctxt))
	},

	UPDATE_USER_CONTEXT (state, payload) {
		const new_ctxt = Object.assign(state.userContext, payload)
		state.userContext = new_ctxt
		const metaContext = JSON.parse(localStorage.getItem('app.laima.metaContext'))
		metaContext.user = new_ctxt
		localStorage.setItem('app.laima.metaContext', JSON.stringify(metaContext))
	}
}