export default {
    shieldMethod: state => {
        try {
            return state.pendingShieldedRequest.data.method
        }
        catch (e) {
            return null
        }
    },
    shieldCause: state => {
        try {
            return state.pendingShieldedRequest.data.cause
        }
        catch (e) {
            return null
        }
    },
    getUserContext: state => {
        const ctxt = state.userContext || {}
        if (Object.keys(ctxt)[0]) {
            let cloned = Object.assign({}, ctxt)
            cloned.fullname = cloned.name + ' ' + cloned.last_name
            delete cloned.name
            delete cloned.last_name
            return cloned
        }
    },
    workspaceIdToName: state => {
        return Object.fromEntries(state.availableWorkspaces.map(item => [item.id, item.name]))
    },
    heldRoles: state => state.actorContext.roles,
    heldScope: state => state.actorContext.scope,
    currencyFormat: state => {
        return new Intl.NumberFormat(`${state.langs[0]}-${state.stateCode}`, {
            style: 'currency',
            currency: state.currency
        })
    }
}