import Vue from 'vue'
import db from '@/db.js'

const SLOT_EDIT_CONTEXT_FNS = {
    category: msg => ({msg}),
    date: msg => ({msg: msg.format('DD/MM/YYYY HH:mm')}),
    doctor: doc => ({name: doc.name, last_name: doc.last_name}),
    during: msg => ({msg: msg / 60}),
    start: msg => ({msg: msg.format('HH:mm')}),
    end: msg => ({msg: msg.format('HH:mm')}),
    default: () => {}
}

export default {
    async editClinic ({commit}, [vm, clinicID, payload, callback]) {
        let clinicPayload = {
            select: {
                id: clinicID
            },
            payload
        }
        const clinicUpdateResponse = await vm.requestResource('patch_clinic', {
            vm,
            data: clinicPayload
        })
        if (clinicUpdateResponse.status === 200) {
            commit('PATCH_CLINIC', {
                clinicID,
                payload
            })
            await db.clinic.where({id: clinicID}).modify(payload)
            callback()
        }
        else {
            vm.$notifyError(vm.$t('notifs.clinic_update_failed'))
        }
    },
    async resizeSlot ({commit, state}, [eventPassed, vm]) {
        const {event, oldDate} = eventPassed
        const originalID = event.id
        const {doctorID} = event
        let start = new Date(event.start)
        let newEnd = new Date(event.end)
        const startTime = start.getTime()
        const endTime = newEnd.getTime()

        const oldPayload = {
            ...event,
            start,
            end: oldDate
        }

        if (oldDate < new Date()) {
            commit('REMOVE_SCHEDULE_SLOT', [doctorID, originalID])
            commit('ADD_SCHEDULER_SLOT', oldPayload)
            return vm.notifyError(vm.$t('notifs.visit_in_the_past'))
        }

        const slotsClear = state.schedulerSlots[doctorID].every(item => {
            if (item.id === originalID) return true
            const localStart = item.start.getTime()
            const localEnd = item.end.getTime()
            return ((startTime < localStart && endTime <= localStart) || (startTime >= localEnd && endTime > localEnd))
        })
        
        if (!slotsClear) {
            vm.notifyError(vm.$t('notifs.visit_would_overlap'))
            commit('REMOVE_SCHEDULE_SLOT', [doctorID, originalID])
            return commit('ADD_SCHEDULER_SLOT', oldPayload)
        }

        const schedulerBreakSlots = state.schedulerBreakSlots[doctorID] || []
        const breaksClear = schedulerBreakSlots.every(item => {
            if (item.id === originalID) return true
            const localStart = item.start.getTime()
            const localEnd = item.end.getTime()
            return ((startTime < localStart && endTime <= localStart) || (startTime >= localEnd && endTime > localEnd))
        })
        if (!breaksClear) {
            vm.notifyError(vm.$t('notifs.cannot_overlap_with_break'))
            commit('REMOVE_SCHEDULE_SLOT', [doctorID, originalID])
            return commit('ADD_SCHEDULER_SLOT', oldPayload)
        }

        // all clear, proceed with commiting the changes
        const during = [
            start.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`),
            newEnd.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`)
        ]
        const data = {
            select: { id: originalID },
            payload: { during }
        }
        const resp = await vm.requestResource('put_scheduleslot', {
            vm, data
        })
        commit('REMOVE_SCHEDULE_SLOT', [doctorID, originalID])

        if (resp.status === 200) {
            commit('ADD_SCHEDULER_SLOT', {
                ...oldPayload,
                end: newEnd
            })

            const context = SLOT_EDIT_CONTEXT_FNS.end(newEnd)
            const msg = vm.$t('slot_edit_msgs.end', context)
            vm.notifySuccess(msg)
        }
        else {
            const data = resp.data
            let msg = 'Failed to change the appointment duration'
            if (data.during && data.during[0].match('office')) {
                msg = 'No available offices'
            }
            commit('ADD_SCHEDULER_SLOT', oldPayload)
            vm.notifyError(msg)
        }
    },

    async editSlot({ commit }, [slotID, interimPayload, forUpdate, successMsg, vm]) {
        // edit the slot in SlotDetails component
        const newStart = interimPayload.start
        const newEnd = interimPayload.end
        const doctorID = interimPayload.split
        const scheduleID = interimPayload.scheduleID
        if (forUpdate.during) {
            forUpdate['during'] = forUpdate.during.map(i => i.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`))
        }
        const payload = {...forUpdate}

        const resp = await vm.requestResource('put_scheduleslot', {
            vm: vm.$refs.visitForm,
            scope: 'step-1',
            errRespMap: {
                during: 'duration'
            },
            data: {
                select: {
                    id: slotID
                },
                payload
            }
        })
        if (resp.status === 200) {
            commit('ADD_SCHEDULER_SLOT', {
                ...vm.oldSlot,
                id: interimPayload.id,
                start: newStart,
                end: newEnd,
                split: doctorID,
                title: interimPayload.title,
                scheduleID
            })
            vm.oldSlot = null
            vm.overridingSchedule = null
            vm.overridingDoctor = null
            commit('RESET_INTERIM_SLOT')
            
            let fieldUpdated = successMsg[0] || 'default'
            const context = SLOT_EDIT_CONTEXT_FNS[fieldUpdated](successMsg[1])
            const msg = vm.$t(`slot_edit_msgs.${fieldUpdated}`, context)

            setTimeout(() => {
                vm.notifySuccess(msg)
            }, 500)
        }
        else {
            commit('UPDATE_INTERIM_SLOT', {class: 'interim-overlap'})
        }
    },

    async dragSlot ({commit, getters}, [event, vm]) {
        // the first step is to delete the old event, hold a copy of it and perform the desired validations.
        // if all's clear, add the updated event. Otherwise, restore the old.
        const originalID = event.originalEvent.id
        const originalDoctorID = event.originalEvent.doctorID
        const newStart = new Date(event.event.start)
        const newEnd = new Date(event.event.end)
        commit('REMOVE_SCHEDULE_SLOT', [originalDoctorID, originalID])
        commit('ADD_LOADING_SLOT', [newStart, newEnd, event.originalEvent.split])
        const originalStart = new Date(event.originalEvent.start)
        const originalEnd = new Date(event.originalEvent.end)
        
        const oldPayload = {
            ...event.event,
            start: originalStart,
            end: originalEnd
        }
        
        if (new Date(event.originalEvent.start) < new Date()) {
            commit('ADD_SCHEDULER_SLOT', oldPayload)
            commit('REMOVE_LOADING_SLOT')
            return vm.notifyError(vm.$t('notifs.visit_in_the_past'))
        }

        const payload = {
            during: [
                newStart.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`),
                newEnd.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`)
            ]
        }

        const doctorChanged = event.oldSplit !== event.newSplit

        // need to update schedule ID?
        if (originalStart.format('DD/MM') !== newStart.format('DD/MM') || doctorChanged) {
            payload.schedule = getters.docToDateToScheduleMap[originalDoctorID][newStart.format('DD/MM')]
            oldPayload.scheduleID = payload.schedule
        }
        
        // need to update doctor ID?
        if (doctorChanged) {
            payload.schedule = getters.docToDateToScheduleMap[event.newSplit][newStart.format('DD/MM')]
            oldPayload.split = event.newSplit
        }

        // add the new event
        let data = { 
            payload,
            select: { id: originalID }
        }
        
        const resp = await vm.requestResource('put_scheduleslot', {
            vm,
            data
        })
        commit('REMOVE_LOADING_SLOT')
        if (resp.status === 200) {
            commit('ADD_SCHEDULER_SLOT', {
                ...oldPayload,
                start: newStart,
                end: newEnd
            })
            vm.notifySuccess(vm.$t('notifs.visit_moved', {newStart: newStart.format('DD/MM HH:mm')}))
            if (doctorChanged) {
                const context = SLOT_EDIT_CONTEXT_FNS.doctor(vm.docs[event.newSplit])
                const msg = vm.$t('slot_edit_msgs.doctor', context)
                setTimeout(() => {
                    vm.notifySuccess(msg)
                }, 200)
            }
        }
        else {
            const data = resp.data
            let msg = vm.$t('notifs.visit_move_failed') 
            if (data.during && data.during[0].match('office')) {
                msg = vm.$t('overlapMessages.main')
            }
            commit('ADD_SCHEDULER_SLOT', oldPayload)
            vm.notifyError(msg)
        }
    },

    async resizeBreak ({commit, state}, [eventPassed, vm]) {
        const {event, oldDate} = eventPassed
        const originalID = event.id
        const {scheduleID, doctorID} = event
        const newStart = new Date(event.start)
        const newEnd = new Date(event.end)
        const endOfSchedule = new Date(state._schedulerClosureHoursEachDoc[(newStart.getDay() - 1)].doctors[doctorID].scheduleDuring[1])

        const oldPayload = {
            class: 'lunch',
            id: originalID,
            doctorID,
            scheduleID,
            start: new Date(event.start),
            end: oldDate
        }

        if (newEnd < new Date()) {
            vm.notifyError(vm.$t('notifs.no_change_expired_schedule'))
            commit('REMOVE_SCHEDULE_BREAK', [doctorID, originalID])
            return commit('ADD_SCHEDULER_BREAK_SLOT', oldPayload)
        }
        
        if (newEnd.getTime() > endOfSchedule.getTime()) {
            vm.notifyError(vm.$t('notifs.no_break_outside_schedule'))
            commit('REMOVE_SCHEDULE_BREAK', [doctorID, originalID])
            return commit('ADD_SCHEDULER_BREAK_SLOT', oldPayload)
        }

        const slotsClear = state.schedulerSlots[doctorID].every(item => {
            const localStart = item.start.getTime()
            const localEnd = item.end.getTime()
            return ((newStart < localStart && newEnd <= localStart) || (newStart >= localEnd && newEnd > localEnd))
        })
        if (!slotsClear) {
            vm.notifyError(vm.$t('overlapMessages.changeWouldOverlapSlot'))
            commit('REMOVE_SCHEDULE_BREAK', [doctorID, originalID])
            return commit('ADD_SCHEDULER_BREAK_SLOT', oldPayload)
        }
        
        const breaksClear = state.schedulerBreakSlots[doctorID].every(item => {
            if (item.id === originalID) return true
            const localStart = item.start.getTime()
            const localEnd = item.end.getTime()
            return ((newStart < localStart && newEnd <= localStart) || (newStart >= localEnd && newEnd > localEnd))
        })
        if (!breaksClear) {
            vm.notifyError(vm.$t('overlapMessages.no_overlap_with_breaks'))
            commit('REMOVE_SCHEDULE_BREAK', [doctorID, originalID])
            return commit('ADD_SCHEDULER_BREAK_SLOT', oldPayload)
        }

        // all clear. Commit the changes
        const oldBreaks = state.schedulerBreakSlots[doctorID].filter(item => item.class === 'lunch' && item.id !== originalID && item.start.format('MM-DD') === oldDate.format('MM-DD'))
        const breaksFormatted = oldBreaks.map(item => {
            return [item.start, item.end].map(datestr => (new Date(datestr).format('HH:mm')))
        })
        // add the new event
        breaksFormatted.push([
            newStart.format('HH:mm'),
            newEnd.format('HH:mm')
        ])
        let data = {
            select: { id: scheduleID },
            payload: { gaps: breaksFormatted }
        }
        const resp = await vm.requestResource('put_schedule', {
            vm,
            data
        })
        commit('REMOVE_SCHEDULE_BREAK', [doctorID, originalID])
        if (resp.status === 200) {
            commit('ADD_SCHEDULER_BREAK_SLOT', {
                doctorID,
                scheduleID,
                class: 'lunch',
                id: originalID,
                start: new Date(newStart),
                end: new Date(newEnd)
            })
            vm.notifySuccess(
                vm.$t('notifs.break_changed', {newEnd: newEnd.format('HH:mm')})
            )
        } 
        else {
            commit('REMOVE_SCHEDULE_BREAK', [doctorID, originalID])
            commit('ADD_SCHEDULER_BREAK_SLOT', oldPayload)
            vm.notifyError(vm.$t('notifs.break_changed_failed'))
        }
    },

    async resizeSchedule ({commit, state, dispatch}, [eventPassed, vm]) {
        const {event, oldDate} = eventPassed
        const dateStr = oldDate.format('MM-DD')
        const originalID = event.id
        const {doctorID} = event
        const start = new Date(event.start)
        const newEnd = new Date(event.end)  
        const newEndTime = newEnd.getTime()
        const scheduleDate = new Date(start.format('YYYY-MM-DD'))
        scheduleDate.setHours(0)
        scheduleDate.setMinutes(0)
        const businessHours = state.businessHours[scheduleDate]

        if (newEndTime < new Date()) {
            vm.notifyError(vm.$t('notifs.no_change_expired_schedule'))
            return commit('UPDATE_SCHEDULE_DURATION', [doctorID, originalID, oldDate])
        }

        if (newEndTime > businessHours[1].getTime()) {
            vm.notifyError(vm.$t('notifs.no_extend_schedule_outside_clinic'))
            return commit('UPDATE_SCHEDULE_DURATION', [doctorID, originalID, oldDate])
        }
        const schedulerSlots = (state.schedulerSlots[doctorID] || [])
        const slotsClear = schedulerSlots.filter(item => item.start.format('MM-DD') === dateStr).every(item => {
            const localStart = item.start.getTime()
            const localEnd = item.end.getTime()
            return (localEnd <= newEndTime && localStart < newEndTime)
        })
        if (schedulerSlots.length && !slotsClear) {
            vm.notifyError(vm.$t('overlapMessages.changeWouldOverlapSlot'))
            return commit('UPDATE_SCHEDULE_DURATION', [doctorID, originalID, oldDate])
        }

        const schedulerBreakSlots = (state.schedulerBreakSlots[doctorID] || [])
        const breaksClear = schedulerBreakSlots.filter(item => item.start.format('MM-DD') === dateStr).every(item => {
            if (item.id === originalID) return true
            const localStart = item.start.getTime()
            const localEnd = item.end.getTime()
            return (localEnd <= newEndTime && localStart < newEndTime)
        })
        if (!breaksClear) {
            vm.notifyError(vm.$t('overlapMessages.changeWouldOverlapBreak'))
            return commit('UPDATE_SCHEDULE_DURATION', [doctorID, originalID, oldDate])
        }

        let data = {
            select: { id: originalID },
            payload: {
                during: [
                    start.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`),
                    newEnd.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`)
                ]
            }
        }
        
        const resp = await vm.requestResource('put_schedule', { vm, data })
        if (resp.status === 200) {
            data = {
                min_date: vm.$refs.vuecal.view.endDate.toJSON().split('T')[0]
            }
            dispatch('fetchSlots', {
                vm: vm.$parent,
                data,
                bh: vm.$parent.clinic.business_hours
            })
            vm.emitSchedulerUpdate()
            vm.notifySuccess(vm.$t('notifs.schedule_set', {newEnd: newEnd.format('HH:mm')}))
        }
        else {
            vm.notifyError(vm.$t('notifs.schedule_end_change_failed'))
        }
    },

    async editPatient ({commit, state}, [select, payload, vm]) {
        const resp = await vm.requestResource('patch_patientresource', {
            data: {
                select,
                payload
            },
            vm
        })
        if (resp.status === 200) {
            commit('UPDATE_PATIENT', [select.id, payload])
            return vm.notifySuccess(vm.$t('notifs.patient_updated'))
        }
        vm.notifyError(vm.$t('notifs.patient_update_failed'))
        vm.patientSidebarActive = false
    },

    async updateProcedureTemplate ({commit}, [code, procID, fieldName, value, labelValue, vm]) {
        const data = {
            select: {
                code
            },
            payload: {
                [fieldName]: value
            }
        }
        const resp = await vm.requestResource('patch_proceduretemplate', {
            loadingIn: '.is-selected .whitespace-no-wrap',
            data,
            vm
        })
        if (resp.status === 200) {
            commit('UPDATE_CLINIC_PROCEDURE', [procID, fieldName, value])
            vm.selectedProcedure = {}
            return vm.notifySuccess(vm.$t('misc.changed_field_to', {
                field: fieldName,
                to: labelValue
            }))
        }
        vm.notifyError(vm.$t('notifs.failed_procedure_update'))
    },

    async updateClinicProcedure ({commit}, [procID, fieldName, value, labelValue, vm, callback, genericLoading]) {
        const data = {
            select: {
                id: procID
            },
            payload: {
                [fieldName]: value
            }
        }
        const payload = {
            data,
            vm
        }
        if (!genericLoading) {
            payload.loadingIn = '.is-selected .whitespace-no-wrap'
        }
        const resp = await vm.requestResource('patch_clinicprocedure', payload)
        
        if (resp.status === 200) {
            commit('UPDATE_CLINIC_PROCEDURE', [procID, fieldName, value])
            vm.selectedProcedure = {}
            if (callback) {
                return callback()
            }
            return vm.notifySuccess(vm.$t('misc.changed_field_to', {
                field: fieldName,
                to: labelValue
            }))
        }
        vm.notifyError(vm.$t('notifs.failed_procedure_update'))
    }

}