<template>
<laima-form
        ref="lf"
        :title="title || defaultTitle"
        @changed="changedField"
        :readonly="readonly || false"
        :size="size"
        :modelsContainer="initialData || models"
        :formFields="slotFields"
        :onSubmit="true">
    <template #footer>
        <div></div>
    </template>
</laima-form>
</template>

<script>
export default {
    name: 'add-edit-patient',

    props: {
        size: {},
        initialData: {
            default: null
        },
        readonly: {
            default: false
        },
        title: {}
    },

    computed: {
        slotFields () { 
            return [
                {
                    name: 'patient_id',
                    placeholder: this.loadingPatient && ' ' || '01234567890',
                    label: this.$i18n.t('fields.patient_id'),
                    type: 'text',
                    validators: {numeric: true, required: true, min: 11, max:11}
                },
                {
                    name: 'name',
                    placeholder: this.loadingPatient && ' ' || this.$i18n.t('placeholders.name'),
                    label: this.$i18n.t('fields.name'),
                    type: 'text',
                    validators: {required: true},
                    loading: this.loadingPatient
                },
                {
                    name: 'last_name',
                    placeholder: this.loadingPatient && ' ' || this.$i18n.t('placeholders.lastname'),
                    label: this.$i18n.t('fields.lastname'),
                    type: 'text',
                    validators: {required: true},
                    loading: this.loadingPatient
                },
                {
                    name: 'address1',
                    label: this.$i18n.t('fields.address'),
                    type: 'text',
                    placeholder: this.loadingPatient && ' ' || this.$i18n.t('placeholders.address'),
                    loading: this.loadingPatient,
                    validators: { required: true }
                },
                {
                    name: 'city',
                    label: this.$i18n.t('fields.city'),
                    type: 'text',
                    placeholder: this.loadingPatient && ' ' || this.$i18n.t('placeholders.city'),
                    loading: this.loadingPatient,
                    validators: { required: true }
                },
                {   
                    name: 'phone',
                    type: 'tel',
                    label: this.$i18n.t('fields.phone'),
                    placeholder: this.loadingPatient && ' ' || this.$i18n.t('placeholders.phone'),
                    loading: this.loadingPatient,
                    validators: { required: true }
                },
                {
                    name: 'email',
                    type: 'email',
                    label: this.$i18n.t('fields.email'),
                    loading: this.loadingPatient,
                    placeholder: this.loadingPatient && ' ' || this.$i18n.t('placeholders.email'),
                },
                {
                    name: 'has_primary_teeth',
                    type: 'switch',
                    loading: this.loadingPatient,
                    label: this.$i18n.t('fields.primary_teeth'),
                },
                {
                    name: 'teething',
                    type: 'switch',
                    label: this.$i18n.t('fields.teething'),
                    loading: this.loadingPatient
                },
                {
                    name: 'pregnant',
                    type: 'switch',
                    label: this.$i18n.t('fields.pregnant'),
                    loading: this.loadingPatient
                },
                {
                    name: 'lactating',
                    type: 'switch',
                    label: this.$i18n.t('fields.lactating'),
                    loading: this.loadingPatient
                },
            ]
        },
        booleanFields () {
            return this.slotFields.filter(i => i.type == 'switch').map(i => i.name)
        }
    },

    data () {
        const patient = this.$i18n.t('headers.patient')
        return {
            defaultTitle: patient,
            loadingPatient: false,
            models: {
                name: '',
                last_name: '',
                address1: '',
                city: '',
                phone: '',
                email: '',
                has_primary_teeth: false,
                teething: false,
                lactating: false,
                pregnant: false,
                // chronic_diseases: []
            }
        }
    },

    mounted () {
        if (this.initialData) {
            const initialData = this.initialData
            for (let key of Object.keys(this.models)) {
                let initVal = initialData[key]
                if (initVal !== undefined) {
                    this.$set(this.models, key, initVal)
                }
            }
        }
    },

    methods: {
        async changedField ([field, val]) {
            this.$set(this.models, field, val)
            if (this.initialData) this.$emit('updatedPatient', [field, val])
            if (field === 'patient_id') {
                this.loadingPatient = true
                const resp = await this.requestResource('get_patientresource', {no_loading: true, data: {patient_id: val}})
                this.loadingPatient = false
                if (resp.status === 200) {
                    // unify the results
                    const respData = resp.data || {}
                    Object.assign(respData, respData.actor)
                    delete respData.actor
                    respData.patient_id = val
                    Object.keys(this.models).forEach(key => {
                        let val = respData[key]
                        if (val != undefined) {
                            if (!val && respData.actor) val = respData.actor[key]
                            this.$set(this.models, key, val)
                        }
                    })
                }
            }
        }
    },
}
</script>

<style lang="scss">
    .duration-input {
        padding: 0;
    }
</style>