<template>
    <vx-card>
        <div slot="no-body" class="tabs-container">
            <vs-tabs :key="keyIndex" alignment="left" position="top" v-model="activeTab" class="open-overflow" ref="tabs">
                <vs-tab :label="$t('headers.clinic_details')" icon-pack="feather" icon="icon-info" class="open-overflow">
                    <div class="tab-text open-overflow">
                        <edit-clinic />
                    </div>
                </vs-tab>

                <vs-tab :label="$t('headers.pref')" icon-pack="feather" icon="icon-settings">
                    <div class="tab-text">
                        <clinic-preferences />
                    </div>
                </vs-tab>

                <vs-tab :label="$t('headers.procedures')" icon-pack="feather" icon="icon-heart">
                    <div class="tab-text">
                        <procedures />
                    </div>
                </vs-tab>

                <vs-tab :label="$t('headers.widget')" icon-pack="feather" icon="icon-box">
                    <div class="tab-text">
                        <widget-settings :clinic="clinic"/>
                    </div>
                </vs-tab>

            </vs-tabs>
        </div>
    </vx-card>
</template>

<script>
import EditClinic from './EditClinic.vue'
import Procedures from './Procedures.vue'
import ClinicPreferences from './ClinicPreferences.vue'
import WidgetSettings from './WidgetSettings.vue'


export default {

    created () {
        const clinicID = this.$store.state.auth.selectedWorkspace.id
        if (!this.$store.state.core.procedureTemplates) {
			this.$store.dispatch('core/fetchProcedureTemplates', {vm: this})
		}
		if (!this.$store.state.core.clinicProcedures[0]) {
			this.$store.dispatch('core/fetchClinicProcedures', {vm: this, clinicID})
		}
    },
    
    components: {
        EditClinic,
        Procedures,
        ClinicPreferences,
        WidgetSettings
    },
    
    computed: {
        clinic () {
            return this.$clone(this.$store.state.core.clinics.find(clinic => clinic.id == this.clinicID))
        }
    },

    data () {
        return {
            activeTab: 0,
            keyIndex: 0
        }
    },

    watch: {
        tabsPosition () {
            this.keyIndex++
        }
    }
}

</script>

<style lang="scss">
    .con-vs-tabs {
        border-top-left-radius: .5rem;
    }
</style>