<template>
    <div class="h-screen flex w-full bg-img">
        <div class="vx-col flex items-center justify-center flex-col sm:w-1/2 md:w-3/5 lg:w-3/4 xl:w-1/2 mx-auto text-center">
            <!-- <img src="@/assets/images/pages/not-authorized.png" alt="graphic-not-authorized" class="mx-auto mb-4"> -->
            <h2 class="sm:mx-0 mx-4 sm:mb-12 mb-8 text-5xl d-theme-heading-color">{{ $t('headers.account_registered') }} </h2>
            <h3 class="sm:mx-0 mx-4 sm:mb-12 mb-8 d-theme-text-inverse" style="color: #567156">{{ message }}</h3>
        </div>
    </div>
</template>


<script>
export default {
    computed: {
        message () {
            let email = this.$store.state.auth.pendingEmail
            const msg = this.$t('misc.activ_instr')
            return msg + email ? ` (${email})` : ''
        }
    }
}

</script>