<template>
<div>
    <form-wizard color="rgba(var(--vs-primary), 1)" ref="wizard" errorColor="rgba(var(--vs-danger), 1)" class="first-form-clinic" title="" :key="formIndex" subtitle=""
                :finishButtonText="$t('buttons.submit')">
        <tab-content :title="$t('headers.basics')" class="mb-5" icon="feather icon-home" :before-change="validateStep1">
            <form data-vv-scope="step-1">
                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin">
                        <vs-input icon-no-border icon="icon icon-briefcase"
                            icon-pack="feather"
                            :label="$t('fields.clinic_name')"
                            :data-vv-as="$t('fields.clinic_name').toLowerCase()"
                            v-model="workspace_name"
                            v-validate="'required'"
                            val-icon-danger="clear"
                            :danger="errors.has('workspace_name', 'step-1')"
                            :danger-text="errors.first('workspace_name', 'step-1')"
                            name='workspace_name'
                            class="w-full" />
                    </div>
                </div>
                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin">
                        <label for="" class="vs-input--label">{{ $t('headers.desc') }}</label>
                        <vs-textarea
                            name="description"
                            v-model="clinic.description"
                            class="w-full"
                            val-icon-danger="clear"
                            :data-vv-as="$t('headers.desc').toLowerCase()"
                            :danger="errors.has('description', 'step-1')"
                            style="background: white" :danger-text="errors.first('description', 'step-1')" />
                    </div>
                </div>
            </form>
        </tab-content>

        <tab-content :title="$t('headers.details')" class="mb-5 pb-8" icon="feather icon-mail" :before-change="validateStep2">
            <form data-vv-scope="step-2">
                <vs-divider class='mt-8 mb-0'><small>{{ $t('headers.contact') }}</small></vs-divider>
                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin">
                        <vs-input val-icon-danger="clear"
                            :danger="errors.has('phone', 'step-2')"
                            :danger-text="errors.first('phone', 'step-2')"
                            :data-vv-as="$t('fields.phone').toLowerCase()"
                            :label="$t('fields.phone')"
                            name="phone"
                            v-model="clinic.phone"
                            placeholder="+48123456789"
                            class="mt-5 w-full" />
                    </div>
                </div>

                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin">
                        <vs-input v-validate="'required|email'"
                            val-icon-danger="clear"
                            :danger="errors.has('email', 'step-2')"
                            :danger-text="errors.first('email', 'step-2')"
                            :label="$t('fields.email')"
                            :data-vv-as="$t('fields.email').toLowerCase()"
                            name="email"
                            v-model="clinic.email"
                            placeholder="username@gmail.com"
                            class="mt-5 w-full" />
                    </div>
                </div>

                <vs-divider class='mt-8 mb-0'><small>{{ $t('fields.address') }}</small></vs-divider>
                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full auto-margin mb-4">
                        <vs-input v-validate="'required'" val-icon-danger="clear"
                            :danger="errors.has('address1', 'step-2')"
                            :danger-text="errors.first('address1', 'step-2')"
                            :label="$t('fields.address')"
                            :data-vv-as="$t('fields.address').toLowerCase()"
                            :placeholder="$t('placeholders.address')"
                            name="address1"
                            v-model="clinic.address1"
                            class="mt-5 w-full" />
                    </div>
                </div>

                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin" :class="errors.first('country', 'step-2') && 'has-errors'">
                        <label class="text-sm">{{ $t('headers.country') }}</label>
                        <vs-select style="background: #fff" name="country" :options="countries" v-validate="'required'" v-model="clinic.country" data-vv-as="country">
                            <template v-slot:option="option">
                                <country-flag style="position: relative; top: 3px; right: 4px;" :country="option.value.toLowerCase()" size='small' />
                                {{ option.label }}
                            </template>
                        </vs-select>
                        <div v-show="errors.has('country', 'step-2')" class="con-text-validation span-text-validation-danger vs-input--text-validation-span v-enter-to" style="height: 19px;">
                            <span class="span-text-validation">
                                {{ errors.first('country', 'step-2') }}
                            </span>
                        </div>
                    </div>
                </div>

                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin">
                        <vs-input val-icon-danger="clear"
                            :danger="errors.has('zipcode', 'step-2')"
                            :danger-text="errors.first('zipcode', 'step-2')"
                            :placeholder="$t('placeholders.zipcode')"
                            :label="$t('fields.zipcode')"
                            name="zipcode"
                            v-model="clinic.zipcode" class="mt-5 w-full" />
                    </div>
                </div>

                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin">
                        <vs-input
                            :danger="errors.has('city', 'step-2')"
                            :danger-text="errors.first('city', 'step-2')"
                            :label="$t('fields.city')"
                            :placeholder="$t('placeholders.city')"
                            class="mt-5 w-full"
                            name="city"
                            v-validate="'required'"
                            val-icon-danger="clear"
                            v-model="clinic.city"
                            />
                    </div>
                </div>

                <vs-divider class='mt-8 mb-0'><small>{{ $t('headers.legal') }}</small></vs-divider>

                <div class="vx-row">
                    <div class="vx-col md:w-1/2 w-full mt-5 auto-margin">
                        <vs-input
                            :label="$t('fields.tax_id')"
                            placeholder="0123456789"
                            v-validate="'required|numeric|length:10'"
                            val-icon-danger="clear"
                            :danger="errors.has('tax_id', 'step-2')"
                            :danger-text="errors.first('tax_id', 'step-2')"
                            name="tax_id"
                            v-model="clinic.tax_id"
                            class="mt-5 w-full" />
                    </div>
                </div>
            </form>
        </tab-content>

    </form-wizard>
</div>
</template>

<script>
import auth from "@/auth/authService"
import {
    FormWizard,
    TabContent
} from 'vue-form-wizard'
import 'vue-form-wizard/dist/vue-form-wizard.min.css'
import CountryFlag from 'vue-country-flag'

export default {

    computed: {
        reg_token() {
            return this.$route.params.reg_token
        },
        isInvitedView() {
            return this.$route.name === 'activateInvitedAccount'
        }
    },

    components: {
        FormWizard,
        TabContent,
        CountryFlag
    },

    props: {
        overValidateStep1: null,
        overValidateStep2: null
    },

    data() {
        return {
            formIndex: 0,
            workspace_name: null,
            clinic: {
                country: {
                    label: 'Polska',
                    value: 'PL'
                }
            },
            clinicID: null
        }
    },

    methods: {

        clear() {
            this.formIndex++
            this.workspace_name = null
            this.$set(this.clinic, {})
            // this.clinic = Object.assign({}, {
            //     country: {label: 'Polska', value: 'PL'}
            // })
            this.clinicID = null
        },

        async validateStep1() {
            if (this.overValidateStep1) return await this.overValidateStep1()
            // create the workspace
            if (await this.$validator.validateAll('step-1')) {
                this.$set(this.clinic, 'laima_email', this.workspace_name)
                const activation_link = this.isInvitedView ? `/api/actor/invitee/activate/email/${this.reg_token}/` : `/api/actor/created/activate/email/${this.reg_token}/`
                const resp = await this.$http.post(
                    activation_link,
                    JSON.stringify({
                        workspace_name: this.workspace_name
                    }), {
                        headers: {
                            Range: 'post'
                        }
                    }
                )

                let err
                if (resp.status === 422) {
                    err = resp.data
                    const errors = this.errors
                    // if (err.name) err.workspace_name = err.name
                    Object.entries(err).forEach(item => {
                        let [keyname, errs] = item
                        errs.forEach(errmsg => {
                            errors.add({
                                scope: 'step-1',
                                field: keyname,
                                msg: errmsg
                            })
                        })
                    })
                    return false
                } else if (resp.status !== 200) {
                    err = resp.data
                }

                if (err) {
                    this.$router.push({
                        name: 'fatal',
                        query: {
                            title: 'Failed to activate account',
                            message: err
                        }
                    })
                    return false
                }

                return true

            }

            return false

        },

        async validateStep2() {
            if (this.overValidateStep2) return await this.overValidateStep2()
            if (await this.$validator.validateAll('step-2')) {
                // user is logged in now, create a clinic
                this.clinic['country'] = this.clinic.country.value
                const resp = await this.$http.post(
                    '/api/clinic/',
                    JSON.stringify(this.clinic), {
                        headers: {
                            Range: 'post'
                        }
                    }
                )
                let err
                if (resp.status === 422) {
                    err = resp.data
                    const errors = this.errors
                    if (err.name) err.workspace_name = err.name
                    Object.entries(err).forEach(item => {
                        let [keyname, errs] = item
                        errs.forEach(errmsg => {
                            errors.add({
                                scope: 'step-2',
                                field: keyname,
                                msg: errmsg
                            })
                        })
                    })
                    return false
                } else if (resp.status !== 200) {
                    err = resp.data
                }

                if (err) {
                    this.$router.push({
                        name: 'fatal',
                        query: {
                            title: 'Failed to configure account',
                            message: err
                        }
                    })
                    return false
                }
                this.clinicID = resp.data.id
                auth.logOut({
                    reason: 'configured',
                    to: '/'
                })
                return true

            }
            return false
        },

    }
}
</script>

<style lang="scss">
.first-form-clinic {

    input,
    button {
        font-family: inherit;
    }
}

.first-form-header {
    font-weight: 300;

    &.sub {
        color: #5d5d5d;
    }
}
</style>
