export default {
    partial_patch_clinic ({commit, rootState}, [vm, select, payload]) {
        console.log(select, payload);
        const clinicID = select.id
        const workspaces = rootState.auth.actorContext.workspaces
        payload.name = workspaces[clinicID]
        commit('core/PATCH_CLINIC', {clinicID, payload})
    },

    partial_post_clinic ({commit, rootState}, [vm, empty, payload]) {
        const clinicID = payload.id
        const workspaces = rootState.auth.actorContext.workspaces
        payload.name = workspaces[clinicID]
        commit('core/ADD_CLINIC', payload)
    }
}