<template>
    <div class="vx-col mt-20" style="max-width: 400px; margin: 0 auto">
        <div v-if="verified">
            <statistics-card-line
                hideChart
                class="mb-base text-center"
                icon="SmileIcon"
                :statistic="$t('headers.email_verified')"
                color='success' />
            <h5 class="text-center">{{ $t('headers.auto_redirect_warn') }}</h5>
        </div>
        <p class="text-center mt-16" style="font-size: 20px;" v-if="verrors">{{verrors}}</p>
    </div>
</template>

<script>
import StatisticsCardLine from '@/components/statistics-cards/StatisticsCardLine.vue'
export default {
    components: {
        StatisticsCardLine
    },
    data () {
        return {
            verified: false,
            verrors: ''
        }
    },
    async mounted () {
        const verif_code = this.$route.params.verif_code
        const url = `/api/actor/verify/email/${verif_code}/`
        this.$store.commit('STATE_LOADING_ON', {})
        const resp = await this.$http.post(url, {}, {headers: {'Range': 'get'}})
        this.$store.commit('STATE_LOADING_OFF', {})

        if (resp.status === 200) {
            this.verified = true
            // commit the change
            this.$store.commit('auth/UPDATE_ACTOR_CONTEXT', {email_confirmed: true})
            await this.sleep(3)
            return this.$router.push('/')
        }

        // console.log(resp)
        this.verrors = Object.entries(resp.data).map(item => {
            let [k, v] = item
            return v.join(', ')
        }).join('\n')

    }
}

</script>