<template>
<div id="sms" class="vx-row w-full  ml-auto mr-auto pr-4" :key="componentKey">
  	<div class="mr-auto mt-5 w-1/3">
		<vx-card class=' w-full ' >
			<div class="relative w-full h-full" id="send-text">
				<div class="send-text-card w-full h-full">
					<div class="vx-row w-full mb-2">
						<div class="vx-col w-full ml-auto mr-auto">
							<h4>{{ $t('headers.send_sms') }}</h4>
						</div>
					</div>
					<div class="vx-row mb-2 mt-12">
						<div class="vx-col w-full ml-auto mr-auto">
							<vs-select
								class="w-full"
								:placeholder="$t('placeholders.select_rec')"
								multiple
								autocomplete
								name="Recipients"
								:data-vv-as="$t('fields.recipients').toLowerCase()"
								:danger="$validator.errors.has('Recipients')"
								v-validate="'required'"
								v-model="selectedPatients">
								<vs-select-item :key="index" :value="item.value" :text="item.text" v-for="(item, index) in patients" />
							</vs-select>
							<span class="text-danger text-sm">{{ errors.first('Recipients') }}</span>
						</div>
					</div>
					<div class="vx-row mt-6 mb-6">
						<div class="vx-col w-full  ml-auto mr-auto">
							<vs-textarea
								counter="200"
								type="content"
								height="10rem"
								class="w-full text-msg"
								:danger="$validator.errors.has('Message')"
								icon-pack="feather"
								icon="icon-mail"
								icon-no-border
								name="Message"
								v-validate="'required|max:200'"
								:data-vv-as="$t('fields.message').toLowerCase()"
								:placeholder="$t('placeholders.message')"
								v-model="msg" />
								<span class="text-danger text-sm">{{ errors.first('Message') }}</span>
						</div>
					</div>
					<div class="vx-row w-full ml-auto mr-0 mt-16">
						<vs-button class="mr-3 w-1/4" icon-pack="feather" icon="icon-send" icon-no-border @click="sendText">{{ $t('buttons.send') }}</vs-button>
						<vs-button class="w-1/4" color="danger" icon-pack="feather" icon="icon-trash" icon-no-border type="border" @click="resetForm">{{ $t('buttons.reset') }}</vs-button>
					</div>
				</div>
			</div>
		</vx-card>
	  </div>
	<div class="mt-5 w-2/3 sms-reports">
		<vx-card class="ml-6 w-full" >
			<div class="vx-col w-full ml-auto mr-auto mb-8">
				<div class="flex justify-between">
					<h4>{{ $t('headers.text_reports') }}</h4>
					<feather-icon v-tooltip="$t('buttons.refresh')" class='text-black pr-1 cursor-pointer hover:text-primary' @click="refresh" svgClasses="w-5 h-5" icon="RefreshCwIcon"></feather-icon>
				</div>
			</div>
			<div class="vx-col sms-col" id="sms-list">
				<vs-table max-items="10" :data="smsStatuses" hoverFlat notSpacer stripe class='reports-table'>
					<template slot="thead">
						<vs-th>{{ $t('headers.date') }}</vs-th>
						<vs-th>{{ $t('fields.phone') }}</vs-th>
						<vs-th>{{ $t('fields.status') }}</vs-th>
						<vs-th>{{ $t('headers.subject') }}</vs-th>
					</template>
					<template slot-scope="{data}">
						<vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data">
							<vs-td>
								{{ tr.date | formatDate }}
							</vs-td>
							<vs-td>
								{{ tr.number }}
							</vs-td>

							<!-- <vs-td>
								{{ tr.name }}
							</vs-td> -->

							<vs-td>
								<status-ball v-tooltip="{
									content: statusDetails(tr),
									classes: 'text-sm tracking-normal'
								}"
								:status="TEXT_STATUS_MAP[tr.status]" />
							</vs-td>

							<vs-td>
								{{ tr.subject }}
							</vs-td>
						</vs-tr>
					</template>

				</vs-table>
				<div class="flex justify-center mt-12">
					<button class="vs-pagination--buttons hover:bg-primary mx-2 w-12 h-12" v-tooltip="$t('misc.older')" @click="prevTimeframe">
						<i class="vs-icon notranslate icon-scale material-icons" style="font-size: 1.6rem">chevron_left</i>
					</button>
						
					<button class="vs-pagination--buttons hover:bg-primary mx-2 w-12 h-12" v-tooltip="$t('misc.newer')" :disabled="page == 1" @click="nextTimeframe">
						<i class="vs-icon notranslate icon-scale material-icons" style="font-size: 1.6rem">chevron_right</i></button>
				</div>
			</div>
		</vx-card>
	</div>
</div>
</template>

<script>

const TEXT_STATUS_MAP = {
	accepted: 'primary',
	queued: 'primary',
	sending: 'success',
	sent: 'success',
	failed: 'danger',
	delivered: 'primary',
	undelivered: 'danger',
	receiving: 'success',
	received: 'success'
}

export default {
  	data () {
		return {
			msg: '',
			page: 1,
			selectedPatients: [],
			componentKey: 0,
			TEXT_STATUS_MAP
		}
	},
	filters: {
		formatDate (date) {
			const month = date.toLocaleString('default', { month: 'short' })
			return date.format(`DD ${month} 'YY HH:MM`)
		}
	},
	async mounted () {
		const store = this.$store
		if (!this.patients[0]) {
			store.dispatch('core/listAllPatients', {vm: this})
		}
		this.$store.dispatch('core/listTextStatuses', {vm: this, page: this.page})
	},

	computed: {
		smsStatuses () {
			return this.$store.state.core.textStatuses
		},
		patients () {
			return this.$store.state.core.patients.map(i => {
				return {
					text: i.name + ' ' + i.last_name,
					value: i.actor_id
				}
			}).sort((a, b) => {
				let aName = a.text.toLowerCase()
				let bName = b.text.toLowerCase()
				if (aName < bName) {
					return -1
				}
				if (aName > bName) {
					return 1
				}
				return 0
			})
		}
	},

	methods: {
		async sendText () {
			const valid = await this.$validator.validateAll()
			if (!valid) return
			const resp = await this.requestResource('post_sendText', {
				data: {
					recipients: this.selectedPatients,
					msg: this.msg
				},
				vm: this,
				loadingIn: '.send-text-card'
			})
			if (resp.status === 200) {
				const data = +(resp.data.scheduled_count || '0')
				if (data) {
					this.notifySuccess(this.$t('notifs.scheduled_text_submission', {count: data}))
				}
				else {
					this.notifyError(this.$t('notifs.zero_texted'))
				}
				this.resetForm()
			}
			else {
				this.notifyError(this.$t('notifs.text_failed'))
			}
		},
		resetForm () {
			this.selectedPatients = []
			this.msg = ''
			this.componentKey++
		},
		refresh () {
			this.$store.dispatch('core/listTextStatuses', {vm: this, page: this.page})
		},
		statusDetails (tr) {
			if (tr.status === 'failed') {
				return tr.error
			}
			return tr.status
		},
		prevTimeframe () {
			this.page++
			this.$store.dispatch('core/listTextStatuses', {vm: this, page: this.page})
		},
		nextTimeframe () {
			this.page--
			this.$store.dispatch('core/listTextStatuses', {vm: this, page: this.page})
		}
	}
	
}
</script>

<style lang="scss">
#sms {
	.text-msg {
		textarea {
			height: 10rem;
		}
	}
	.vs-con-tbody {
		border: none;
	}
}
</style>