import Vue from 'vue'
const ALLOWED_LEVELS = ["info", "warning", "success", "ok", "error", "critical", "warn"]

const mutations = {
	REGISTER_ALERT (state, payload) {
		const errs = []
		let name = payload.name

		if (!name) {
			errs.push('`name` key is required')
		}
		delete payload.name
		const content = payload.content
		if (!content) {
			errs.push('`content` key is required')
		}

		const level = payload.level
		if (!level) {
			errs.push('`level` key is required')
		}

		if (level && !ALLOWED_LEVELS.includes(level) ) {
			errs.push(`'${level}' is not a recognized level name`)
		}
		
		if (errs[0]) {
			console.warn(`! Validation error registering global alert: ${errs.join(', ')}`)
			return
		}

		state.globalAlerts[name] = {
			level,
			content
		}
	},

	UNSUPPORTED_BROWSER (state) {
		state.browserOK = false
	},

	DEREGISTER_ALERT (state, name) {
		Vue.delete(state.globalAlerts, name)
	},

	CLEAR_SYNCING_STACK (state) {
		state.nowSyncing = []
	},

	ADD_TO_SYNCING_STACK (state, item) {
		state.nowSyncing.push(item)
	},

	TOGGLE_SYNC (state, flag) {
		state.inSync = flag
	},

	UNLOCK_APP (state) {
		state.appLocked = false
	},

	LOCK_APP (state) {
		state.appLocked = true
	},

	STATE_LOADING_ON (state) {
		state.stateLoading = true
	},

	STATE_LOADING_OFF (state) {
		state.stateLoading = false
	},

	// Vertical NavMenu
	TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE(state, value) {
		state.isVerticalNavMenuActive = true
	},
	TOGGLE_REDUCE_BUTTON(state, val) {
		state.reduceButton = val
	},
	UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN(state, val) {
		state.verticalNavMenuItemsMin = val
	},
	UPDATE_VERTICAL_NAV_MENU_WIDTH(state, width) {
		state.verticalNavMenuWidth = width
	},
	// ////////////////////////////////////////////
	// UI
	// ////////////////////////////////////////////

	TOGGLE_CONTENT_OVERLAY(state, val) { state.bodyOverlay       = val },
	UPDATE_THEME(state, val)           { state.theme             = val },
	UPDATE_WINDOW_WIDTH(state, width)  { state.windowWidth       = width },
	UPDATE_WINDOW_SCROLL_Y(state, val) { state.scrollY = val },

	SET_GLOBAL_MESSAGE (state, payload) {
		// payload = [msg, level] where level => ['primary', 'danger', 'warning', 'success']
		state.globalMessage = payload
	},

	SET_LOCK_SCREEN_IDLE_TIME (state, val) {
		if (!val) return
		val = +val
		const key = 'app.laima.screenLock.ttl'
		window.localStorage.setItem(key, val)
		Vue.set(state.screenLock, 'defaultIdleTime', val)
	},

	LOCK_SCREEN(state) {
		window.localStorage.setItem('app.laima.screenLock.on', true)
		Vue.set(state.screenLock, 'isLocked', true)
	}, 
	
	UNLOCK_SCREEN(state) {
		window.localStorage.setItem('app.laima.screenLock.on', false)
		Vue.set(state.screenLock, 'isLocked', false)
	},

	ENABLE_SCREEN_LOCK(state) {
		Vue.set(state.screenLock, 'isSet', true)
		window.localStorage.setItem('app.laima.screenLock.isSet', true)
	}, 
	
	DISABLE_SCREEN_LOCK(state) {
		Vue.set(state.screenLock, 'isSet', false)
		window.localStorage.setItem('app.laima.screenLock.isSet', false)
	}
}

export default mutations

