<template>
	<div id="procedures-list-view" class="data-list">
		<add-new-procedure ref="addproc" :addNewProcedure.sync="addProcSidebar" />
		<vs-table ref="table" v-model="selectedProcedure"
			search
			:data="procedures"
			:max-items="itemsPerPage"
			@selected="onSelectedProcedure">

			<div slot="header" class="ml-0">
				<div class="flex flex-wrap-reverse items-center data-list-btn-container">
					<vs-button color="success" class="mb-4 cursor-pointer flex items-center justify-center text-lg font-medium" @click="addProcSidebar=true">
						<feather-icon icon="PlusCircleIcon" svgClasses="w-5 h-5 relative" style="top: 3px; right: 5px" />
						<span class="ml-2 text-base">{{ $t('buttons.add_proc') }}</span>
					</vs-button>
				</div>
			</div>

			<template slot="thead">
				<vs-th sort-key="procedure_name">{{ $t('fields.proc_name') }}</vs-th>
				<vs-th sort-key="category">{{ $t('headers.category') }}</vs-th>
				<vs-th sort-key="price">{{ $t('headers.price') }} ({{ $store.state.auth.currency }})</vs-th>
				<vs-th sort-key="maxprice">{{ $t('fields.maxprice') }} ({{ $store.state.auth.currency }})</vs-th>
				<vs-th sort-key="duration">{{ $t('fields.proc_duration') }} (min)</vs-th>
				<vs-th>Action</vs-th>
			</template>

			<template slot-scope="{data}">
				<tbody class="table-body">
					<vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" v-show="!tr.hidden">
						<vs-td>
							<p v-if="selectedProcedure.id !== tr.id || tr.legacy" class="font-medium truncate">{{ tr.name }}</p>
							<lm-input
								v-else-if="selectedProcedure.id === tr.id"
								:danger="$validator.errors.has('name')"
								:data-vv-as="$t('fields.proc_name').toLowerCase()"
								v-model="tr.name"
								autocomplete="off"
								class="text-black"
								name="name"
								type="text"
								v-validate="'required'"
								@blur="updateField(tr, 'name')"
								@keypress.enter="updateField(tr, 'name')"
							/>
						</vs-td>
						<vs-td>
							<p class="font-normal truncate">{{ tr.category }}</p>
						</vs-td>
						<vs-td>
							<p class="font-normal truncate" v-if="selectedProcedure.id !== tr.id">
								{{ tr.minprice }}
							</p>
							<template v-else-if="selectedProcedure.id === tr.id">
								<div class="relative">
									<div class="absolute validation-msg">
										<v-popover
											v-show="$validator.errors.has('minprice')"
											popoverBaseClass="tooltip"
											popoverArrowClass="border-danger tooltip-arrow popover-arrow"
											popoverInnerClass="bg-danger rounded-full text-white tracking-normal text-sm p-1 px-2"
											ref="tooltip"
											trigger="hover">
												<feather-icon 
													class="text-danger animated pulse delay-1s infinite"
													icon="AlertCircleIcon" />
												<template slot="popover">
													{{ $validator.errors.first('minprice') }}
												</template>
										</v-popover>
									</div>
								</div>
								<vs-input
									:danger="$validator.errors.has('minprice')"
									:data-vv-as="$t('headers.min_price').toLowerCase()"
									autocomplete="off"
									class="text-black"
									min="5"
									name="minprice"
									step="5"
									style="max-width: 90px"
									type="number"
									@blur="updateField(tr, 'minprice')"
									@keypress.enter="updateField(tr, 'minprice')"
									v-model="tr.minprice"
									v-validate="'required|numeric|min_value:5|max_value:10000'"
								/>
							</template>
						</vs-td>
						<vs-td>
							<template v-if="selectedProcedure.id !== tr.id">
								<p class="font-normal truncate" v-if="!!parseInt(tr.maxprice)">
									{{ tr.maxprice }}
								</p>
								<p v-else>
									-
								</p>
							</template>
							<template v-else>
								<div class="relative">
									<div class="absolute validation-msg">
										<feather-icon v-tooltip="$t('misc.maxprice_note')" v-if="!$validator.errors.has('maxprice')" icon="InfoIcon" svgClasses="text-primary" />
										<v-popover
											v-else
											popoverBaseClass="tooltip"
											popoverArrowClass="border-danger tooltip-arrow popover-arrow"
											popoverInnerClass="bg-danger rounded-full text-white tracking-normal text-sm p-1 px-2"
											ref="tooltip"
											trigger="hover">
												<feather-icon 
													class="text-danger animated pulse delay-1s infinite"
													icon="AlertCircleIcon" />
												<template slot="popover">
													{{ $validator.errors.first('maxprice') }}
												</template>
										</v-popover>
									</div>
								</div>
								<vs-input
									:danger="$validator.errors.has('maxprice')"
									:data-vv-as="$t('headers.max_price').toLowerCase()"
									autocomplete="off"
									class="text-black"
									min="5"
									name="maxprice"
									step="5"
									style="max-width: 90px"
									type="number"
									v-model="tr.maxprice"
									v-validate="'required|numeric|min_value:0|max_value:10000'"
									@blur="updateField(tr, 'maxprice')"
									@keypress.enter="updateField(tr, 'maxprice')"
								/>
							</template>
						</vs-td>
						<vs-td>
							<p class="font-normal truncate" v-if="selectedProcedure.id !== tr.id">{{ tr.duration }}</p>
							<vs-input
								v-else
								:danger="$validator.errors.has('duration')"
								:data-vv-as="$t('fields.duration').toLowerCase()"
								:min="default_visit_interval"
								:step="default_visit_interval"
								autocomplete="off"
								class="text-black"
								name="duration"
								style="max-width: 90px"
								type="number"
								v-debounce:300ms="partial(updateField, tr, 'duration')"
								v-model="tr.duration"
								v-validate="'required|numeric|min_value:0|max_value:10000'"
							/>
						</vs-td>
						<vs-td class="whitespace-no-wrap">
							<feather-icon
								@click.stop="removeProcedurePrompt(tr, $event)"
								class="mr-4"
								icon="Trash2Icon"
								svgClasses="w-6 h-6 hover:text-danger stroke-current"
								v-tooltip="$t('headers.remove_procedure')"
							/>
						</vs-td>
					</vs-tr>
				</tbody>
			</template>
		</vs-table>
	</div>
</template>

<script>
// import { isThisMinute } from 'date-fns'
import AddNewProcedure from './AddNewProcedure.vue'
export default {

	components: {
		AddNewProcedure
	},

	data () {
		return {
			itemsPerPage: 6,
			addProcSidebar: false,
			selectedProcedure: {},
		}
	},	

	computed: {
		default_visit_interval () {
			const activeClinic = this.$store.getters['core/activeClinic']()
			return activeClinic && activeClinic.default_visit_interval
		},
		procedures () {
			return this.$clone(this.$store.state.core.clinicProcedures)
		},
	},

	methods: {

		onSelectedProcedure (ev) {
		},

		addNewProcedure () {
			
		},

		removeProcedurePrompt (tr) {
			this.$vs.dialog({
				text: this.$t('misc.delete_procedure_prompt'),
				acceptText: this.$t('buttons.accept'),
				cancelText: this.$t('buttons.cancel'),
                type: 'confirm',
				color: 'danger',
                title: this.$t('headers.remove_procedure'),
                accept: this.partial(this.removeProcedure, tr)
            })
		},

		removeProcedure (tr) {
			this.$store.dispatch('core/removeProcedure', [this, tr])
		},

		async updateField (tr, fieldName) {
			const isValid = await this.$validator.validateAll()
			if (isValid) {
				if (tr.maxprice != 0 && parseInt(tr.minprice) > parseInt(tr.maxprice)) {
					return this.$validator.errors.add({
						field: 'maxprice',
						msg: this.$t(
							'misc.must_be_gte', {
								field: this.$t('headers.max_price').toLowerCase(),
								otherField: this.$t('headers.min_price').toLowerCase()
							}
						)
					})
				}
			}
			const value = tr[fieldName]
			console.log(value);
			let labelValue = value
			if (fieldName === 'minprice' || fieldName === 'maxprice') {
				labelValue = value + ' ' + this.$store.state.auth.currency
			}
			else if (fieldName === 'duration') {
				labelValue = value + ' min'
			}
			if (fieldName === 'name' && !tr.legacy) {
				this.$store.dispatch('core/updateProcedureTemplate', [tr.code, tr.id, fieldName, value, labelValue, this])
			}
			else {
				this.$store.dispatch('core/updateClinicProcedure', [tr.id, fieldName, value, labelValue, this])
			}

		}
	},

	mounted () {
		window.proc = this
	}
}
</script>

<style lang="scss">
@import "@/assets/scss/vuexy/_variables.scss";
#procedures-list-view {
	.header-table {
		margin-left: 1.22rem;
		margin-right: 1.22rem;
	}
	.con-text-validation, .vs-input--icon-validate {
		display: none;
	}
	.validation-msg {
		left: -26px;
		top: 10px;
	}

}

</style>