<template>
  <div id="patient-list-view" class="data-list">
	<vs-sidebar vs-custom-content position-right parent="body" default-index="-1" color="primary" class="add-edit-patient" :value="patientSidebarActive">
		<div class="header-sidebar" slot="header">
			<div class="flex justify-between w-full h-full bg-primary text-white">
				<h5 class="pl-4 flex self-center text-white font-light">{{ sideBarTitle }} </h5>
				<feather-icon class="pr-4 text-white cursor-pointer" svgClasses="w-6 h-6" @click="patientSidebarActive = false" icon="XIcon"/>
			</div>
		</div>
		<add-edit-patient @updatedPatient="updatedPatient" :initialData="selectedPatient" title="Patient's details" ref="patient" size="small" />
		<vs-button v-if="!selectedPatient" @click="submitPatient" class="w-full rounded-none" size="large" type="filled" color="primary">{{ $t('buttons.save') }}</vs-button>
	</vs-sidebar>

	<vs-table ref="table" v-model="selectedPatient" pagination :max-items="itemsPerPage" search :data="patients">
		<div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
			<div class="flex flex-wrap-reverse items-center data-list-btn-container">
				<vs-button color="success"  class="mb-4 cursor-pointer flex items-center justify-center text-lg font-medium" @click="addNewPatient">
					<feather-icon icon="UserPlusIcon" svgClasses="w-5 h-5 relative" style="top: 3px; right: 5px" />
					<span class="ml-2 text-base">{{ $t('headers.add_new') }}</span>
				</vs-button>
			</div>
		</div>

		<template slot="thead">
			<vs-th sort-key="name">{{ $t('fields.name') }}</vs-th>
			<vs-th sort-key="email">{{ $t('fields.email') }}</vs-th>
			<vs-th sort-key="phone">{{ $t('fields.phone') }}</vs-th>
			<vs-th sort-key="active">{{ $t('fields.status') }}</vs-th>
			<vs-th>{{ $t('headers.actions') }}</vs-th>
		</template>

		<template slot-scope="{data}">
			<tbody>
				<vs-tr :data="tr" :key="indextr" v-for="(tr, indextr) in data" @click.prevent="onSelectedPatient(tr, $event)">
					<vs-td>
						<p class="product-name font-medium truncate">{{ tr | fullname }}</p>
					</vs-td>
					<vs-td>
						<p class="product-price">{{ tr.email }}</p>
					</vs-td>
					<vs-td>
						<p class="product-price">{{ tr.phone }}</p>
					</vs-td>
					<vs-td>
						<status-ball :status="tr.status == 1 && 'success' || 'danger'" />
					</vs-td>
					<vs-td class="whitespace-no-wrap">
						<feather-icon v-tooltip="$t('headers.add_visit')" icon="PlusSquareIcon" class="mr-4" svgClasses="w-6 h-6 hover:text-success stroke-current" @click.stop="addNewVisit(tr, $event)" />
						<v-popover :ref="`patient-slot-selection-${tr.id}`" popoverArrowClass="no-arrow-picker" class="w-0 float-left flex" @hide="contentOverlay.style.zIndex=-1" @show="contentOverlay.style.zIndex=5000">
							<template slot="popover">
								<monthly-slots-heatmap
									@timeFrameChanged="updateMonthlyPicker"
									@dateSelected="datepickerDateChanged($event, tr)"
									:date="(new Date()).format('YYYY-MM')" 
									set="all" />
							</template>
						</v-popover>
						<feather-icon v-tooltip="$t('headers.view_visits')" icon="CalendarIcon" class="mr-4" svgClasses="w-6 h-6 hover:text-primary stroke-current" @click.stop="viewVisits(tr)" />
					</vs-td>
				</vs-tr>
			</tbody>
		</template>
	</vs-table>
  </div>
</template>

<script>
import MonthlySlotsHeatmap from '@/views/pages/scheduler/MonthlySlotsHeatmap.vue'

export default {
	
	data () {
		return {
			itemsPerPage: 6,
			patientSidebarActive: false,
			selectedPatient: null,
		}
	},

	filters: {
		fullname (tr) {
			return tr.name + ' ' + tr.last_name
		}
	},

	components: {
		MonthlySlotsHeatmap
	},

	computed: {
		sideBarTitle () {
			const patient = this.selectedPatient
			return patient ?  this.$i18n.t('headers.editing') + ' '  + patient.name + ' ' + patient.last_name : this.$i18n.t('headers.add_patient')
		},
		patients () {
			return this.$store.state.core.patients
		},
		clinic () {
			return this.$store.getters['core/activeClinic']()
		}
	},

	methods: {

		addNewVisit (tr) {
			const dropdown = this.$refs[`patient-slot-selection-${tr.id}`][0]
			this.$store.dispatch('core/fetchMonthlyHeatmap', [new Date(), this])
			this.contentOverlay.style.zIndex = 5000
			dropdown.show()
		},

		addNewPatient () {
			this.selectedPatient = null
			this.patientSidebarActive = true
		},

		clearForm () {
            try {
                Object.keys(this.$refs.patient.models).forEach(key => {
                    this.$set(this.$refs.patient.models, key, '')
                })
            }
            catch (e) {}
		},

		datepickerDateChanged (date, tr) {
			this.$store.commit('core/SET_INTERIM_PATIENT', tr)
			this.$router.push({name: 'Scheduler', query: {
				view: 'day',
				date: date.getTime()
			}})
		},
		
		updateMonthlyPicker (ev) {
			const date = ev.endDate
			this.$store.dispatch('core/fetchMonthlyHeatmap', [date, this])
		},
		
		updatedPatient ([field, val]) {
			const select = {
				id: this.selectedPatient.id
			}
			const payload = {
				[field]: val,
				patient_id: this.selectedPatient.patient_id
			}
			this.$store.dispatch('core/editPatient', [select, payload, this])
		},

		onSelectedPatient () {
			this.patientSidebarActive = true
		},

		async submitPatient () {
			const patientValid = await this.$refs.patient.$refs.lf.$validator.validateAll('step-1')
			if (patientValid) {
				const patientPayload = Object.assign({}, this.$refs.patient.models)
				const patient_id = patientPayload.patient_id.toString()

				// is patient_id already present locally?
				if (this.$store.state.core.patients.find(i => i.patient_id == patient_id)) {
					this.patientSidebarActive = false
					this.notifyError(this.$t('notifs.patient_registered'))
				}

                let [yearborn, monthBorn] =  [patient_id.slice(0, 2), patient_id.slice(2, 4)]
                if (parseInt(monthBorn) > 12) {
                    yearborn = '20' + yearborn
                }
                else {
                    yearborn = '19' + yearborn
                }
                patientPayload.year_born = yearborn
                patientPayload.country = 'PL'
                const resp = await this.requestResource('post_patientClinicRegistration', {
                    data: patientPayload,
                    scope: 'step-1',
                    vm: this.$refs.patient.$refs.lf,
                    errRespMap: {
                        year_born: 'patient_id'
                    }
				})
				if (resp.status === 200) {
					const fullname = patientPayload.name + ' ' + patientPayload.last_name
					const localPatient = Object.assign({}, this.$refs.patient.models)
					localPatient.status = 1
					this.$store.commit('core/ADD_PATIENT', localPatient)
					this.notifySuccess(this.$t('notifs.patient_added', {fullname}))
					this.patientSidebarActive = false
				}
			}
		},

		viewVisits (tr) {
			const date = new Date()
			const fullname = tr.name + ' ' + tr.last_name
			const msg = this.$t('misc.filtered_patient', {fullname})
            this.$store.commit('SET_GLOBAL_MESSAGE', [msg , 'primary'])
            this.$store.dispatch('core/listSlotsByPatient', {vm: this, patient_id: tr.id, callback: () => {
				this.$store.commit('core/SET_FILTERED_PATIENT', tr)
			}})
			this.$router.push({name: 'Scheduler', query: {
				view: 'month',
				date: date.getTime()
			}})
		},
	},

	mounted () {
		this.isMounted = true
		window.pats = this
		this.contentOverlay = document.querySelector('#content-overlay')
		this.$store.dispatch('core/listAllPatients', {vm: this})
	},

	watch: {
		selectedPatient (val) {
			if (val) {
				this.patientSidebarActive = true
			}
		},
		patientSidebarActive (val) {
			if (val) {
                window.addEventListener('keydown', e => {
                    if (e.key == 'Escape') {
                        this.patientSidebarActive = false
                    }
                }, {once: true})
			}
			else {
				this.selectedPatient = null
			}
		}
	}
	
}
</script>

<style lang="scss">
.patient-slot-selection {
	.vs-dropdown--menu--after {
		display: none !important;
	}
}
.add-edit-patient {
    .vs-sidebar {
        .vs-sidebar--header {
            padding: 0;
            z-index: 1;
            border: none;
        }
        .header-sidebar {
            height: 42px;
            box-shadow: 3px 2px 15px #00000038;
        }
        .vs-sidebar--items {
            padding: 0;
        }
        min-width: 470px;
    }
    .laima-form {
        box-shadow: none;
        .fields-box {
            &:last-child {
                border-bottom: 1px solid #dadada;
            }
        }
        .vx-card__header {
            border-radius: 0 !important;
        }
    }
    .vx-card {
        border-radius: 0 !important;
    }
}

.vue-popover-theme {
	max-width: 350px;
}

</style>
