import { aggregation } from './utils'
import UpdateManager from './update_manager.js'
const FINGERPRINT_KEY = 'app.laima:fngprt'
const PUBLIC_KEY = "BEkBzZFbgJRXHm2kq9KKHUkq0uH_QNf2kkBc8foV9UZKMpzQOARCKgtJJcZ11_iUbGmrJUAdJRPppWHGW9L-fVk"
const swToClientChannel = new BroadcastChannel('swToClient')
const clientToSwChannel = new BroadcastChannel('clientToSw')

function urlB64ToUint8Array (base64String) {
    const padding = '='.repeat((4 - base64String.length % 4) % 4)
    const base64 = (base64String + padding).replace(/-/g, '+').replace(/_/g, '/')
    const rawData = window.atob(base64)
    const outputArray = new Uint8Array(rawData.length)
    for (let i = 0; i < rawData.length; ++i) {
        outputArray[i] = rawData.charCodeAt(i)
    }
    return outputArray
}

function createUUID() {
    return 'xxxxxxxx-xxxx-4xxx'.replace(/[xy]/g, c => {
        let r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8)
        return v.toString(16)
    })
}

class Fingerprint {
    constructor () {
        let pre = window.localStorage.getItem(FINGERPRINT_KEY)
        if (!pre) {
            pre = createUUID()
        }
        this.__fingerprint = pre
        document.addEventListener("visibilitychange", () => {
            if (document.visibilityState === 'hidden' && this.__fingerprint) {
                window.localStorage.setItem(FINGERPRINT_KEY, this.__fingerprint)
            }
        }) 
    }
    get fingerprint () {
        return this.__fingerprint
    }
}

class PushManager {
    async getServiceWorkerRegistration () {
        let registration = await navigator.serviceWorker.getRegistration()
        if (!registration) {
            registration = await window.navigator.serviceWorker.register('/sw.js', {
                updateViaCache: 'none'
            })
            await window.navigator.serviceWorker.ready
        }
        registration.addEventListener('updatefound', () => {
            clientToSwChannel.postMessage({
                action: 'skipWaiting'
            })
        })
        return registration
    } 

    async claimNotificationPermission() {
        const perm = Notification.permission
        var granted = false
        if (perm !== 'granted') {
            this.lockApp()
            const status = await Notification.requestPermission()
            if (status === 'granted') {
                this.unlockApp()
                granted = true
            }
        }
        else if (perm === 'granted') {
            this.unlockApp()
            granted = true
        }
        return granted
    }

    lockApp() {
        this.app.$store.commit('LOCK_APP')
    }

    async subscribeUser(registration, forceSubscribeUser=false) {
        let subscription = await registration.pushManager.getSubscription()
        if (!subscription) {
            try {
                subscription = await registration.pushManager.subscribe({
                    userVisibleOnly: true,
                    applicationServerKey: urlB64ToUint8Array(PUBLIC_KEY)
                })
                await this.updateSubscriptionOnServer(subscription)
            }
            catch (e) {
                console.error(e)
                this.app.notifyError(this.app.$t('notifs.push_sub_error'))
            }
        }
        if (forceSubscribeUser) {
            await this.app.sleep(2)
            await this.updateSubscriptionOnServer(subscription)
        }
        return subscription
    }

    unlockApp() {
        this.app.$store.commit('UNLOCK_APP')
    }

    async updateSubscriptionOnServer(subscription) {
        const deviceID = this.fingerprint
        const actor = this.app.$store.state.auth.actorContext.id
        const data = {
            select: {
                actor
            },
            payload: {
                devices: {
                    [deviceID]: subscription.toJSON()
                }
            }
        }
        const patch_resp = await this.app.requestResource('patch_user', {
            vm: this.app,
            data
        })
        if (patch_resp.status === 200) {
            this.app.$store.commit("auth/UPDATE_USER_CONTEXT", data)
        }
    }
}

class SyncManager extends aggregation(Fingerprint, PushManager, UpdateManager) {

    constructor (app, db) {
        super() 
        this.app = app
        this.db = db
        this.isSetup = false
        swToClientChannel.onmessage = this.receiveFromSW.bind(this)
    }
    
    checkPermissions () {
        return !!('Notification' in window && window.navigator.serviceWorker)
    }

    async commitPartialUpdate (data) {
        const {op, resource, query, payload} = data
        const actionName = `partial_${op}_${resource}`
        this.app.$store.dispatch(actionName, [this.app, query, payload])
    }

    async receiveFromSW (ev) {
        const respMsgArr = ev.data.filter(i => i.deviceID !== this.fingerprint)
        for (let item of respMsgArr) {
            await this.commitPartialUpdate (item)
        }
    }
    
    async setup (forceSubscribeUser=false) {
        if (this.isSetup) return
        const registration = await this.getServiceWorkerRegistration()
        const permGranted = await this.claimNotificationPermission()
        this.isSetup = true
        if (!permGranted) return
        await this.subscribeUser(registration, forceSubscribeUser)
    }
}

export default SyncManager