<template>
<div>
    <h4 class="mt-4">{{ $t('headers.invitee_question') }}</h4>
    <p class="mt-6 mb-6">
        {{ $t('headers.invitation_desc') }}
    </p>
    <form>
        <div class="vx-row">
            <div class="vx-col md:w-1/2 w-full mt-5 ">
                <vs-input 
                    type="email" 
                    :label="$t('fields.email')" 
                    v-model="email" 
                    class="w-full" 
                    name="email" 
                    val-icon-danger="clear"
                    :danger="errors.has('email')"
                    :danger-text="errors.first('email')"
                    v-validate="'required|email'" />
            </div>
        </div>

        <div class="vx-row">
            <div class="vx-col md:w-1/2 w-full mt-5">
                <vs-select name='scope' v-model="scope" :danger-text="errors.first('scope')" :danger="errors.has('scope')" class="w-full select-large"
                    :label="$t('fields.scopes')" v-validate="'required'">
                    <vs-select-item :key="scope" :value="scope" :text="scope" v-for="scope in $store.state.auth.scopes" class="w-full" />
                </vs-select>
            </div>
        </div>

        <div class="vx-row">
            <div class="vx-col md:w-1/2 w-full mt-5">
                <vs-select multiple name='workspaces' v-model="workspaces" :danger-text="errors.first('workspaces')" :danger="errors.has('workspaces')" class="w-full select-large"
                    :label="$t('fields.clinics')" v-validate="'required'">
                    <vs-select-item :key="item.id" :value="item.id" :text="item.name" v-for="item in $store.state.auth.availableWorkspaces" class="w-full" />
                </vs-select>
            </div>
        </div>

        <div class="vx-row">
            <div class="vx-col w-full">
                <div class="mt-8 flex flex-wrap items-center justify-end">
                <vs-button class="ml-auto mt-2" @click="submit" :disabled="!formValid">{{ $t('buttons.submit') }}</vs-button>
                <vs-button class="ml-4 mt-2" type="border" color="warning" @click="resetData">{{ $t('buttons.reset')}}</vs-button>
                </div>
            </div>
        </div>


    </form>
</div>
</template>

<script>
export default {
    data () {
        return {
            email: "",
            scope: "",
            workspaces: ""
        }
    },
    computed: {
        formValid () {
            return !this.errors.any()
        }
    },
    methods: {
        async submit () {
            await this.$validator.validate()
            if (this.formValid) {
                this.$store.commit('STATE_LOADING_ON', {})
                const resp = await this.$http.post(
                    '/api/actor/invite/',
                    JSON.stringify({
                        email: this.email,
                        scope: this.scope,
                        workspaces: this.workspaces.map(item => item.toString())
                    }),
                    {headers: {'Range': 'post'}}
                )
                this.$store.commit('STATE_LOADING_OFF', {})
                if (resp.status >= 400) {
                    let server_errors_copy = resp.data
                    const fields = this.$validator.fields.items.map(item => item.name)

                    Object.entries(server_errors_copy).forEach(item => {
                        let [keyname, errs] = item
                        if (fields.includes(keyname)) {
                            if (!Array.isArray(errs)) {
                                errs = Object.values(errs)
                            }
                            errs.forEach(errmsg => {
                                if (Array.isArray(errmsg)) {
                                    errmsg = errmsg.join(', ')
                                }
                                this.errors.add({
                                    field: keyname,
                                    msg: errmsg
                                })
                            })
                            delete server_errors_copy[keyname]
                        }
                    })
                    this.notifyError(this.$t('notifs.op_failed'))
                }
                else {
                    this.notifySuccess(
                        this.$t('notifs.sent_invitation_to', {email: this.email}),
                    )
                    this.$router.push({name: 'staff-list'})
                }
            }
        },
        resetData () {
            this.email = ''
            this.scope = ''
        }
    }
}
</script>