<template>
	<div id="VueCal" class="vuecal" :class="mainClasses">
		<add-new-slot ref="newslot" v-if="$store.state.core.calendarMode === 'scheduler'"
			:doctorID="splitSelectedDoctor"
			:scheduleID="scheduleID"
			:endHour="newSlotHourEnd"
			:startHour="newSlotHourStart"/>
		<slot-details v-if="$store.state.core.calendarMode === 'scheduler'" refs="viewSlot"/>
		<schedule-header ref="header"
			:calendar="activeCalendar"
			:currentView="currentView"
			:isMounted="isMounted"
			:currentDate="currentDate"
			:currentViewChanged="currentViewChanged" />
		<div class="calendar-mask absolute w-full h-full top-0 left-0" v-if="searchbarOn"></div>
		<div v-if="currentView !== 'month' && $store.state.core.emptyTimeframe">
			<div class="relative center">
				<img src="@/assets/images/pages/nodocs.png" alt="not-found" class="mx-auto center" width="324">
				<h3 class="text-center font-light mb-16" style="color: #718096">{{ $t('headers.no_visits_in_timeframe') }}</h3>
			</div>
		</div>
		<vue-cal
			v-show="currentView == 'month' || $store.state.core.calendarMode === 'scheduler' && !$store.state.core.emptyTimeframe"
			ref="vuecal"
			:active-view="currentView"
			:cell-click-hold="false"
			:class="[`${currentView}-mode`, searchbarOn && 'searchbar-on' ]"
			:dblclickToNavigate="false"
			:disable-views="['years', 'year']"
			:dragToCreateThreshold="5"
			:editable-events="{drag: true, resize: true, create: true}"
			:events="currentView === 'month' && monthlyEvens || allEvents"
			:hide-weekdays="[7]"
			:locale="$i18n.locale"
			:min-event-width="100"
			:on-event-create="onGenericEventCreate"
			:on-event-dblclick="onEventDBClick"
			:selected-date="currentDate"
			:showWeekNumbers="false"
			:snap-to-time="15"
			:splitDays="splitDays"
			:time-from="minHour"
			:time-to="maxHour"
			:time-step="default_visit_interval"
			hideViewSelector
			hide-title-bar
			showAllDayEvents
			show-week-numbers
			sticky-split-labels
			today-button
			week-number-cell
			@cell-dblclick="addNewEvent($event)"
			@event-drag-create="dragCreateEvent($event)"
			@event-drop="onSlotMoved"
			@event-duration-change="onDurationChange"
			@view-change="calendarChangedTimeframe" >

			<template #cell-content="{ cell, view, split, events }">
				<div v-if="currentView !== 'month' && daysClosed.includes(cell.specialHours.day - 1)">
					<div class="closed w-full h-full absolute">
						<span>{{ $t('headers.clinic_closed') }}</span>
					</div>
				</div>
				<div v-else-if="scheduleLoading" class="loading-box h-full w-full flex z-50">
					<div class="spinner justify-center self-center">
						<div class="double-bounce1"></div>
						<div class="double-bounce2"></div>
					</div>
				</div>
				<div
					class="vuecal__no-event h-full flex"
					:class="!events.length && !currentScheduleMeta.find(item => cell.formattedDate === item.date.format('YYYY-MM-DD')) && 'no-schedule' || 'no-visits'"
					:data-displaytext="noScheduleDisplayText"
					v-else-if="view.id === 'week'">
				</div>
				<div v-else-if="view.id === 'month'">
					<div class="vuecal__cell-date" v-if="view.endDate.format('MM') == cell.startDate.format('MM')" :class="events[0] && [events[0].status, 'cell-with-events']" @click="events[0] && selectDayFromMonthly(events[0])">
						<div class="vuecal__event-title" :class="events[0] && 'has-events'">
							<h4 class="font-light"><div>{{ cell.content }}</div></h4>
							<div class="w-full absolute left-0 mt-1">
								<div class="text-center center text-white">{{ statusMap[events[0] && events[0].status] }}</div>
							</div>
						</div>
						<div class="absolute top-0 left-0" v-if="events[0]">
							<div class="relative m-2">
								<img :src="docs[events[0].split].avatar"
									alt="doctor-img"
									v-tooltip.top-center="getDocsName(docs[events[0].split])"
									class="m-0 rounded-full shadow-md cursor-pointer absolute z-30"
									width="32"
									height="32"/>
                            </div>
						</div>
					</div>
					<div v-else class="vuecal__cell-date">
						<div class="vuecal__event-title">
							<h4 class="text-grey font-light">{{ cell.content }}</h4>
						</div>
					</div>
				</div>

			</template>

			<template #no-event>
				<div class="w-full flex h-full no-visits"></div>
			</template>
			<template #weekday-heading="{heading}" v-if="currentView !== 'month'">
				<p @click="selectDay(heading)"
					class='cursor-pointer hover:underline hover:text-primary'>
					{{ heading.label }} {{ heading.date.format('DD') }}
				</p>
			</template>
			<template #event="{event}">
				<div v-if="event.class === 'main'" class="h-full" :class="slotDynClass(event)">
					<div class="vuecal__event-title text-black truncate" v-html="event.title"></div>
					<div class="vuecal__event-time truncate">
						{{ new Date(event.start).formatTime('HH:mm') }}
						<span>&nbsp;- {{ new Date(event.end).formatTime('HH:mm') }}</span>
					</div>
					<template v-if="new Date(event.start) >= currentDate">
						<div class="schedule-control-btns right-0 top-0 absolute">
							<div class="flex" style="margin: 5px 2px 0 0">
								<span class="control-btn no-rotation " style=" z-index: 12; opacity: 0" @click="showSlotDetails(event)">
									<feather-icon v-tooltip="{content: $t('buttons.show_details'), classes: 'text-sm'}" class='text-primary' icon="InfoIcon" svgClasses="w-6" style="height: inherit; bottom: 3px" />
								</span>
								<span class="control-btn delete-schedule " style=" z-index: 12; opacity: 0" @click="openCancelAppointmentPrompt(event)">
									<feather-icon v-tooltip="{content: $t('buttons.cancel'), classes: 'text-sm'}" class='text-danger' icon="XCircleIcon" svgClasses="w-6" style="height: inherit; bottom: 3px" />
								</span>
							</div>
						</div>
					</template>
				</div>
				<div v-else-if="event.class === 'lunch'">
					<div class="vuecal__event-time">
						<span class="font-medium">{{ $t('headers.break') }}: </span>
						{{ new Date(event.start).formatTime('HH:mm') }}
						<span> - {{ new Date(event.end).formatTime('HH:mm') }}</span>
					</div>
				</div>
				<div v-else-if="event.class && event.class.match('closed-morning|closed-evening')">
					<div class="vuecal__event-time">
						<template v-if="event.class === 'closed-morning'">
							<span class="font-medium">{{ $t('headers.unavailable_until') }}: </span>
							<span>{{ new Date(event.end).formatTime('HH:mm')}}</span>
						</template>
						<template v-else>
							<div :style="{ 'margin-top': (event.end - event.start) / 6e+4 <= 20  && '-10px' || 0 }">
								<span class="font-medium">{{ $t('headers.unavailable_from') }}: </span>
								<span>{{ new Date(event.start).formatTime('HH:mm') }}</span>
							</div>
						</template>
					</div>
				</div>
				<div v-else-if="event.class === 'slot-loading'" class="h-full">
					<div class="loading-box slot-loading w-full h-full flex z-50">
						<div class="spinner justify-center self-center">
							<div class="double-bounce1"></div>
							<div class="double-bounce2"></div>
						</div>
					</div>
				</div>
				<div v-else-if="event.class && event.class.match('interim-slot|interim-overlap')">
					<div class="vuecal__event-title text-black truncate" v-html="event.title"></div>
					<div class="vuecal__event-time truncate">
						{{ new Date(event.start).formatTime('HH:mm') }}
						<span>&nbsp;- {{ new Date(event.end).formatTime('HH:mm') }}</span>
					</div>
				</div>
				<span v-else class="hidden"></span>
			</template>

			<template #split-label="{ split }">
				<div>
					<div class="items-center justify-center flex">
						<vs-avatar
							v-if="!docs[split.id] || !docs[split.id].avatar"
							style="font-size: 23px;"
							size="35px"
							class="m-0"
							:class="`doctor-avatar-${split.index+1}`"
							:text="`${docs[split.id].name[0]}${docs[split.id].last_name[0]}`"/>
							<img v-else :src="docs[split.id].avatar"
								alt="doctor-img"
								:class="`doctor-avatar-${split.index+1}`"
								class="m-0 rounded-full shadow-md cursor-pointer absolute z-30 doctor-avatar border-solid"
								width="50"
								height="50"/>
					</div>
					<h5 class="doc-header text-center mt-10 font-normal" :style="`color: ${split.color}`">{{ split.label }}</h5>
				</div>
			</template>

		</vue-cal>
		<schedule-planner ref="planner" v-show="$store.state.core.calendarMode === 'planner'" />
	</div>
</template>

<script>
import AddNewSlot from './AddNewSlot.vue'
import SlotDetails from './SlotDetails.vue'
import ScheduleHeader from './ScheduleHeader.vue'
import SchedulePlanner from './SchedulePlanner.vue'
import utils from './utils.js'

function round_to_precision (x, precision) {
    const y = +x + (precision === undefined ? 0.5 : precision/2)
    return y - (y % (precision === undefined ? 1 : +precision))
}

const VALID_VIEWS = ['day', 'week', 'month']

export default {
	components: {
		AddNewSlot,
		SlotDetails,
		ScheduleHeader,
		SchedulePlanner
	},
	beforeDestroy () {
		this.$store.commit('SET_GLOBAL_MESSAGE', null)
		this.$store.commit('core/RESELECT_FILTERED_PATIENT')
		this.$store.commit('core/RESET_INTERIM_PATIENT')
		this.$store.commit('core/RESET_SEARCHED_PATIENT_SLOTS')
	},
	mounted () {
		this.$nextTick(() => {
			this.$store.commit('UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN', true)
		})
		this.isMounted = true
		window.cal = this

		// set interim patien, if present
		const interimPatient = this.$store.state.core.interimPatient
		if (interimPatient) {
			const fullname = interimPatient.name + ' ' + interimPatient.last_name
			const msg = `Adding new appointment for <b class='font-medium'>${fullname}</b>`
			this.$store.commit('SET_GLOBAL_MESSAGE', [msg , 'primary'])
		}

		// parse the query, if present
		const query = this.$router.currentRoute.query
		const gotoView = query.view
		const gotoDate = query.date && new Date(parseInt(query.date))
		
		if (gotoDate) {
			this.$nextTick(() => {
				this.currentDate = gotoDate
				let data  = {
					min_date: gotoDate.toJSON().split('T')[0]
				}
				this.$store.dispatch('core/fetchSlots', {
					data,
					view: this.currentView,
					vm: this,
					bh: this.clinic.business_hours,
					currentDate: gotoDate
				})
				const businessHours = this.clinic.business_hours
				this.$store.commit('core/SET_BUSINESS_HOURS', [businessHours, this.$refs.vuecal.view.startDate])
			})
		} 
		if (gotoView && VALID_VIEWS.includes(gotoView)) {
			this.$nextTick(() => {
				this.$refs.vuecal.switchView(gotoView)
			})
		}
		window.location.href = window.location.href.split('?')[0]
		this.$watch('this.$refs.vuecal.view.startDate', 
			() => {
				this.currentViewChanged++
				this.$store.dispatch('core/fetchSlots', {view: this.currentView, vm: this, bh: this.clinic.business_hours, currentDate: this.currentDate})
				const businessHours = this.clinic.business_hours
				this.$store.commit('core/SET_BUSINESS_HOURS', [businessHours, this.$refs.vuecal.view.startDate])
			},
			{
				immediate: true
			}
		)
	},	

	data: () => ({
		searchbarOn: false,
		outlierEvents: [],
		currentDate: new Date(),
		currentViewChanged: 0,
		isMounted: false,
		limsChanged: 0,
		newSlotHourStart: null,
		newSlotHourEnd: null,
		roundTo: 15,
		rawSelectedDoctor: null,
		splitSelectedDoctor: null,
		scheduleID: null,
		deleteEventFunction: () => {},
		selectedSlotID: null,
		dailyMinHour: '08:00',
		dailyMaxHour: '18:00',
		lastEventDBClick: null
	}),

	computed: {
		activeCalendar () {
			this.currentViewChanged
			if (this.calendarMode === 'scheduler') {
				return this.$refs.vuecal
			}
			return this.$refs.planner && this.$refs.planner.$refs.cal
		},
		allSlots () {
            return Object.values(this.$store.state.core.schedulerSlots).flatMap(i => i)
		},
		calendarMode () {
			return this.$store.state.core.calendarMode
		},
		clinic () {
			return this.$store.getters['core/activeClinic']() || {}
		},
		currentScheduleMeta () {
			try {
				return this.$store.state.core.scheduleMetaMap[this.selectedDoctor.actor] || []
			}
			catch (e) {
				return []
			}
		},
		currentView (ret) {
			this.currentViewChanged
			try {
				ret = this.$refs.vuecal.view.id
			}
			catch (e) {
				ret = 'week'
			}
			if (ret === 'day') {
				this.adjustDailyView()
			}
			return ret
		},
		daysClosed () {
			const bh = this.clinic.business_hours
			return Array.from('x'.repeat(6), (_, i) => i).filter(i => !bh[i])
		},
		default_visit_interval () {
			return this.clinic.default_visit_interval
		},
		docs () {
			return this.$store.state.core.docs
		},
		mainClasses () {
			return {
				compressed: this.currentView === 'day' && this.sidebarOn,
				singleDoc: Object.keys(this.docs).length === 1
			}
		},
		minHour (raw) {
			this.limsChanged
			try {
				if (this.currentView === 'day') {
					raw = this.dailyMinHour
				}
				else {
					raw = this.$store.state.core._currentScheduleLims[this.selectedDoctor.actor][0]
				}
			}
			catch (e) {
				raw = '08:00'
			}
			const [hour, min] = raw.split(':')
			return parseInt(hour) * 60 + parseInt(min)
        },
        maxHour (raw) {
			this.limsChanged
			try {
				if (this.currentView === 'day') {
					raw = this.dailyMaxHour
				}
				else {
					raw = this.$store.state.core._currentScheduleLims[this.selectedDoctor.actor][1]
				}
			}
			catch (e) {
				raw = '18:00'
			}
			const [hour, min] = raw.split(':')
			return parseInt(hour) * 60 + parseInt(min)
		},
		noScheduleDisplayText () {
			return this.$t('headers.no_schedules')
		},
		scheduleLoading () {
			return this.$store.state.core.scheduleLoading
		},
		selectedDoctor () {
			const store = this.$store.state.core
			return store.calendarActiveDoctor || store.docs[Object.keys(store.schedulerSlots)[0]] || store.docs[Object.keys(store.docs)[0]] || {}
		},
		sidebarOn () {
			return this.$store.state.core.addSlotPrompt || this.$store.state.core.viewSlotPrompt
		},

		allEvents () {
			return [
				...this.mainEvents,
				...this.outlierEvents
			]
		},

		monthlyEvens () {
			// atm only support the searched patient's slots overview
			return this.$store.state.core.searchedPatientSlots
		},

		mainEvents () {
			if (this.currentView === 'week') {
				const actorID = this.selectedDoctor.actor
				return this.$store.getters['core/allSchedulerSlotsByDoctor'](actorID, this)
			}
			if (this.currentView === 'day') {
				const slots = this.$store.getters['core/aggregateAllSchedulerEvents'](this.currentDate, this)
				return slots
			}
		},
		splitDays () {
			if (this.currentView === 'day')	 {
				return Object.entries(this.$store.state.core.docs).map(([docID, obj], index) => {
					const label = [obj.name, obj.last_name].join(' ')
					return {
						index,
						id: parseInt(docID),
						class: `doctor-split`,
						label
					}
				})
			}
			return []
		},

		statusMap () {
			return {
				confirmation_pending: this.$t('misc.visit_pending'),  
				confirmed: this.$t('misc.visit_confirmed'),  
				booked: this.$t('misc.visit_booked'),  
			}
		}
	},

	methods: {
		addNewEvent (startTime, endTime=null, split=null) {
			if (this.currentView === 'month') return
			const now = new Date()

			// double click on an event?
			const nowSeconds = Math.round(now.getTime() / 1000)
			if (this.lastEventDBClick == nowSeconds || (this.lastEventDBClick - 1) == nowSeconds || (this.lastEventDBClick + 1) == nowSeconds) {
				return
			}
			if (startTime < now) {
				this.$store.commit('core/RESET_INTERIM_SLOT')
				return this.notifyError(this.$t('notifs.no_visits_in_past'))
			}
			let doctorID = this.selectedDoctor.actor
			if (this.currentView === 'day' && !(startTime instanceof Date)) {
				doctorID = startTime.split
				startTime = startTime.date
			}
			else if (split) doctorID = split
			this.splitSelectedDoctor = doctorID
			
			let hour = parseInt(startTime.format('HH'))
			let minutes = round_to_precision(startTime.format('mm'), 15)
			if (minutes == 60) {
				hour++
				minutes = '00'
			}
			this.newSlotHourStart = new Date(startTime.getFullYear(), startTime.getMonth() , startTime.getDate(), hour, minutes)
			// overlapping with another slot/break/closure?
			const newSlotDate = this.newSlotHourStart.format('MM-DD')
			const endSlotDate = endTime || this.newSlotHourStart.addMinutes(this.default_visit_interval)
			this.newSlotHourEnd = endSlotDate
			const newSlotDateTime = this.newSlotHourStart.getTime()
			const endSlotDateTime = endSlotDate.getTime()
			const sameDaySlots = this.allEvents.filter(slot => {
				const {start, end} = slot
				return start instanceof Date && newSlotDate === start.format('MM-DD') && start.getTime() != end.getTime()
			})
			
			const sameDocOverlaps = sameDaySlots.find(slot => {
				if (doctorID != slot.doctorID) return false
				let {start, end} = slot
				start = start.getTime()
				end = end.getTime()
				
				return (
					start < newSlotDateTime && end > newSlotDateTime // between the slot
				) || (
					start < endSlotDateTime && end > endSlotDateTime // between the slot
				) || (
                    start > newSlotDateTime && end < endSlotDateTime // slot within
                )
			})
			
			if (sameDocOverlaps) {
				this.deleteEventFunction()
				return this.notifyError(this.$t('notifs.no_time_new_visit'))
			}

			// do we have enough office space to allow new appointment?
			const officeCount = this.clinic.office_count
			const officeOverlaps = this.allEvents.reduce((prev, slot) => {
				if (doctorID == slot.doctorID || slot.class != 'main') return prev
				let {start, end} = slot
				start = start.getTime()
				end = end.getTime()
				let res = (
                    start <= newSlotDateTime && end > newSlotDateTime // within the slot
                ) || (
                    start < endSlotDateTime && end >= endSlotDateTime // within the slot
                ) || (
                    start >= newSlotDateTime && end <= endSlotDateTime // slot within
                )
				return res + prev
			}, 0)

			if (officeOverlaps >= officeCount) {
				this.deleteEventFunction()
				return this.notifyError(this.$t('overlapMessages.noOffices'))
			}

			const dayIndex = this.newSlotHourStart.getDay() - 1
			
			this.scheduleID = this.$store.state.core._schedulerClosureHoursEachDoc[dayIndex].doctors[doctorID].scheduleID
			
			this.$store.commit('core/SET_ADD_SLOT_PROMPT', true)
			this.toggleBodyOverflow('hidden')
			const payload = {
				start: this.newSlotHourStart,
				end: endSlotDate,
				class: 'interim-slot',
				title: 'New Appointment',
				split: parseInt(doctorID)
			}
			this.$store.commit('core/SET_INTERIM_SLOT', payload)
		},

		adjustDailyView () {
			const scheduleMap = this.$store.state.core.scheduleMetaMap
			for (let docID of Object.keys(this.docs)) {
				if (!scheduleMap[docID]) {
					// no schedule
					setTimeout(() => {
						const el = document.querySelector(`.vuecal__bg .vuecal__cell-content[data-split="${docID}"]`)
						if (el) {
							el.dataset['displaytext'] = this.noScheduleDisplayText
							el.classList.add('no-schedule')
						}
					}, 200)
				}
			}
		},

		calendarChangedTimeframe ($event) {
			if (this.currentView === 'month') return
			if (this.currentView === 'week' || (this.currentView === 'day' && this.currentDate.addDays(-1) == $event.startDate || this.currentDate.addDays(1) == $event.startDate)) {
				this.currentDate = $event.startDate.clone()
			}
			const businessHours = this.clinic.business_hours
			this.$store.commit('core/SET_BUSINESS_HOURS', [businessHours, this.currentDate])
		},

		cancelAppointment (ev) {
			return () => {
				this.$store.dispatch('core/cancelAppointment', [ev, this])
			}
		},

		dbEventClick () {
			this.addNewEvent.call(this, arguments)
		},

		dragCreateEvent (ev) {
			if (this.currentView === 'month') return
			if (this.currentView === 'day') {
				return this.addNewEvent(ev.start, ev.end, ev.split)
			}
			this.addNewEvent(ev.start, ev.end)
		},

		getDocsName (doc) {
			return doc.name + ' ' + doc.last_name
		},

		showSlotDetails (ev) {
			// change the view to daily
			const start = ev.start.clone()
			this.currentDate = start
			this.$refs.vuecal.switchView('day', start)
			this.$store.dispatch('core/viewSlotDetails', [ev.id, this])
		},

		openCancelAppointmentPrompt (ev) {
			this.$vs.dialog({
				text: this.$t('notifs.prompt_cancel_visit'),
				acceptText: this.$t('buttons.accept'),
				cancelText: this.$t('buttons.cancel'),
                type: 'confirm',
				color: 'danger',
                title: this.$t('notifs.visit_cancel_cta'),
                accept: this.cancelAppointment(ev)
            })
		},

		onDurationChange (event) {
			if (this.currentView === 'month') return
			this.$store.dispatch('core/resizeSlot', [event, this])
		},

		onEventDBClick (event) {
			this.lastEventDBClick = Math.round((new Date()).getTime() / 1000)
		},
		
		onGenericEventCreate (event, deleteEventFunction) {
			event.class = 'interim-slot'
			event.title = this.$t('notifs.new_visit_cta')
			this.deleteEventFunction = deleteEventFunction
			return event
		},

		onSlotMoved (event) {
			if (this.currentView === 'month') return
			this.$store.dispatch('core/dragSlot', [event, this])
		},

		selectDay (heading) {
			const date = heading.date.clone()
			this.currentDate = date
			this.$refs.vuecal.switchView('day', date)
		},

		selectDayFromMonthly (event) {
			// fetch the slots from that date 
			const date = event.start
			this.currentDate = date
			const data = {
				min_date: date.toJSON().split('T')[0]
            }
			this.$store.dispatch('core/fetchSlots', {data, view: this.currentView, vm: this, bh: this.clinic.business_hours, currentDate: date})
			this.$refs.vuecal.switchView('day', date)
		},

		slotDynClass (event) {
			const base = [event.status]
			const delta = event.endTimeMinutes - event.startTimeMinutes
			if (delta <= 15) base.push('is-brief')
			if (delta <= 30) base.push('is-half-hour')
			return base
		}
	},

	watch: {
		async mainEvents () {
			await this.sleep(.5)
			this.outlierEvents = utils.addOutliers(this)
			if (this.currentView === 'day') {
				let date = this.currentDate.format('DD/MM')
				let lims = this.$store.getters['core/scheduleDurationsPerDate'][date]
				while (!lims) {
					await this.sleep(.1)
					lims = this.$store.getters['core/scheduleDurationsPerDate'][date]
				}
				this.dailyMinHour = lims[0]
				this.dailyMaxHour = lims[1]
			}
		}
	}
}
</script>


