<template>
<div class="h-screen flex w-full bg-img vx-row no-gutter items-center justify-center" id="page-login" style="z-index: 9999;">
		<div class="vx-col sm:w-1/2 md:w-1/2 lg:w-full sm:m-0">
			<div class="lg:hidden  w-full flex -mt-4">
				<img src="@/assets/images/laima-logo-symbol-only.png" alt="login" class="pb-6 logo-solo mx-auto">
			</div>
			<vx-card class="max-w-2xl mx-auto">
				<div slot="no-body" class="full-page-bg-color">
					<swiper ref="swiper" :options="swiperOptions">
						<swiper-slide>
							<div class="vx-row no-gutter justify-center items-center">
								<div class="vx-col hidden lg:block lg:w-1/2 flex-shrink-0">
									<img src="@/assets/images/laima-logo-plain-vertical.png" alt="login" class="flex-shrink-0 w-full mx-auto bg-contain">
								</div>

								<div class="vx-col sm:w-full md:w-full lg:w-1/2">
									<div class="p-8 login-tabs-container">

										<div class="vx-card__title mb-10">
											<h4 class="mb-1 font-semibold">{{ title }}</h4>
											<p>{{ welcomeText }}</p>
										</div>

										<div>
											<vs-input
													name="email"
													icon-no-border
													icon="icon icon-user"
													icon-pack="feather"
													:placeholder="this.$t('placeholders.email')"
													v-model="email"
													v-on:keyup.enter="login"
													class="w-full"/>

											<vs-input
													type="password"
													name="password"
													icon-no-border
													icon="icon icon-lock"
													icon-pack="feather"
													:placeholder="this.$t('fields.password')"
													v-model="password"
													v-on:keyup.enter="login"
													class="w-full mt-6" />

											<div class="flex flex-wrap justify-between mt-8 my-5">
												<router-link :to="{name: 'pageForgottenPassword'}">{{ $t('headers.forgot_pass') }}</router-link>
											</div>
											<vs-button  type="border" @click="registerUser">Register</vs-button>
											<vs-button class="float-right" :disabled="!validateForm" @click="login">{{ $t('headers.login') }}</vs-button>

										</div>

									</div>
								</div>
							</div>
						</swiper-slide>
						<swiper-slide>
							<div class='w-selection-container' ref='contsel'>
								<vs-row>
									<vs-col vs-w="12">
										<h3 class='p-6'>{{ $t('headers.select_clinic') }}</h3>
										<vs-divider class="custom-divider"></vs-divider>
										<vs-row v-for="workspace_obj in availableWorkspaces" :key="workspace_obj.id"  vs-type="flex" vs-justify="space-around" vs-align="center">
											<vs-col class="workspace-tile" vs-w="12" >
													<div class="workspace-row noselect" @click="selectWorkspace(workspace_obj)">
															{{ workspace_obj.name }}
															<svg xmlns="http://www.w3.org/2000/svg" width="24px" height="24px" viewBox="0 0 24 24" fill="none" 
																stroke="currentColor" stroke-width="2" stroke-linecap="round" stroke-linejoin="round" 
																class="chevron-custom feather feather-chevron-right w-4 h-4"><polyline points="9 18 15 12 9 6"></polyline>
															</svg>
													</div>
											</vs-col>
											<vs-divider class="custom-divider"></vs-divider>
										</vs-row>
									</vs-col>
								</vs-row>
							</div>
						</swiper-slide>
					</swiper>
				</div>
			</vx-card>
		</div>
	</div>
</template>

<script>
import auth from '@/auth/authService'
import 'swiper/dist/css/swiper.min.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'

export default {
	components: {
		swiper,
		swiperSlide
	},

	async mounted () {
		// const loggedin = await this.$store.state.auth.isUserLoggedIn()
		// if (loggedin) {
		// 	this.notifyAlreadyLogedIn()
		// 	router.push(router.currentRoute.query.to || '/')
		// }
	},

	data () {
		return {
			email: '',
			password: '',
			swiperOptions: {
				allowTouchMove: false
			}
		}
	},

	computed: {
		login_sources () {
			return {
				resetPass: {
					title: this.$t('login_sources.resetPass.title'),
					msg: this.$t('login_sources.resetPass.msg')
				},
				activated: {
					title: this.$t('login_sources.activated.title'),
					msg: this.$t('login_sources.activated.msg')
				},
				configured: {
					title: this.$t('login_sources.configured.title'),
					msg: this.$t('login_sources.configured.msg')
				},
				compromised: {
					title: this.$t('login_sources.compromised.title'),
					msg: this.$t('login_sources.compromised.msg')
				}
			}	
		},
		validateForm() {
			return !this.errors.any() && this.email != '' && this.password != '';
		},
		selectedWorkspace () {
			return this.$store.state.auth.selectedWorkspace
		},
		availableWorkspaces () {
			return this.$store.state.auth.availableWorkspaces
		},
		title () {
			const source = this.$route.query.source
			return source && this.login_sources[source] && this.login_sources[source].title || this.$t('headers.login')
		},
		welcomeText () {
			const source = this.$route.query.source
			return source && this.login_sources[source] && this.login_sources[source].msg || this.$t('headers.welcome_back')
		}
	},

	methods: {
		
		forceLogout() {
			auth.clearLocalStorage()
			this.$http.get('/api/actor/logout/')
		},

		login () {
			this.forceLogout()
			const loginPayload = {
				userDetails: {
					email: this.email,
					password: this.password
				},
				slider: this.$refs.swiper.swiper,
				...this
			}
			if (Object.keys(this.$store.state.auth.selectedWorkspace)[0]) {
				loginPayload.userDetails.workspace = this.$store.state.auth.selectedWorkspace.id
			}
			this.$store.dispatch('auth/login', [loginPayload, this])
		},

		notifyAlreadyLogedIn() {
			// this.$vs.notify({
			// 	title: 'Login Attempt',
			// 	text: 'You are already logged in!',
			// 	position:'top-center',
			// 	iconPack: 'feather',
			// 	icon: 'icon-alert-circle',
			// 	color: 'warning'
			// });
		},

		selectWorkspace (workspace) {
			this.$store.commit('auth/SET_CURRENT_WORKSPACE', workspace)
			this.login()
		},

		async registerUser() {
			// if(await this.$store.state.auth.isUserLoggedIn()) {
			// 	this.notifyAlreadyLogedIn();
			// 	return false;
			// }
			this.$router.push({name: 'pageRegister'})
		},

	}
}
</script>

