}
.w-selection-container {
	overflow: auto;
	height: 323px;
	width: 350px;
	background: white;
	margin: 0 auto;
	.custom-divider {
		margin: 0;
	}
	h3 {
		text-align: center;
		position: sticky;
		top: 0;
		z-index: 1;
		background: #fff;
		box-shadow:0 2px 25px -2px #eee;
	}
	.workspace-tile {
		width: 100%;
		&:hover {
			background: #eff2f7ad;
			cursor: pointer;
		}
		&:active {
			background: #eff2f7;
			cursor: pointer;
		}
	}
	.chevron-custom {
		float: right;
		margin-right: 50px;
		position: relative;
		top: 11px;
	}
	.workspace-row {
		font-size: 15px;
