<template>
    <div class="h-screen flex w-full bg-img vx-row no-gutter justify-center items-center">
        <div class="vx-col sm:w-3/5 md:w-3/5 lg:w-3/4 xl:w-3/5 sm:m-0 m-4" v-if="!isInvitedView">
            <vx-card>
                <div slot="no-body" class="full-page-bg-color">
                    <div class="form-wrap">
                        <h3 class='text-center first-form-header pt-8 pb-6'>{{ $t('headers.account_config') }}</h3>
                        <h5 class='text-center first-form-header sub'>
                            {{ $t('headers.account_activated') }}
                        </h5>
                        <first-form-clinic />
                    </div>
                </div>
            </vx-card>
        </div>

        <div v-else>
            <h2 class="mb-4 text-center p-10 pb-2">{{ $t('headers.account_activated_short') }}</h2>
            <h4 class="mb-4 text-center p-6 pb-10" style="color: rgb(100, 100, 100)"> {{ $t('headers.gonna_redirect') }}</h4>
        </div>
    </div> 
</template>

<script>
import EventBus from '@/EventBus.js'
import FirstFormClinic from './FirstFormClinic'

export default {

    components: {
        FirstFormClinic
    },

    async mounted () {
        if (this.isInvitedView) {
            await this.sleep(2)
            this.$router.push('/')
        }
    },

    computed: {
        reg_token () {
            return this.$route.params.reg_token
        },
        isInvitedView () {
            return this.$route.name === 'activateInvitedAccount'
        }
    },
    
    data () {
        return {
            workspace_name: ''
        }
    },
    
    methods: {
        async validateAndLogin () {
            const valid = await this.$validator.validate()
            if (!valid) return

            const activation_link = this.isInvitedView ? `/api/actor/invitee/activate/email/${this.reg_token}/` : `/api/actor/created/activate/email/${this.reg_token}/`
            const resp = await this.$http.post(
                activation_link,
                JSON.stringify({workspace_name: this.workspace_name}), 
                {headers: {Range: 'post'}}
            )

            let err
            if (resp.status === 422) {
                err = resp.data
                const errors = this.errors
                return Object.entries(err).forEach(item => {
                    let [keyname, errs] = item
                    errs.forEach(errmsg => {
                        errors.add({
                            field: keyname,
                            msg: errmsg
                        })
                    })
                })
            }
            else if (resp.status !== 200) {
                err = resp.data
            }

            if (err) {
                return this.notifyError(
                    this.$t('notifs.account_activation_failed')
                )
            }
            EventBus.$emit('login', {})
            // this.$router.push({name: 'page-login', query: {source: 'activated'}})
        }
    }
}
</script>

<style lang="scss">
    .form-wrap {
        max-height: '1034px';
        overflow: 'auto';
    }
</style>