<template>
    <laima-form
        formScope="step-1"
        @changed="onUpdated"
        ref="laimaForm"
        :onSubmit="true"
        :modelsContainer="widgetContainer"
        :formFields="formFields">
        <template #afterfield="{item}">
            <div v-if="widgetContainer.use_widget && item.name === 'website' && widgetContainer.website" 
                :class="hasWidgetInstalled ? 'success' : 'warning'"
                class="con-vs-alert form-alert flex pl-4 con-vs-alert-primary con-icon">
                <div class="vs-alert con-icon w-full">
                    <i :class='isVerifyingDomain && "animated infinite rotated slow"'
                        @click="!isVerifyingDomain && domainChanged()"
                        class="vs-icon notranslate icon-scale icon-alert feather icon-refresh-cw text-black null cursor-pointer hover:text-grey-900"></i>
                    <div class="pl-4" v-if="!hasWidgetInstalled" :style="isVerifyingDomain && {opacity: .5}">
                        <div class="alert-text font-medium text-lg">
                            {{ $t('headers.widget_not_installed') }}.
                        </div>
                        <div class="alert-text font-normal my-2">
                            {{ $t('headers.copy_script') }}.
                        </div>
                        <pre class="code-snippet rounded select-text"><code class="inline-block bg-transparent"><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>script </span><span class="token attr-name">src</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>https://cdn.statically.io/gh/laima-app/laimafront/dev/frameloader.min.js<span class="token punctuation">"</span></span><span class="token punctuation">&gt;</span></span></code></pre>
                    </div>
                    <div class="pl-4" v-else>
                        <div class="alert-text font-medium text-lg">
                            {{ $t('headers.widget_installed') }}.
                        </div>
                    </div>
                </div>
            </div>
        </template>
        <template #websiteBeforeValue>
            <vs-select
                name="website"
                class="absolute"
                style="max-width: 85px; z-index: 1000; border: none;"
                v-model="protocol">
                <vs-select-item value="http://" text="http://"  />
                <vs-select-item value="https://" text="https://"  />
            </vs-select>
        </template>
        <template #use_widget>
            <vs-switch
                color="success"
                v-model="widgetContainer.use_widget"
            />
        </template>
        <template #footer>
            <div></div>
        </template>
    </laima-form>

</template>

<script>

export default {
    props: ['clinic'],
    data () {
        return {
            isVerifyingDomain: false,
            protocol: 'http://',
            widgetContainer: {
                use_widget: false,
                widget_select_clinic: false,
                procedures: []
            }
        }
    },
    computed: {
        activeClinic () {
            return this.$store.getters['core/activeClinic']() || {}
        },
        clinicsLen () {
            return this.$store.state.core.clinics.length
        },
        hasWidgetInstalled () {
            return this.activeClinic.widget_installed
        },
        formFields () {
            return [
                {
                    name: 'use_widget',
                    type: 'switch',
                    label: this.$t('fields.use_widget'),
                    helpText: this.$t('help_texts.use_widget')
                },
                {
                    name: 'widget_select_clinic',
                    type: 'switch',
                    hidden: !this.widgetContainer.use_widget || !this.widgetContainer.website || this.clinicsLen === 1,
                    label: this.$t('fields.widget_select_clinic'),
                    helpText: this.$t('help_texts.widget_select_clinic')
                },
                {
                    name: 'website',
                    type: 'text',
                    hidden: !this.widgetContainer.use_widget,
                    label: this.$t('fields.website'),
                    helpText: this.$t('help_texts.website'),
                    placeholder: this.$t('placeholders.website'),
                    validators: {
                        required: true,
                        url: true
                    }
                },
                {
                    name: 'procedures',
                    type: 'select',
                    multiple: true,
                    hidden: !this.widgetContainer.use_widget,
                    label: this.$t('headers.procedures'),
                    helpText: this.$t('help_texts.widgets_procedures'),
                    model: this.widgetContainer.procedures,
                    options: this.procedures,
                    validators: {
                        required: true
                    }
                }
            ]
        },
        procedures () {
            const procedures = this.$store.state.core.clinicProcedures || []
            return procedures.map(i => ({value: i.id, text: i.name}))
        }
    },
    mounted () {
        window.wid = this
        this.$set(this.widgetContainer, 'use_widget', !!this.activeClinic.domain)
        if (this.activeClinic.domain) {
            const url = new URL(this.activeClinic.domain)
            this.protocol = (url && url.protocol || 'http:') + '//'
            this.$set(this.widgetContainer, 'website', url.hostname)
        }
        const in_widget_procs = this.$store.state.core.clinicProcedures.filter(i => i.in_widget).map(i => i.id)
        this.widgetContainer.procedures = in_widget_procs
    },
    methods: {
        domainChanged () {
            // check if widget installed
            this.isVerifyingDomain = true
            const clinicID = this.activeClinic.id
            const callback = resp => {
                if (resp.status === 200) {
                    this.isVerifyingDomain = false
                }
            }
            const url = this.protocol + this.widgetContainer.website
            this.$store.dispatch('core/verifyDomain', [clinicID, url, this, callback])
        },
        async onUpdated ([field, val]) {
            let cachedValue = null
            if (field === 'procedures') {
                cachedValue = this.widgetContainer[field]
            }
            this.$set(this.widgetContainer, field, val)
            if (field === 'widget_select_clinic') {
                this.$store.dispatch('core/editClinic', [this, this.activeClinic.id, {
                    widget_select_clinic: val
                }, () => {
                    this.notifySuccess('Changes saved')
                }])
                if (this.widgetContainer.website) {
                    return this.domainChanged((new URL(this.protocol + val)).origin)
                }
                return
            }
            const valid = await this.$refs.laimaForm.$validator.validateAll('step-1')
            if (process.env.NODE_ENV === 'production' && !valid) return
            if (field === 'website') {
                const url = new URL(this.protocol + val)
                const origin = url.origin
                let payload = {
                    domain: origin
                }
                let msg = this.$t('notifs.set_clinic_url', {url: url.href})
                this.$store.dispatch('core/editClinic', [this, this.activeClinic.id, payload, () => {
                    this.notifySuccess(msg)
                }])
                return this.domainChanged(origin)
            }
            if (field === 'procedures') {
                const added = val.filter(i => !cachedValue.includes(i))[0]
                const diff = cachedValue.filter(i => !val.includes(i))[0]
                const lastProcID = added || diff
                const value = !!added
                const prefix = value && 'added' || 'removed'
                let msg = `notifs.${prefix}_widget_proc`
                this.$store.dispatch('core/updateClinicProcedure', [lastProcID, 'in_widget', value, null, this, () => {
                    this.notifySuccess(this.$t(msg, {proc: this.procedures.find(i => i.value === lastProcID).text}))
                }, true])
                return this.domainChanged(origin)
            }
            
        }
    },
    watch: {
        protocol (val, oldval) {
            const domain = this.widgetContainer.website
            if (domain) {
                const oldaddr = oldval + domain
                const newaddr = val + domain
                const msg = this.$t('notifs.changed_clinic_url', {oldaddr, newaddr})
                const payload = {
                    domain: newaddr
                }
                this.$store.dispatch('core/editClinic', [this, this.activeClinic.id, payload, () => {
                    this.notifySuccess(msg)
                }])
                this.domainChanged(newaddr)
            }
        },
        'widgetContainer.use_widget' (val) {
            if (!val) {
                const payload = {
                    domain: null
                }
                this.$delete(this.widgetContainer, 'website')
                this.$store.dispatch('core/editClinic', [this, this.activeClinic.id, payload, () => {
                    this.notifySuccess(this.$t('notifs.widget_off'))
                }])
            }
        }
    }
}
</script>

<style lang="scss">
    @import "@/assets/scss/vuexy/_variables.scss";
    #id-website {
        input, .input-span-placeholder {
            padding-left: 6.9rem !important;
        }
        
    }
    .con-select[name="website"] {
        input {
            background: rgb(248, 248, 248);
            border: none;
            border-right: 1px solid $border-color;
            top: 1px;
            left: 1px;
            height: 40px !important;
            border-radius: 4px 0 0 4px;
        }

    }
    .code-snippet {
        background: #2d2d2d;
        padding: 1em;
        margin: .5em 0;
        text-align: left;
        overflow: auto;
        color: #ccc;
        &>* {
            font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
            font-size: 1em;
            white-space: pre;
            word-spacing: normal;
            word-break: normal;
            word-wrap: normal;
            line-height: 1.5;
            tab-size: 4;
            hyphens: none;
        }
        .token {
            &.tag, &.attr-name, &.namespace, &.deleted {
                color: #e2777a;
            }
            &.string, &.char, &.attr-value, &.regex, &.variable {
                color: #7ec699;
            }
            &.punctuation {
                color: #ccc;
            }

        }
    }
    
</style>