<template>
    <div class="clearfix">
        <h5 class="mb-2">{{ $t('headers.texted_verif_code') }}</h5>
        <div class='vx-col w-full mt-5'>{{ $t('headers.enter_verif_code_cta') }}</div>
        <vac :left-time="60000" @finish="smsPromptExpired = true">
            <div slot="process" slot-scope="{ timeObj }">
                <div class='vx-col w-full mt-5'>
                    <vs-avatar style="left: 50%;font-size: 23px;transform: translateX(-50%);" size="large" color="danger" text-color="danger" :text="`${(timeObj.ceil.s).toString()}s`" />
                </div>
                <masked-input 
                    @click="$ev => $ev.target.select()"
                    type="text"
                    name="pin"
                    class="vs-inputx vs-input--input hasValue text-center font-big"
                    :mask="[/\d/, /\d/, /\d/, /\d/]"
                    :guide="true"
                    autocomplete="off"
                    v-model="smsVerifyCode"
                    placeholder="----"
                    placeholderChar="-">
                </masked-input>
            </div>
            <h3 class="pt-4 text-center text-danger" slot="finish">{{ $t('headers.too_late') }} </h3>
        </vac>
    </div>
</template>

<script>
import vueAwesomeCountdown from 'vue-awesome-countdown'
import Vue from 'vue'
Vue.use(vueAwesomeCountdown, 'vac')

export default {
    data () {
        return {
            smsVerifyCode: null
        }
    },
    methods: {
        async verifySmsCode (val) {
            const resp = await this.$http.post(
                `/api/actor/activate/phone/${val}/`, {}, {
                    headers: { Range: 'get' }
                })
            if (resp.status >= 422) {
                return this.notifyError( this.$t('notifs.verif_code_invalid') )
            }
            this.notifySuccess(this.$t('notifs.verif_code_ok'))
            this.$store.commit('auth/UPDATE_ACTOR_CONTEXT', {
                phone_confirmed: true
            })
            // proceed to login
            this.$router.push('/')
            this.$store.commit('auth/SET_NEEDED_PHONE_VERIF', false)
        }
    },
    watch: {
        async smsVerifyCode(val) {
            if (val && !val.match('-')) {
                await this.verifySmsCode(val)
            }
        }
    }
}
</script>