<template>
    <vs-prompt
        style="z-index: 10000000"
        type="dialog"
        :title="$t('headers.auth_req_cta')"
        :buttonsHidden="true"
        :active="$store.state.auth.resourceLocked">
        <div id="shield-dialog" class="">
            <h5 class="mb-2">{{ message }}</h5>
            <!-- <div class='vx-col w-full mt-5'>{{ secondaryMessage }}</div> -->
            <vac :left-time="180000" @finish="$store.commit('auth/RESOURCE_UNLOCK')">
                <div slot="process" slot-scope="{ timeObj }">
                    <div class='vx-col w-full mt-5'>
                        <vs-avatar style="left: 50%;font-size: 23px;transform: translateX(-50%);" 
                            size="large" color="danger" 
                            text-color="danger" 
                            :text="`${(timeObj.ceil.s).toString()}s`" />
                    </div>
                    
                    <masked-input
                        @click="$ev => $ev.target.select()"
                        style="caret-color: transparent;"
                        autocomplete="off"
                        class="vs-inputx vs-input--input hasValue  font-big"
                        type="text"
                        name="pin"
                        :mask="[/\d/, /\d/, /\d/, /\d/, /\d/, /\d/]"
                        v-model="shieldCode"
                        placeholder="______" >
                    </masked-input>
                </div>
                <h3 class="pt-4 text-center text-danger" slot="finish">{{ $t('headers.too_late') }}</h3>
            </vac>
        </div>
    </vs-prompt>
</template>

<script>
import vueAwesomeCountdown from 'vue-awesome-countdown'

import Vue from 'vue'
Vue.use(vueAwesomeCountdown, 'vac')

export default {
    computed: {
        message () {
            const method = this.$store.getters['auth/shieldMethod']
            const texted_code_msg = this.$t('misc.texted_code_msg')
            const enter_pin_code = this.$t('misc.enter_pin_code') 
            return method && method === 'sms' ? texted_code_msg : enter_pin_code
        },
        requestPayload () {
            return this.$store.state.auth.pendingShieldedRequest
        }
    },

    data () {
        return {
            shieldCode: null
        }
    },

    methods: {
        async resubmitResource () {
            const requestData = Object.assign({}, this.$store.state.auth.pendingShieldedRequest)
            delete requestData.data
            const opts = requestData.opts
            opts.query = {
                shieldcode: this.shieldCode
            }
            const resp = await this.requestResource(requestData.resource, opts)
            if (resp.status === 200) {
                this.notifySuccess(this.$t('misc.auth_ok'))
                this.$store.commit('auth/RESOURCE_UNLOCK')
                this.$store.commit('auth/SET_SHIELDED_RESPONSE', {
                    data: resp.data,
                    source: requestData.resource
                })
            }
            else if (resp.status === 423 && resp.data.cause !== 'Shield token not supplied or invalid') {
                this.notifyError(this.$t('misc.auth_failed'))
                this.$store.commit('auth/RESOURCE_UNLOCK')
            }
        }
    },

    watch: {
        async shieldCode (val) {
            if (val && !val.match('_') && val.length === 6) {
                await this.resubmitResource()
            }
        }
    }
}
</script>

