<template>
	<div v-if="$store.state.browserOK" id="app" :class="vueAppClasses">

		<!-- App locked screen in response to lack of necessary permissions -->
		<app-lock v-if="appLocked"/>

		<!-- Extra auth handler view -->
		<shield-dialog v-if="resourceLocked"/>

		<!-- If user logging in for the first time, ask to fill out basic form -->
		<user-complete-data v-if="userContextMissing"/>

		<!-- If syncing resources with the server -->
		<sync-in-progress v-if="inSync"/>

		<!-- Login page -->
		<login v-if="isAuthed !== null && !isAuthed"/>

		<!-- Screen lock views -->
		<div v-if="showLockWarning" class="veil select-none" style="background: radial-gradient(circle, rgba(255, 255, 255, .2), #fff;">
			<div class="flex content-center flex-wrap justify-center h-full">
				<vs-alert class="expiry-warning text-center justify-center flex" color="warning" active="true" icon-pack="feather" icon="icon-alert-triangle" style=" background: #fff0e5; height: auto">
					<h3 class="text-warning">
						{{ $t('headers.session_expires_in', {seconds: secondsToExpiry}) }}
					</h3>
					<h5 class="mt-2" style="color: #6b6b6b">
						{{ $t('headers.dismissLockWarning') }}
					</h5>
				</vs-alert>
			</div>
		</div>
		<v-idle
			v-if="screenlockSet && loggedIn"
			class="hidden"
			ref="idle"
			:wait="1"
			:duration="$store.state.screenLock.defaultIdleTime"
			:reminders="[15]"
			@remind="showWarningFlag=true"
			@idle="$store.commit('LOCK_SCREEN')"
			:events="idlenessEvents" />
		<lock-screen v-if="showLockScreen"/>

		<!-- Application view wrapper -->
		<router-view v-if="isAuthed" :style="cloakBlur" @setAppClasses="setAppClasses"/>
	</div>

	<!-- Unsupported browser notice -->
	<div class="text-center mt-20" v-else>
		<svg xmlns="http://www.w3.org/2000/svg" class="w-16 h-16 text-danger" fill="none" viewBox="0 0 24 24" stroke="currentColor">
			<path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
		</svg>
		<h2 class="text-center mt-8">
			{{ $t('headers.unsupported_browser') }}
		</h2>
	</div>
</template>

<script>
import auth from "@/auth/authService"
import db from '@/db.js'
import EventBus from '@/EventBus.js'
import SyncManager from '@/core/sync.js'
import AppLock from './components/AppLock.vue'
import LockScreen from './LockScreen.vue'
import ShieldDialog from './components/ShieldDialog.vue'
import UserCompleteData from './components/UserCompleteData.vue'
import SyncInProgress from './components/SyncInProgress.vue'
import Login from '@/views/pages/Login.vue'

const LOCALE = 'pl'

export default {

	components: {
		AppLock,
		LockScreen,
		ShieldDialog,
		SyncInProgress,
		UserCompleteData,
		Login
	},

	data () {
		return {
			db,
			isAuthed: null,
			vueAppClasses: [],
			diff: 1,
			showWarningFlag: false,
			mounted: false,
			idlenessEvents: ['mousemove', 'keydown', 'mousedown', 'touchstart']
		}
	},

	watch: {
		'$store.state.stateLoading' (val) {
			const router_content = document.querySelector('.router-content')
			if (!router_content) return
			if (val) {
				this.$vs.loading({
					container: '.content-wrapper',
					clickEffect: true
				})
				this.$nextTick(() => {
					router_content.classList.add('blur-3')
				})
			}
			this.$vs.loading.close('.content-wrapper > .con-vs-loading')
			router_content.classList.remove('blur-3')
		},
		async appReady (val) {
			if (val) {
				if (!this.$store.getters['auth/getUserContext']) {
					this.$store.commit('auth/USER_MISSING', true)
				}
				else {
					await this.syncManager.setup()
				}
				this.$i18n.locale = LOCALE
				this.$store.dispatch('core/fetchClinics')
				this.$store.dispatch('core/fetchDoctors')
			}
		},
		'$vs.rtl'(val) {
			document.documentElement.setAttribute("dir", val ? "rtl" : "ltr")
		}
	},

	computed: {
		appLocked () {
			return this.$store.state.appLocked
		},
		appReady () {
			const state = this.$store.state.auth
			return !state.actorContextLoading && !state.metaContextLoading && !this.inSync
		},
		cloakBlur () {
			const modalOn = this.userContextMissing || this.showLockScreen || this.resourceLocked || this.appLocked || this.inSync
			return modalOn && {
				filter: 'grayscale(1) blur(5px)'
			} || {}
		},
		inSync () {
			return this.$store.state.inSync
		},
		loggedIn () {
			return !!this.$store.state.auth.spec
		},
		resourceLocked () {
			return this.$store.state.auth.resourceLocked
		},
		screenlockSet () {
			return this.$store.state.screenLock.isSet
		},
		secondsToExpiry () {
			this.mounted
			return this.$refs.idle && this.$refs.idle.seconds || 30
		},
		showLockWarning () {
			return this.screenlockSet && this.loggedIn && !this.showLockScreen && this.showWarningFlag
		},
		showLockScreen () {
			return this.screenlockSet && this.loggedIn && this.$store.state.screenLock.isLocked
		},
		userContextMissing () {
			return this.$store.state.auth.userMissing
		}
	},

	methods: {
		controlLockScreenWarningFlag () {
			this.showWarningFlag = false
		},


		setAppClasses (classesStr) {
			this.vueAppClasses.push(classesStr)
		},

		handleWindowResize () {
			this.$store.commit('UPDATE_WINDOW_WIDTH', window.innerWidth)
			// Set --vh property
			document.documentElement.style.setProperty('--vh', `${window.innerHeight * 0.01}px`);
		},

		handleScroll () {
			// this.$store.commit('UPDATE_WINDOW_SCROLL_Y', window.scrollY)
		},

		login () {
			const toPath = this.$route.path
			EventBus.$emit('logout', {toPath})
			// this.$router.push({
			// 	name: 'page-login',
			// 	query: { to: toPath }
			// })
		}
	},

	async mounted () {
		EventBus.$on('logout', obj => {
			this.isAuthed = false
		})
		EventBus.$on('login', obj => {
			this.isAuthed = true
		})
		const syncManager = new SyncManager(this, db)
		this.syncManager = syncManager
		this.$root.syncManager = syncManager
		if (!syncManager.checkPermissions) {
			this.$store.commit('UNSUPPORTED_BROWSER')
		}
		EventBus.$on('unauthed', () => {
			this.login()
		})

		let vh = window.innerHeight * 0.01;
		document.documentElement.style.setProperty('--vh', `${vh}px`);
		let dir = this.$vs.rtl ? "rtl" : "ltr"
		document.documentElement.setAttribute("dir", dir)

		const authed = auth.isAuthenticated()
		if (!authed) {
			return this.login()
		}
		let metaContext = window.localStorage.getItem('app.laima.metaContext')
		let actorContext = window.localStorage.getItem('app.laima.actorInfo')
		try {
			metaContext = JSON.parse(metaContext)
			actorContext = JSON.parse(actorContext)
		} 
		catch (e) {
			console.error('An error ocurred while trying to parse localstorage context', e)
			return this.login()
		}

		this.$store.dispatch('auth/setActorContext', [actorContext, 'meta-sync', this, metaContext])
		this.$store.commit('UPDATE_WINDOW_WIDTH', window.innerWidth)

		window.addEventListener('resize', this.handleWindowResize)
		window.addEventListener('scroll', this.handleScroll)

		// attach idleness events listeners to switch off showWarningFlag
		this.idlenessEvents.forEach(event => {
			window.addEventListener(event, this.controlLockScreenWarningFlag)
		})

		this.mounted = true
	},

	beforeDestroy () {
		this.idlenessEvents.forEach(event => {
			window.removeEventListener(event, this.controlLockScreenWarningFlag)
		})
	},

	destroyed () {
		window.removeEventListener('resize', this.handleWindowResize)
		window.removeEventListener('scroll', this.handleScroll)
	}
}

</script>

<style lang="scss">

.lm-overlay {
	z-index: 100000;
	width: 100%;
	height: 100%;
	position: fixed;
	top: 0;
	background: radial-gradient(circle, rgba(255, 255, 255, 0.2), #232323);
}
.expiry-warning {
	.vs-alert {
		font-size: 1.6rem !important;
		width: 550px;
	}
	i {
		font-size: 2rem;
	}
}
</style>
