<template>
    <laima-form formScope="step-1"
            ref="laimaform"
            :title="$t('headers.business_hours')"
            :modelsContainer="hoursModel"
            :formFields="formFields">

            <template #before-fields>
                 <vs-alert
                    v-if="lastSlotDate"
                    class="form-alert flex warning pl-4"
                    active="true" icon-pack="feather"
                    icon="icon-alert-triangle">
                    <div class="pl-4">
                        <span class="alert-text w-100 font-normal">
                            {{ $t('headers.bh_changes_warning') }}
                            <span class="text-warning" v-if="$i18n.locale == 'en'">{{ format(lastSlotDate, "iiii', the' do 'of' LLLL Y") }}.</span>
                            <span class="text-warning" v-else>{{ format(lastSlotDate, "ii/LL/Y", {locale: currentLocale}) }}.</span>
                        </span>
                        <span class="alert-text w-100 flex mt-2 font-normal">
                            {{ $t('headers.bh_changes_disclaimer') }}
                        </span>
                    </div>
                </vs-alert>
                <div class="fields-box py-2">
                    <div class="field-wrapper flex items-center">
                        <div class="field-desc relative">
                            <div class="field-label relative pl-2">
                                {{ $t('headers.apply_hrs_to_all') }}
                            </div>
                            <div class="field-help pl-2">
                                {{ $t('headers.apply_the_same_to_all') }}
                            </div>
                        </div>
                        <div class="field-value-wrap ">
                            <div class="field-control items-center">
                                <div class="field-value">
                                    <div class="flex justify-around">
                                        <vs-switch color="success" v-model="applyToAllDays"/>
                                    </div>
                                </div>
                            </div>
                            <div class="field-control items-center mt-8" v-if="applyToAllDays">
                                <div class="field-value">
                                    <time-range
                                        name="business hours"
                                        v-validate="{range: true}"
                                        :errorsArray="$validator.errors.first('business hours', 'step-1')"
                                        v-model="commonHours" />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <vs-alert
                    class="form-alert flex info pl-4"
                    active="true" icon-pack="feather"
                    icon="icon-info">
                    <div class="pl-4">
                        <span class="alert-text font-normal w-100" style="color: #1c658a;">
                            {{ $t('headers.disabled_days_disclaimer') }}
                        </span>
                    </div>
                </vs-alert>


            </template>

            <template #label="{item}">
                <div v-tooltip="{content: isDisabled(item) && $t('misc.include_day_in_all') || $t('misc.remove_day_from_all'), classes: 'text-sm tracking-normal'}" >
                    <input type='hidden' :name="`hidden${item.name}`" v-validate="'included:true,false'"/>
                    <vs-checkbox
                        @input="flipDaySwitch(item)"
                        icon-pack="feather"
                        icon="icon-check"
                        color="primary"
                        :value="!isDisabled(item)" />
                </div>
                <div class="ml-4 flex items-center" :class="isDisabled(item) && 'opacity-25'">{{item.label}}</div>
            </template>

            <template #fieldcontrol="{item}">
                <div class="field-value">
                    <time-range
                        :errorsArray="$validator.errors.first(item.name, 'step-1')"
                        :data-vv-as="item.label || item.name"
                        :disabled="applyToAllDays || item.disabled || false"
                        :name="item.name"
                        v-validate="{range: true}"
                        v-model="hoursModel[item.name]"
                        @input="updatedField"
                    />
                </div>
            </template>
    </laima-form>
</template>

<script>
import TimeRange from '@/components/TimeRange.vue'
import { format } from 'date-fns'
import { pl } from 'date-fns/locale'

const weekIndexes = ["0", "1", "2", "3", "4", "5"]
export default {
    props: ['clinic'],
    components: {
        TimeRange
    },
    watch: {
        async commonHours (val) {
            const isValid = await this.$validator.validate('step-1.business hours')
            if (!isValid) return
            Object.entries(this.hoursModel).forEach((item, index) => {
                if (item[0]) {
                    this.$set(this.hoursModel, index, val)
                    
                }
            })
            await this.updatedField()
        }
    },
    computed: {
        clinicID () {
            return this.$store.state.auth.selectedWorkspace.id
        },
        currentLocale () {
            return this.locales[this.$i18n.locale]
        },
        formFields () {
            return [
                {name: "0", label: this.$t('datetime.mon'), type: 'timerange', disabled: !this.hoursModel["0"][0]},
                {name: "1", label: this.$t('datetime.tue'), type: 'timerange', disabled: !this.hoursModel["1"][0]},
                {name: "2", label: this.$t('datetime.wed'), type: 'timerange', disabled: !this.hoursModel["2"][0]},
                {name: "3", label: this.$t('datetime.thu'), type: 'timerange', disabled: !this.hoursModel["3"][0]},
                {name: "4", label: this.$t('datetime.fri'), type: 'timerange', disabled: !this.hoursModel["4"][0]},
                {name: "5", label: this.$t('datetime.sat'), type: 'timerange', disabled: !this.hoursModel["5"][0]},
            ]
        }
    },

    data () {
        let hoursModel
        if (JSON.stringify(this.clinic.business_hours) === '{}') {
            hoursModel = Object.fromEntries(weekIndexes.map(i => [i, []]))
        }
        else {
            hoursModel = Object.fromEntries(weekIndexes.map(index => {
                return [index, this.clinic.business_hours[index] || []]
            }))
        }
        return {
            format,
            hoursModel,
            locales: {
                pl
            },
            applyToAllDays: false,
            lastSlotDate: null,
            commonHours: ['08:00', '17:00']
        }
    },
    
    async mounted () {
        window.bbb = this
        // find when the last slot is
        const resp = await this.requestResource('list_scheduleslot', {
            data: {},
            headers: {Overwrite: this.$store.state.auth.selectedWorkspace.id},
            query: {
                order_by: 'daily_range',
                limit: 1,
                order_dir: 'desc',
                select: 'daily_range'
            }
        })
        if (resp.status === 200) {
            this.lastSlotDate = new Date(resp.data.data[0].daily_range[0])
        }
    },

    methods: {
        isDisabled (item) {
            return !this.hoursModel[item.name][0]
        },

        async updatedField () {
            const isValid = await this.$validator.validateAll('step-1')
            if (!isValid) return

            const payload = {
                business_hours: this.$refs.laimaform.localModels
            }

            const clinicUpdateResponse = await this.requestResource(
                'put_clinic',
                {
                    vm: this,
                    data: {
                        select: {
                            id: this.clinicID
                        },
                        payload
                    },
                    headers: {Overwrite: this.clinicID},
                }
            )
            if (clinicUpdateResponse.status === 200) {
                this.$store.commit('core/PATCH_CLINIC', {
                    clinicID: this.clinicID,
                    payload
                })
                this.notifySuccess(this.$t('notifs.changed_bh'))
            }
        },

        async flipDaySwitch (item) {
            if (!this.hoursModel[item.name][0]) {
                this.$set(this.hoursModel, item.name, ['08:00', '17:00'])
                await this.updatedField()
            }
            else {
                this.$set(this.hoursModel, item.name, [])
                await this.updatedField()
            }
        }
    }
}
</script>

