<template>
	<div ref="coninput"
		:style="styleLabel"
		:class="[`vs-input-${color}`,{
			'isFocus':isFocus
		}]"
		class="vs-component vs-con-input-label vs-input">
		<div :id="`id-${name}`" :class="{'lm-input-group': isCompound, 'vs-con-input': !isCompound, danger}">
			<div class="icon-before flex" v-if="iconBefore">
				<feather-icon class="" svgClasses="h-5 w-5" :icon="iconBefore" />
			</div>
			<label :class="hasIcon" class="before-input-label flex" v-if="$slots.labelBefore">
				<slot name="labelBefore">
				</slot>
			</label>
			<input
				ref="vsinput"
				:style="style"
				:autofocus="autofocus"
				:disabled="disabled"
				:class="[size, labelTrans, hasLeftIcon, hasRightIcon, { 'hasValue':value !== '' }]"
				:placeholder="null"
				:value="value"
				v-bind="$attrs"
				:type="$attrs.type?$attrs.type:'text'"
				class="vs-inputx vs-input--input"
				v-on="listeners">
			<label class="after-input-label" v-if="$slots.labelAfter">
				<slot name="labelAfter">
				</slot>
			</label>
			<transition name="placeholderx">
				<span
					v-if="!value && $attrs.placeholder"
					ref="spanplaceholder"
					:style="styleLabel"
					:class="[size]"
					class="input-span-placeholder vs-input--placeholder"
					@click="focusInput">
					{{ $attrs.placeholder }}
				</span>
			</transition>
		</div>


	</div>
</template>

<script>
import _color from '@/assets/utils/color.js'

export default {
	name:'VsInput',
	inheritAttrs: false,
	props:{
		name: {
			default: ''
		},
		value:{
			default:'',
			type:[String,Number]
		},
		autofocus:{
			default:false,
			type:[Boolean,String]
		},
		disabled: {
			default: false,
			type: Boolean
		},
		loading: {
			default: false,
			type: Boolean
		},
		color: {
			default:'primary',
			type:String
		},
		success:{
			default:false,
			type:Boolean
		},
		danger:{
			default:false,
			type:Boolean
		},
		warning:{
			default:false,
			type:Boolean
		},
		size:{
			default:'normal',
			type:String
		},
		iconBefore: {
			default: '',
			type: String
		},
		iconAfter: {
			default: '',
			type: String
		}
	},
	inject: {
		elForm: {
			default: ''
		},
		elFormItem: {
			default: ''
		}
	},
	data:()=>({
		isFocus:false
	}),
	computed:{
		hasLeftIcon () {
			return this.iconBefore && 'hasLeftIcon'
		},
		hasRightIcon () {
			return this.iconAfter && 'hasRightIcon'
		},
		hasIcon () {
			return (this.iconBefore || this.iconAfter) && 'hasIcon'
		},
		hasLeftLabel () {
			return this.$slots.labelBefore
		},
		hasRightLabel () {
			return this.$slots.labelAfter
		},
		isCompound () {
			return this.hasLeftLabel || this.hasRightLabel || this.iconBefore || this.iconAfter
		},
		labelTrans () {
			if (this.$slots.labelBefore) return 'has-label-before w-auto'
			if (this.$slots.labelAfter) return 'has-label-after w-auto'
		},
		style (){
			return {
				border: `1px solid ${this.isFocus?_color.getColor(this.color,1):'rgba(0, 0, 0,.2)'}`,
				'border-left': (this.hasLeftIcon && this.hasLeftLabel) && 'none !important',
				'border-right': (this.hasRightIcon && this.hasRightLabel) && 'none !important',
			}
		},
		styleLabel(){
			return {
				color: this.isFocus?_color.getColor(this.color,1):null,
			}
		},
		listeners(){
			return {
				...this.$listeners,
				input: (evt) => {
					this.$emit('input',evt.target.value)
				},
				focus: (evt) => {
					this.$emit('focus',evt)
					this.changeFocus(true)
				},
				blur: (evt) => {
					this.$emit('blur',evt)
					this.changeFocus(false)
				}
			}
		},
		isValue(){
			return this.value
		}
	},
	methods:{
		// animation
		changeFocus(booleanx) {
			this.isFocus = booleanx
		},
		beforeEnter(el) {
			el.style.height = 0
		},
		enter(el, done){
			let h = el.scrollHeight
			el.style.height = h + 'px'
			done()
		},
		leave: function (el) {
			el.style.height = 0 + 'px'
		},
		focusInput(){
			this.$refs.vsinput.focus()
		}
	},
	watch: {
		loading (val) {
			if (val) {
				this.$vs.loading({
					background: 'transparent',
					container: `#id-${this.name}`,
					scale: 0.45
				})
			}
			else {
				this.$vs.loading.close(`#id-${this.name} > .con-vs-loading`)
			}
		}
	}
}
</script>

<style lang="scss">
@import "@/assets/scss/vuexy/_variables.scss";
.danger {
	.vs-inputx {
		border: 1px solid rgba(var(--vs-danger),1)!important;
		
	}
}
.lm-input-group {
	display: inline-flex;
	box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .05) !important;
	border-radius: 5px;
	&.danger {
		border: 1px solid rgba(var(--vs-danger),1)!important;
		background: darken($primary, 5);
		input {
			border: none !important;
			border-top-right-radius: 4px;
			border-bottom-right-radius: 4px;
		}
	}
	.vs-inputx {
		box-shadow: none !important;
	}
	.vs-con-input {
		flex-direction: row !important;
		justify-content: start !important;
	}
	.icon-before, .icon-after {
		background: darken($primary, 5);
		color: #fff;
		padding: .7rem;
	}
	.icon-before {
		border-top-left-radius: inherit;
		border-bottom-left-radius: inherit;
	}
	.icon-after {
		border-top-right-radius: inherit;
		border-bottom-right-radius: inherit;
	}
	.has-label-before {
		border-top-left-radius: 0;
		border-bottom-left-radius: 0;
		border-left: none !important;
	}
	.has-label-after {
		border-top-right-radius: 0;
		border-bottom-right-radius: 0;
		border-right: none !important;
	}
	.before-input-label {
		&:not(.hasIcon) {
			border-top-left-radius: inherit;
			border-bottom-left-radius: inherit;
		}
		height: 100%;
		padding: .7rem;
		color: #fff;
		background: $primary;
		border: 2px solid transparent;
	}
	.after-input-label {
		border-top-right-radius: inherit;
		border-bottom-right-radius: inherit;
		height: 100%;
		padding: .7rem ;
		color: #fff;
		background: $primary;
		border: 2px solid transparent;
	}
}
</style>