export default {
    addOutliers: vm => {
        const isWeekly = vm.currentView === 'week'
        const scheduleLims = vm.$store.state.core._currentScheduleLims
        const selectedDoctorID = vm.selectedDoctor.actor
        if (!scheduleLims) return []
        const docs = isWeekly ? [selectedDoctorID] : Object.values(vm.docs).map(i => i.actor)

        if (!docs) return []
        
        return docs.flatMap(doctorID => {
            let scheduleDurations = vm.$store.getters['core/scheduleDurationsPerDoctor'][doctorID]
            let [weeklyMinHour, weeklyMinMinutes, weeklyMaxHours, weeklyMaxMinutes] = scheduleLims[doctorID].flatMap(i => i.split(':'))

            if (!isWeekly) {
                const currentDate = vm.currentDate.format('DD/MM')
                scheduleDurations = scheduleDurations.filter(([scheduleID, [localStart, localEnd]]) => {
                    return localStart.format('DD/MM') == currentDate
                });
                // adjust scheduleLims' min and max bounds
                [weeklyMinHour, weeklyMinMinutes] = Object.values(scheduleLims).map(i => i[0]).sort()[0].split(':');
                [weeklyMaxHours, weeklyMaxMinutes] = Object.values(scheduleLims).map(i => i[1]).sort().reverse()[0].split(':')
            }

            return scheduleDurations.flatMap(([scheduleID, [localStart, localEnd]]) => {
                let morningStart = localStart.clone()
                let eveningEnd = localEnd.clone()
                let split = doctorID
                morningStart.setHours(weeklyMinHour)
                morningStart.setMinutes(weeklyMinMinutes)
                eveningEnd.setHours(weeklyMaxHours)
                eveningEnd.setMinutes(weeklyMaxMinutes)
                const morningSlot = {
                    split,
                    scheduleID,
                    background: true,
                    class: 'closed-morning',
                    start: morningStart,
                    end: localStart
                }
                const eveningSlot = {
                    split,
                    scheduleID,
                    background: true,
                    class: 'closed-evening',
                    start: localEnd,
                    end: eveningEnd
                }
                return [
                    morningSlot,
                    eveningSlot
                ]
            })

        })
    }
}