import auth from "@/auth/authService"
import db from '@/db.js'
import hermes from "@/hermes.js"
import http from "@/axios.js"
import router from '@/router'
import Vue from 'vue'
import EventBus from '@/EventBus.js'

const sleep = hermes.methods.sleep

export default {
    async login({ commit, state, dispatch }, [payload, vm]) {
        commit('STATE_LOADING_ON', {}, { root: true })

        // If user is already logged in notify and exit
        // if (await state.isUserLoggedIn()) {
        //     payload.notifyInfo(vm.$t('notifs.already_logged'))
        //     commit('STATE_LOADING_OFF', {}, { root: true })
        //     return false
        // }
        
        const resp = await http.post(
            '/api/actor/login/', 
            JSON.stringify(payload.userDetails), 
            {headers: {Range: 'post'}}
        )
        const workspaces = resp.data.workspaces
        let err
        commit('STATE_LOADING_OFF', {}, { root: true })
        if (resp.status === 409) {
            // conflict, more than 1 workspaces for the logged in actor
            commit('SET_AVAILABLE_WORKSPACES', workspaces)
            return payload.slider.slideNext()
        }
        else if (resp.status === 422) {
            // validation error (i.e. requested workspace doesn't belong to the requesting actor)
            err = resp.data
        }
        else if (resp.status === 403) {
            // invalid login/password/salt
            err = resp.data
        }

        if (err) {
            commit('STATE_LOADING_OFF', {}, { root: true })
            payload.notifyError(err)
            if (state.availableWorkspaces.length > 1) {
                // workspace chosen, but wrong credentials
                await sleep(1.5)
                location.reload()
            }
            return
        }
        dispatch('setActorContext', [resp.data, 'full-meta', vm])

        dispatch('setupScreenLock', {}, {root: true})
        commit('STATE_LOADING_OFF', {}, { root: true })
        EventBus.$emit('login', {})
        // let goTo = router.currentRoute.query.to
        // if (goTo.match('login')) {
        //     goTo = '/'
        // }
        // router.push(goTo || '/')
    },

    updateUserContext({commit}, payload) {
        const userContext = JSON.parse(localStorage.getItem('app.laima.metaContext'))
        userContext.user = payload
        localStorage.setItem('app.laima.metaContext', JSON.stringify(userContext))
        commit('SET_USER_CONTEXT', payload)
    },

    async setMetaContext ({commit, dispatch}, [metaPayload, vm]) {
        // payload is meant to be either a direct copy of meta response of the localstorage cache of it
        const { user, roles, scopes, state, currency, langs, spec_hashsum } = metaPayload
        let spec
        if (metaPayload.spec) {
            spec = metaPayload.spec
            localStorage.setItem('app.laima.specHashsum', spec_hashsum)
            localStorage.setItem('app.laima.spec', JSON.stringify(spec))
        }
        else {
            try {
                spec = JSON.parse(localStorage.getItem('app.laima.spec'))
            }
            catch (e) {
                return auth.logOut({
                    reason: 'Relogging...'
                })
            }
        }
        
        dispatch('setAPISpec', {
            spec,
            hashsum: spec_hashsum
        })
        commit('SET_USER_CONTEXT', user)
        commit('SAVE_SCOPES', scopes)
        commit('SAVE_ROLES', roles)
        commit('SAVE_LOCALE', {
            langs, state, currency
        })
        commit('SET_META_LOADING', false)

        if (metaPayload.full_sync) {
            dispatch('fullSync', {vm, toSync: metaPayload.full_sync}, {root: true})
        }

        if (metaPayload.partial_sync) {
            for (let payload of metaPayload.partial_sync) {
                try {
                    payload = JSON.parse(payload)
                    await vm.syncManager.processPartialUpdate(payload)
                }
                catch (e) {
                    console.error(e)
                }
            }
        }
    },
    
    async setActorContext({commit, dispatch}, [payload, mode, vm, metaContext]) {
        localStorage.setItem('app.laima.actorInfo', JSON.stringify(payload))
        const active_workspace_id = payload.active_workspace
        commit('SET_ACTOR_CONTEXT', payload)
        commit('SET_AVAILABLE_WORKSPACES', payload.workspaces)
        commit('SET_CURRENT_WORKSPACE', {
            id: active_workspace_id,
            name: payload.workspaces[active_workspace_id]
        })
        commit('SET_ACTOR_LOADING', false)

        if (mode === 'no-meta') return

        const local_spec_hashsum = localStorage.getItem('app.laima.specHashsum')
        const syncColl = await db.sync.toArray()
        let params = [
            `spec_hashsum=${local_spec_hashsum || 'unset'}`
        ]

        // If this called by App's mounted, only get the sync status
        if (mode === 'meta-sync') {
            params.push(`mode=${'sync-only'}`)
        }

        // params = Object.entries(params).map()
        if (syncColl[0]) {
            let tables = await db.sync.toArray()
            tables = tables.map(({table_name,hashsum,timestamp}) => `${table_name}=${hashsum},${timestamp/1000}`)
            params.push(...tables)
            const deviceID = vm.syncManager.fingerprint
            params.push(`device=${deviceID}`)
        }

        const metadoc_resp = await http.get('/api/doc/meta/?' + params.join('&'))
        const status = metadoc_resp.status
        let metaRespData = metadoc_resp.data
        if (status === 200 && !metaRespData.authed) {
            return auth.logOut({
                reason: 'Relogging...'
            })
        }
        if (![200, 204, 416].includes(status) ) {
            // an error
            return
        }
        if (status === 416) {
            // Signal to sync some, or all resources
            if (metaContext) {
                metaContext = Object.assign(metaContext, metaRespData)
                metaRespData = metaContext
            }
        }
        else if (status === 204 && metaContext) {
            // No resources to resync
            metaRespData = metaContext
        }
        EventBus.$emit('login', {})
        
        // If this is called by Login, then follow up
        const existingContext = JSON.parse(localStorage.getItem('app.laima.metaContext') || "{}")
        Object.assign(existingContext, metaRespData)
        const toSave = Object.assign({}, existingContext)
        delete toSave.full_sync
        delete toSave.partial_sync
        localStorage.setItem('app.laima.metaContext', JSON.stringify(toSave))
        dispatch('setMetaContext', [existingContext, vm])
    },

    setAPISpec({commit}, {hashsum, spec}) {
        commit('SET_CURRENT_SPEC_HASHSUM', hashsum)
        commit('SET_CURRENT_SPEC', spec)
    },

    async registerUser({commit}, [payload, vm]) {
        commit('STATE_LOADING_ON', {}, { root: true })
        const url = !payload.isInvited ? '/api/actor/' : `/api/actor/?inv=${payload.isInvited}`
        const resp = await http.post(
            url,
            JSON.stringify(payload.userDetails),
            {headers: {Range: 'post'}}
        )
        commit('STATE_LOADING_OFF', {}, { root: true })
        // we expect this to return 204, account creation pending

        let err
        if (resp.status === 422) {
            // validation error (i.e. requested workspace doesn't belong to the requesting actor)
            err = resp.data
            return Object.entries(err).forEach(item => {
                let [keyname, errs] = item
                errs.forEach(errmsg => {
                    payload.errors.add({
                        field: keyname,
                        msg: errmsg
                    })
                })
            })
            
        }
        else if (resp.status === 409) {
            err = resp.data
            router.push('/')
        }
        else if (resp.status !== 200) {
            err = resp.data
        }

        if (err) {
            return vm.notifyError(err)
        }

        if (resp.status === 200) {
            // exclusively invited user OK status. Redirect to login
            vm.notifySuccess(vm.$t('notifs.registered_text'))
            commit('EMAIL_VERIFICATION_SENT', payload.userDetails)
            await sleep(1.5)
            if (resp.data.needs_phone_ver) {
                // can't login yet, verify phone no first
                const phoneVerifResp = await http.post(
                    '/api/actor/activate/phone/send/',
                    JSON.stringify({phone: payload.userDetails.phone}),
                    { headers: { Range: 'post' } }
                )
                if (phoneVerifResp.status >= 400) {
                    return vm.notifyError('Apologies, we could not complete this operation. Contact our Technical Support of try again later.')
                }
                return commit('SET_NEEDED_PHONE_VERIF', true)
            }
            router.push('/')
        }

    },

    changeWorkspace ({state, commit}, workspace) {
        commit('SET_CURRENT_WORKSPACE', workspace)
        Vue.set(state.actorContext, 'active_workspace', workspace.id)
        localStorage.setItem('app.laima.actorInfo', JSON.stringify(state.actorContext))
        window.location.reload()
    }
}
