<template>
<div id="user-edit-tab-info">
    <vs-prompt :title="$t('headers.enter_verif_code')" :buttonsHidden="true" :type="smsPromptExpired ? 'alert' : 'dialog'" class="prompt-no-cancel" :active.sync="phoneVerifPrompt">
        <div class="">
            <h5 class="mb-2">{{ $t('headers.texted_verif_code') }}</h5>
            <div class='vx-col w-full mt-5'>{{ $t('headers.enter_verif_code_cta') }}</div>
            <vac :left-time="60000" @finish="smsPromptExpired = true">
                <div slot="process" slot-scope="{ timeObj }">
                    <div class='vx-col w-full mt-5'>
                        <vs-avatar style="left: 50%;font-size: 23px;transform: translateX(-50%);" size="large" color="danger" text-color="danger" :text="`${(timeObj.ceil.s).toString()}s`" />
                    </div>
                    <masked-input 
                        @click="$ev => $ev.target.select()"
                        type="text"
                        name="pin"
                        class="vs-inputx vs-input--input hasValue text-center font-big"
                        :mask="[/\d/, /\d/, /\d/, /\d/]"
                        :guide="true"
                        autocomplete="off"
                        v-model="smsVerifyCode"
                        placeholder="----"
                        placeholderChar="-">
                    </masked-input>
                </div>
                <h3 class="pt-4 text-center text-danger" slot="finish">{{ $t('headers.too_late') }} </h3>
            </vac>
        </div>
    </vs-prompt>
    <div class="inline-flex w-full">
        <!-- <div class="vx-card shadow"> -->
            <photo-uploader class="mr-12"/>
        <!-- </div> -->
        <laima-form :title="$t('headers.personal_info')"
                    ref="laima"
                    style="max-height: 194px;"
                    @updated="onUpdatedPersonal"
                    :cleanForm="cleanPersonalPayload"
                    :onSubmit="!isProfileComplete && savePersonalData"
                    :formFields="personalInfoFields"
                    :modelsContainer="$store.getters['auth/getUserContext'] || {}">
        </laima-form>
    </div>

    <laima-form :title="$t('fields.account')"
                class="mt-8"
                formScope="step-2"
                @updated="onUpdatedAccount"
                :modelsContainer="accountContext"
                :formFields="accountFields">

        <template #beforefield="{item}">
            <vs-alert class="form-alert flex primary" v-if="!emailConfirmed && item.name === 'email'"
                        active="true" icon-pack="feather"
                        icon="icon-info">
                <span class="alert-text font-normal">
                    {{ $t('headers.verify_email_cta') }}
                </span>
            </vs-alert>
            <vs-alert class="form-alert flex warning" v-else-if="!phoneConfirmed && item.name === 'phone'"
                        active="true" icon-pack="feather"
                        icon="icon-alert-triangle">
                <div class="flex items-center">
                    <span class="alert-text font-normal">
                       {{ $t('headers.phone_not_verif') }}
                       <b class="font-normal hover:underline cursor-pointer">
                            <a @click="manageSmsPrompt">
                               {{ $t('headers.click_to_verif') }} 
                            </a>
                        </b>
                    </span>
                </div>
            </vs-alert>
        </template> 

    </laima-form>

    

</div>

</template>



<script>
import CountryFlag from 'vue-country-flag'
// import Datepicker from 'vuejs-datepicker'
import vueAwesomeCountdown from 'vue-awesome-countdown'
import PhotoUploader from '@/components/PhotoUploader.vue'
import Vue from 'vue'
Vue.use(vueAwesomeCountdown, 'vac')

export default {

    components: {
		CountryFlag,
		PhotoUploader
    },

    data () {
        return {
            // personalInfoContext: {},
            accountContext: {},
            datePickerActivated: false,
            fullnameRaw: '',
            phoneVerifPrompt: null,
            smsPromptExpired: false,
            smsVerifyCode: null,
		}
	},

    computed: {
        emailConfirmed () {
            return this.$store.state.auth.actorContext.email_confirmed  
        },
        phoneConfirmed () {
            return this.$store.state.auth.actorContext.phone_confirmed  
        },
        actorContext() {
            return this.$store.state.auth.actorContext
        },
        personalInfoFields () {
            return [
                {
                    name: 'fullname',
                    type: 'text',
                    validators: {required: true},
                    helpText: this.$t('help_texts.fullname'),
                    label: this.$t('fields.name'),
                    placeholder: this.$t('placeholders.fullname')
                },
                {
                    name: 'dob',
                    type: 'date',
                    validators: {required: true},
                    label: this.$t('fields.dob')
                }
            ]
        },
        accountFields () {
            return [
                { 
                    name: 'username',
                    type: 'text',
                    validators: {required: true},
                    label: 'Username'
                },
                {
                    name: 'email',
                    type: 'email',
                    validators: {required: true},
                    label: this.$t('fields.email')
                },
                {
                    name: 'phone',
                    type: 'tel',
                    validators: {required: true},
                    helpText: this.$t('help_texts.phone_why'),
                    label: this.$t('fields.phone')
                }
            ]
        },

        isProfileComplete () {
            return !!this.$store.getters["auth/getUserContext"]
        },
    },

    watch: {
        datePickerActivated (val) {
            const el1 = document.querySelector('.con-vs-tabs')
            const el2 = document.querySelector('.con-slot-tabs')
            if (val) {
                el1.style.overflow = 'visible'
                el2.style.overflow = 'visible'
            }
            else {
                el1.style.overflow = ''
                el2.style.overflow = ''
            }
        },
        async smsVerifyCode(val) {
            if (val && !val.match('-')) {
                await this.verifySmsCode(val)
            }
        }
    },

    methods: {
        async onUpdatedAccount ([payload, callback]) {
            const actor = this.$store.state.auth.actorContext.id
            const data = {
                select: {
                    id: actor
                },
                payload: this.cleanEmptyData(payload),
            }

            const resp = await this.requestResource('patch_principalactor', {
                data,
                vm: this,
                scope: 'step-2'
            })

            if (resp.status === 200) {
                if (payload.phone) {
                    payload.phone_confirmed = false
                    this.accountContext.phone_confirmed = false
                }
                if (payload.email) {
                    payload.email_confirmed = false
                    this.accountContext.email_confirmed = false
                }
                callback()
                this.$store.commit('auth/UPDATE_ACTOR_CONTEXT', payload)
            }
        },

        async onUpdatedPersonal ([data, callback]) {
            const actor = this.$store.state.auth.actorContext.id
            data = this.cleanEmptyData(data)
            let payload = {
                select: {actor},
                payload: data
            }
            const patch_resp = await this.requestResource('patch_user', {vm: this, data: payload})
            if (patch_resp.status === 200) {
                this.$store.commit("auth/UPDATE_USER_CONTEXT", data)
                callback()
                return patch_resp
            }
        },

        cleanPersonalPayload (payload) {
            if (payload.fullname) {
                const [name, last_name] = payload.fullname.split(' ')
                if (!last_name) {
                    throw new this.ValidationError(
                        'fullname',
                        'Last name is required'
                    )
                }
                payload = Object.assign(payload, {name, last_name})
                delete payload.fullname
            }
            if (payload.dob) {
                payload.dob = new Date(payload.dob).toISOString().split('T')[0]
            }
            return payload
        },

        async savePersonalData (data) {
            const post_resp = await this.requestResource('post_user', {
                data,
                vm: this,
                scope: 'step-1',
                similarErrMsgMap: {
                    fullname: {
                        '.*?already exists': this.$t('notifs.user_exists')
                    }
                },
                errRespMap: {
                    actor: 'fullname'
                }
            })
            if (post_resp.status === 200) {
                this.$store.dispatch("auth/updateUserContext", data)
                this.notifySuccess(this.$t('notifs.saved_user_details'))
            }
        },

        async manageSmsPrompt () {
            this.smsVerifyCode = null
            this.$store.commit('STATE_LOADING_ON', {})
            const resp = await this.$http.post(
                '/api/actor/activate/phone/send/',
                JSON.stringify({
                    phone: this.accountContext.phone
                }), {
                    headers: {
                        Range: 'post'
                    }
                })
            this.$store.commit('STATE_LOADING_OFF', {})

            if (resp.status >= 400) {
                console.error(resp.data)
                return this.notifyError(
                    this.$t('notifs.failed_send_verif')
                )
            }
            this.phoneVerifPrompt = true

        },

        async verifySmsCode (val) {
            const resp = await this.$http.post(
                `/api/actor/activate/phone/${val}/`, {}, {
                    headers: {
                        Range: 'get'
                    }
                })
            if (resp.status >= 422) {
                this.notifyError(
                    this.$t('notifs.verif_code_invalid')
                )
                this.phoneVerifPrompt = false
                return
            }
            this.notifySuccess(this.$t('notifs.verif_code_ok'))
            this.phoneVerifPrompt = false
            this.accountContext.phone_confirmed = true
            this.$store.commit('auth/UPDATE_ACTOR_CONTEXT', {
                phone_confirmed: true
            })
        },

    },


    mounted () {
        window.ll = this
        // account context
        const accountContext = Object.fromEntries(
            Object.keys(this.spec.get_principalactor.schema).map(item => {
                const defvalue = this.actorContext[item] || null
                return [item, defvalue]
            })
        )
        this.accountContext = Object.assign({}, accountContext)

    }

} 

</script>


<style lang="scss">
#user-edit-tab-info {
    max-width: 1300px;
}
.has-errors {
    .vs__dropdown-toggle {
        border: 1px solid rgba(var(--vs-danger), 1) !important;
    }
}

.verif-note {
    bottom: 5px;
    left: 5px;
    position: relative;
}

.prompt-no-cancel {
    .vs-icon {
        display: none;
    }

    font-size: 26px !important;

    input {
        max-width: 143px;
        font-size: 26px !important;
        color: black;
        left: 50%;
        transform: translateX(-50%);
        margin-top: 19px;
        letter-spacing: 14px;
        text-indent: 12px;
    }
}

.con-vs-avatar.large {
    background: white;
    color: rgba(var(--vs-danger), 1) !important;
    border: 2px solid;
}

</style>
