<template>
  <div>
        <vs-prompt
            :title="currentUser.name"
            :buttonsHidden="true"
            :active.sync="userDetailsPrompt">
            <div>
				<div class="vx-row mb-3">
					<div class="vx-col w-full">
						<vs-input disabled class="w-full" icon-pack="feather" icon="icon-user" icon-no-border :value="currentUser.name + ' ' + currentUser.last_name"/>
					</div>
				</div>
				<div class="vx-row mb-3">
					<div class="vx-col w-full">
						<vs-input disabled type="text" class="w-full" icon-pack="feather" icon="icon-home" icon-no-border :value="currentUser.address1"/>
					</div>
				</div>
				<div class="vx-row mb-3">
					<div class="vx-col w-full">
						<vs-input disabled class="w-full" icon-pack="feather" icon="icon-map" icon-no-border :value="currentUser.city"/>
					</div>
				</div>
				<vs-row vs-align="flex-start" vs-type="flex" vs-justify="center" vs-w="12">
					<vs-col vs-align="center" vs-justify="center" vs-type="flex" vs-w="2">
						<vs-button type="relief" icon-pack="feather" icon="icon-mail"></vs-button>
					</vs-col>
					<vs-col vs-align="center" vs-justify="center" vs-type="flex" vs-w="2">
						<vs-button color="dark" type="relief" icon-pack="feather" icon="icon-message-square"></vs-button>
					</vs-col>
				</vs-row>
            </div>
        </vs-prompt>

    <vx-card class="better-table">
		<div class="mb-8">
			<vs-select width="300px" label="Clinic" class="mb-4" v-model="workspaceSelected">
				<vs-select-item :key="index" :value="item.id" :text="item.name" v-for="(item,index) in $store.state.auth.availableWorkspaces" />
			</vs-select>
		</div>
		<div class="flex flex-wrap justify-between items-center" >
			<div class="table-wrap">
				<vue-good-table ref='table'
					@on-page-change="onPageChange"
					@on-per-page-change="onPerPageChange"
					@on-column-filter="onColumnFilter"
					@on-sort-change="onSortChange"
					@on-selected-rows-change="selectionChanged"
					@on-row-click="onRowClicked"
					mode="remote"
					styleClass="vgt-table condensed"
					:columns="columns"
					:key="key"
					:totalRecords="totalRecords"
					:totalRows="totalRecords"
					:pagination-options="paginationOptions"
					:rows="rows">
					<template slot="table-row" slot-scope="props">
						<span v-if="roles.includes(props.column.field)" class="role-flag">
							<vs-checkbox 
								@input="roleChanged(props)"
								color="success" 
								:value="rows[props.index][props.column.field]"
							> </vs-checkbox>
						</span>
						<span v-else>
							{{props.formattedRow[props.column.field]}}
						</span>
					</template>
				</vue-good-table>
			</div>
		</div>
    </vx-card>
  </div>
</template>

<script>
import { VueGoodTable } from 'vue-good-table';
import 'vue-good-table/dist/vue-good-table.css'

export default {
	components: {
		VueGoodTable
	},

	data () {
		return {
			bool: [],
			totalRecords: 0,
			localWorkspaceSelected: null,
			rows: [],
			page: 1, 
			perPage: 8,
			order_by: ['email'],
			order_dir: 'asc',
			filter: {},
			userDetailsPrompt: false,
			key: 0,
			currentUser: {name: 'df'},
			
		}
	},

	computed: {

		columnDefsBase () {
			const usernamePlaceholder = this.$store.state.auth.actorContext.username
			const scopes = this.$store.state.auth.scopes
			return [
				{
					field: 'id',
					hidden: true
				},
				{
					label: this.$t('fields.email'),
					field: 'email',
					sortable: false,
					filterOptions: {
						enabled: true,
						placeholder: 'user@example.com',
					}
				},
				{
					label: this.$t('fields.username'),
					field: 'username',
					filterOptions: {
						enabled: true,
						placeholder: usernamePlaceholder,
					}
				},
				{
					label: this.$t('fields.phone'),
					field: 'phone',
					width: '200px',
					sortable: false,
					filterOptions: {
						enabled: true,
						placeholder: '+48123456789',
					}
				},
				{
					label: this.$t('fields.scope'),
					field: 'scope',
					width: '150px',
					filterOptions: {
						enabled: true,
						filterDropdownItems: scopes,
						placeholder: this.$t('misc.show_all')
					}
				}
			]
		},

		workspaceSelected: {
			get () {
				return this.localWorkspaceSelected || this.$store.state.auth.selectedWorkspace.id
			},
			async set (val) {
				this.localWorkspaceSelected = val
				await this.fetchRows()
			}
		},

		roles () {
			return this.$store.state.auth.roles
		},

		columns () {
			const roles = []
			this.roles.forEach(role => {
				roles.push({
					label: role[0].toUpperCase() + role.slice(1),
					field: role,
					type: 'boolean',
					sortable: false
				})
			})
			roles.sort((a, b) => {
				if (a.field < b.field) {
					return -1
				}
				if (a.field > b.field) {
					return 1
				}
				return 0
			})
			return [...this.columnDefsBase, ...roles]
		},
		paginationOptions () {
			return {
				perPage: this.perPage,
				enabled: true,
			}
		}
	},

	methods: {
		async onPageChange (params) {
			this.page = params.currentPage
			await this.fetchRows()
		},

		async onPerPageChange (params) {
			this.perPage = params.currentPerPage
			await this.fetchRows()
		},

		async onColumnFilter (params) {
			this.filter = Object.fromEntries(
				Object.entries(params.columnFilters).filter(([col, val]) => {
					return !!val
				})
			)
			await this.fetchRows()
		},

		async onSortChange (params) {
			this.order_dir = params[0].type
			this.order_by = [params[0].field]
			await this.fetchRows()
		},

		async onRowClicked (params) {
			if (params.event.target.type === 'checkbox') return
			const data = {actor: params.row.id}
			const resp = await this.requestResource('get_user', {data, vm: this})
			if (resp.status === 200) {
				this.currentUser = Object.assign({}, this.currentUser, resp.data)
				this.userDetailsPrompt = true
			}
			// TODO: remove from workspace
		},

		loadingOn () {
			this.$vs.loading({
				container: '.vgt-wrap',
				type: 'default'
			})
		},

		loadingOff () {
			this.$vs.loading.close('.vgt-wrap > .con-vs-loading')
		},

		selectionChanged (params) {
			for (let item of this.$refs.table.$refs.table.rows) {
				item.classList.remove('highlighted')
			}
			const indices = params.selectedRows.map(item => item.originalIndex)
			indices.forEach(index => {
				const $el = this.$refs.table.$refs.table.rows[index + 2]
				$el.classList.add('highlighted')
			})
		},

		async roleChanged (props) {
			const roles = this.$store.state.auth.roles
			const affectedRow = this.rows[props.index]
			affectedRow[props.column.field] ^= true
			this.$set(this.rows, props.index, affectedRow)

			const format = {
				actor_id: affectedRow.id
			}

			const data = {
				roles: roles.filter(item => !!affectedRow[item])
			}

			const resp = await this.requestResource('post_grantRole', {format, data, vm: this, no_loading: true})
			if (resp.status === 200) {
				this.notifySuccess(this.$t('notifs.changes_saved'))
			}
			else {
				affectedRow[props.column.field] = false
				this.$set(this.rows, props.index, affectedRow)
				this.key += 1
			}
		},
		
		async fetchRows () {
			this.loadingOn()
			const resp = await this.$http.post(
				`/api/actor/list/members/${this.workspaceSelected}/`,
				JSON.stringify({
					page: this.page,
					limit: this.perPage,
					order_by: this.order_by,
					order_dir: this.order_dir,
					filter: this.filter,
					select: ['id', 'email', 'username', 'roles', 'scope', 'phone']
				}),
				{headers: {Range: 'get'}}
			)
			const local_roles = resp.data.data
			if (!local_roles) {
				this.rows = []
				this.totalRecords = 0
				this.loadingOff()
				return
			}
			
			try {
				this.rows = resp.data.data.map(item => {
					this.roles.forEach(role => {
						item[role] = item.roles.includes(role)
					})
					delete item.roles
					return item
				})
				this.totalRecords = resp.data.total_count
			}
			finally {
				this.loadingOff()
			}

		}
	},

	async mounted () {
		await this.fetchRows()
	}

}

</script>

