<template>
<section class="lm-overlay select-none">
    <div class="flex content-center flex-wrap justify-center h-full">
        <div class="vx-card" style="max-width: 780px;">
            <div class="flex">
                <div class="w-1/3 bg-primary-grad rounded-l-lg flex">
                    <feather-icon svgClasses="w-16 h-16 self-center text-white" class="mx-auto" icon="UserIcon"/>
                </div>
                <div class="w-2/3 px-4">
                    <h3 class="mb-4 mt-6 text-center text-primary">{{ $t('headers.complete_data') }}</h3>
                    <h5 class="mb-4 text-center font-normal text-grey-500">{{ $t('headers.need_your_info') }}</h5>
                    <laima-form
                        class="user-laima-form shadow-none mt-8"
                        ref="laima"
                        :cleanForm="cleanPersonalPayload"
                        :formFields="personalInfoFields"
                        :isLoading="isSaving"
                        :modelsContainer="$store.getters['auth/getUserContext'] || {}"
                        :onSubmit="savePersonalData">
                    </laima-form>
                </div>
            </div>
        </div>
    </div>
</section>

</template>

<script>
export default {
    data: () => ({
        isSaving: false
    }),
    computed: {
        personalInfoFields () {
            return [
                {
                    name: 'fullname',
                    type: 'text',
                    validators: {required: true},
                    helpText: this.$t('help_texts.fullname'),
                    label: this.$t('fields.name'),
                    placeholder: this.$t('placeholders.fullname')
                },
                {
                    name: 'dob',
                    type: 'date',
                    validators: {required: true},
                    label: this.$t('fields.dob')
                }
            ]
        },
    },
    methods: {
        cleanPersonalPayload (payload) {
            if (payload.fullname) {
                const [name, last_name] = payload.fullname.split(' ')
                if (!last_name) {
                    throw new this.ValidationError(
                        'fullname',
                        'Last name is required'
                    )
                }
                payload = Object.assign(payload, {name, last_name})
                delete payload.fullname
            }
            if (payload.dob) {
                payload.dob = new Date(payload.dob).toISOString().split('T')[0]
            }
            return payload
        },

        async savePersonalData (data) {
            const post_resp = await this.requestResource('post_user', {
                data,
                vm: this,
                similarErrMsgMap: {
                    fullname: {
                        '.*?already exists': this.$t('notifs.user_exists')
                    }
                },
                errRespMap: {
                    actor: 'fullname'
                }
            })
            if (post_resp.status === 200) {
                this.isSaving = true
                this.$store.dispatch("auth/updateUserContext", data)
                this.notifySuccess(this.$t('notifs.saved_user_details'))
                await this.$root.syncManager.setup(true)
                setTimeout(() => {
                    this.$store.commit('auth/USER_MISSING', false)
                }, 200)
                this.isSaving = false
            }
        },
    }
}
</script>

