    @import "@/assets/scss/vuexy/_variables.scss";
    .user-laima-form {
        .fields-box {
            .field-desc {
                max-width: 150px !important;
            }
            .field-value-wrap {
                width: 100% !important;
            }
            border: none !important;
        }
        .vs-divider {
            margin-top: 1.5rem !important;
            padding: 0 1rem;
        }
        button {
            margin-left: 1rem !important;
        }
