<template>
    <div class="schedule-settings">
        <laima-form
            @updated="onUpdated"
            formScope="step-1"
            ref="laimaForm"
            :modelsContainer="clinicContainer"
            :formFields="formFields"
            :title="$t('headers.visit_config')" >
        </laima-form>

        <opening-hours class="mt-8" :clinic="clinic" />
        
    </div>
</template>

<script>
import OpeningHours from './OpeningHours.vue'
export default {
    components: {
        OpeningHours
    },

    computed: {
        clinicContainer () {
            return this.$clone(this.$store.state.core.clinics.find(clinic => clinic.id == this.clinicID))
        },

        clinic () {
            return this.$clone(this.$store.state.core.clinics.find(clinic => clinic.id == this.clinicID))
        },

        clinicID () {
            return this.$store.state.auth.selectedWorkspace.id
        },

        formFields () {
            return [
                {
                    name: 'default_visit_interval',
                    type: 'number',
                    label: this.$t('fields.default_visit_interval'),
                    helpText: this.$t('help_texts.default_visit_interval'),
                    placeholder: 'e.g. 30',
                    validators: {
                        required: true,
                        numeric: true
                    }
                },
                {
                    name: 'currency',
                    type: 'select',
                    label: this.$t('fields.def_currency'),
                    options: ['CHF', 'EUR', 'PLN', 'USD'].map(i => ({
                        value: i,
                        text: i
                    }))
                },
                {
                    name: 'send_confirmation_before',
                    type: 'number',
                    label: this.$t('fields.send_confirmation_before'),
                    helpText: this.$t('help_texts.send_confirmation_before'),
                    placeholder: 'e.g. 1',
                    validators: {
                        required: true,
                        numeric: true,
                        min_value: 1,
                        max_value: 4
                    }
                },
            ]
        }
    },
    methods: {
        onUpdated ([payload, callback]) {
            this.$store.dispatch('core/editClinic', [
                this, this.clinic.id, payload, callback
            ])
        }
    }
}
</script>