<template>
<div class="planner-wrap">
    <vue-cal
        activeView="week"
        hideViewSelector
        hide-title-bar
        ref="vuecal"
        show-week-numbers
        today-button
        week-number-cell
        :cell-click-hold="false"
        :dblclickToNavigate="false"
        :editable-events="{title: false, drag: false, resize: true, delete: false, create: false }"
        :disable-views="['years', 'year', 'day', 'month']"
        :events="slots"
        :locale="$i18n.locale"
        :min-event-width="100"
        :hide-weekdays="[7]"
        :snap-to-time="roundTo"
        :time-from="minHour"
        :time-to="maxHour"
        :selectedDate="currentDate"
        @event-duration-change="onDurationChange"
        @view-change="$parent.calendarChangedTimeframe">

        <template #event="{event}">
            <div v-if="event.class === 'schedule'" class='h-full planner-event-wrap' :data-schedule-id="event.id" style="z-index: 11">
                <time-marker :event="event" :roundTo="roundTo" @eventcreated="addNewBreak" :ref="`timemarker-${event.id}`" class="relative w-full h-full"/>
                <div :id="`slot-${event.id}`" :class="(event.endTimeMinutes - event.startTimeMinutes) <= 30 && 'is-brief'">
                    <div class="vuecal__event-title py-1">
                        {{ $t('headers.available') }}
                        <span class="font-normal">
                            ({{ new Date(event.start).formatTime('HH:mm') }}&nbsp;- {{ new Date(event.end).formatTime('HH:mm') }})
                        </span>
                    </div>
                    <template v-if="new Date(event.start) >= today">
                        <div class="schedule-control-btns right-0 top-0 absolute">
                            <div class="flex" style="margin: 5px 2px 0 0">
                                <span class="control-btn add-break" style=" z-index: 12; opacity: 0" @click="startTimemarker(`timemarker-${event.id}`, $t('buttons.add_new_break'))">
                                    <feather-icon v-tooltip="{content: $t('buttons.add_new_break'), classes: 'text-sm' }" class='text-primary' icon="PlusCircleIcon" svgClasses="w-6" style="height: inherit; bottom: 3px" />
                                </span>
                                <span class="control-btn delete-schedule " style=" z-index: 12; opacity: 0" @click="removeScheduleOpenConfirm(event)">
                                    <feather-icon v-tooltip="{content: $t('buttons.remove_schedule'), classes: 'text-sm' }" class='text-danger' icon="XCircleIcon" svgClasses="w-6" style="height: inherit; bottom: 3px" />
                                </span>
                            </div>
                        </div>
                    </template>
                </div>
            </div>
            <div v-else-if="event.class === 'appointment-shadow'" class="w-full">
                <!-- Appointment -->
            </div>
            <div v-else-if="event.class === 'lunch'" class="text-left w-full pl-1">
                <div class="vuecal__event-time">
                    <span v-if="new Date(event.start) >= today" class="control-btn absolute right-0 mt-1" style=" z-index: 12; opacity: 0" @click="removeBreak(event)">
                        <feather-icon v-tooltip="{content: $t('buttons.remove_break'), classes: 'text-sm'}" class='text-danger' icon="XCircleIcon" svgClasses="w-6" style="height: inherit; bottom: 3px" />
                    </span>
                    Break
                    <span class='lunch-details' style="opacity: 0;">({{ new Date(event.start).formatTime('HH:mm') }} - {{ new Date(event.end).formatTime('HH:mm') }})</span>
                </div>
            </div>
            <div v-else-if="event.class === 'closed'">
                <div class="vuecal__event-title">{{ $t('headers.clinic_closed') }}</div>
            </div>
            <div v-else-if="event.class.match('closed-morning|closed-evening')">
                <div class="vuecal__event-time">
                    <template v-if="event.class === 'closed-morning'">
                        <span class="font-medium">{{ $t('headers.clinic_openes_at') }}</span>
                        <span class="pl-1">{{ new Date(event.end).formatTime('HH:mm')}}</span>
                    </template>
                    <template v-else>
                        <span class="font-medium">{{ $t('headers.clinic_closes_at') }}</span>
                        <span class="pl-1">{{ new Date(event.start).formatTime('HH:mm') }}</span>
                    </template>
                </div>
            </div>
        </template>

        <template #cell-content="{ cell, view, events }">
            <div v-if="$parent.daysClosed.includes(cell.specialHours.day - 1)">
                <div class="closed w-full h-full absolute">
                    <span>{{ $t('headers.clinic_closed') }}</span>
                </div>
            </div>
            <div class="vuecal__no-event no-schedule h-full flex" v-else-if="calendarMode === 'planner' && !events.find(item => item.class === 'schedule')">
                <div class="h-full w-full">
                    <time-marker 
                        class="relative w-full h-full"
                        :event="viewToEvent(cell)"
                        :ref="`schedule-tm-${cell.specialHours.day}`"
                        :roundTo="roundTo"
                        @closed="addingScheduleToDay=-1"
                        @eventcreated="addNewSchedule" />
                    <div class="flex justify-center h-full" v-if="addingScheduleToDay != cell.specialHours.day">
                        <div class="inline-block self-center ">
                            <div class="flex flex-col">
                                <span class="text-xl missing-schedule mb-6 text-dark">{{ $t('headers.no_schedules') }}</span>
                                <div>
                                    <vs-button
                                        class="inline-block cursor-pointer"
                                        v-tooltip.bottom="$t('headers.add_schedule')"
                                        color="success"
                                        icon-pack="feather"
                                        icon="icon-plus"
                                        size="large"
                                        style="z-index: 14"
                                        type="border"
                                        :disabled="events[0] && new Date(events[0].start) < today"
                                        @click="startTimemarkerForSchedule(cell)">
                                    </vs-button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
            

    </vue-cal>
</div>
</template>

<script>
import ScheduleHeader from './ScheduleHeader.vue'
import TimeMarker from './TimeMarker.vue'
import 'vue-cal/dist/drag-and-drop.js'

export default {
    components: {
        ScheduleHeader,
        TimeMarker
    },

    data () {
        return {
            today: new Date(),
            eventCreationId: 0,
            timemark: null,
            roundTo: 15,
            breakDefaultDuration: 30,
            scheduleDefaultDuration: 300,
            addingScheduleToDay: -1
        }
    },

    computed: {
        
        calendarMode () {
            return this.$store.state.core.calendarMode
        },

        currentDate () {
            return this.$parent.currentDate
        },

        clinic () {
			return this.$store.getters['core/activeClinic']()
        },
       
        slots () {
            const doctorID = this.selectedDoctor.actor
            return [
                ...this.$store.getters['core/allPlannerSlotsByDoctorPlannerSlots'](doctorID)
            ]
        },

        selectedDoctor () {
            const store = this.$store.state.core
            return store.calendarActiveDoctor || store.docs[Object.keys(store.schedulerSlots)[0]] || store.docs[Object.keys(store.docs)[0]] || {}
        },

        minHour () {
            return this.$store.getters['core/activeClinicsTimeLimits'].min[1]
        },

        maxHour () {
            return this.$store.getters['core/activeClinicsTimeLimits'].max[1]
        }
    },

    methods: {
        emitSchedulerUpdate () {
            // triggered in case of i.e. calendar hour boundaries change
            this.$store.dispatch('core/recalculateOutliers', this.selectedDoctor.actor)
            setTimeout(() => {
                this.$parent.limsChanged++
            }, 200)
        },

        viewToEvent (cell) {
            const dayNumber = cell.specialHours.day
            const day = cell.startDate.clone()
            const [start, end] = [day, day.clone()]
            const [startHour, startMinutes] = this.$store.getters['core/activeClinicsTimeLimits'].min[0].split(':')
            const [endHour, endMinutes] = this.$store.getters['core/activeClinicsTimeLimits'].max[0].split(':')
            start.setHours(startHour)
            start.setMinutes(startMinutes)
            end.setHours(endHour)
            end.setMinutes(endMinutes)
            return {
                start,
                end,
                id: dayNumber,
                doctorID: this.selectedDoctor.actor
            }
        },
        
        removeScheduleOpenConfirm (event) {
            const scheduleIDToRemove = event.id
            const weeklySlots = this.$store.state.core.schedulerSlots[this.selectedDoctor.actor] || []
            const slotCount = weeklySlots.filter(item => {
                return item.scheduleID == scheduleIDToRemove
            }).length
            let text = this.$t('misc.delete_schedule_prompt')
            if (slotCount) {
                text += this.$t('misc.delete_schedule_confirmation', {visitsCount: slotCount})
            }
            this.$vs.dialog({
                text,
                acceptText: this.$t('buttons.accept'),
                cancelText: this.$t('buttons.cancel'),
                type: 'confirm',
                color: 'danger',
                title: this.$t('headers.delete_schedule'),
                accept: this.acceptAlert(event, slotCount)
            })
        },
        acceptAlert (event, hasSlots) {
            return () => {
                this.$store.dispatch('core/removeSchedule', [event, this, hasSlots])
            }
        },
        onDurationChange (event) {
            const eventClass = event.event.class
            if (eventClass === 'lunch') {
                this.$store.dispatch('core/resizeBreak', [event, this])
            }
            else if (eventClass === 'schedule') {
                this.$store.dispatch('core/resizeSchedule', [event, this])
            }
        },

        addNewBreak ([event, timemark]) {
            this.$store.dispatch('core/createBreak', [event, timemark, this.breakDefaultDuration, this])
        },

        addNewSchedule ([event, timemark]) {
            this.$store.dispatch('core/createSchedule', [event, timemark, this.scheduleDefaultDuration, this])
        },
        
        startTimemarker (ref, text) {
            const timemarkerRef = this.$refs[ref]
            timemarkerRef.startTimemarker(text)
        },

        startTimemarkerForSchedule (cell) {
            const dayIndex = cell.specialHours.day
            const ref = `schedule-tm-${dayIndex}`
            this.addingScheduleToDay = dayIndex
            this.startTimemarker(ref, this.$t('headers.add_schedule'))
        },

        removeBreak (event) {
            this.$store.dispatch('core/removeBreak', [event, this ])
        }
    },

    mounted () {
        window.plan = this
    }
}
</script>

