import axios from 'axios'
import EventBus from '@/EventBus.js'
import store from '@/store/store'

const axios_inst = axios.create({
	headers: {
		'Content-Type': "application/json"
	},
	validateStatus(status) {
		return status >= 200 && status < 500
	}
})

axios_inst.interceptors.response.use(resp => {
    const etag = resp.headers.etag
    if (etag) {
		if (store.state.auth.localSpecHashsum !== etag) {
			console.info(resp, 'Spec hashsum changed or missing, fetching new spec version... TODO!!!')
			// store.dispatch('auth/refreshSpec', etag)
		}
	}
	if (resp.status === 401) {
		EventBus.$emit('unauthed')
	}
    return resp
}, error => {
    return Promise.reject(error)
})

export default axios_inst