<template>
    <section class="lm-overlay lock-screen select-none">
        <div class="flex content-center flex-wrap justify-center h-full">
            <div class="vx-card" style="max-width: 600px;">
                <div class="vx-card__collapsible-content vs-con-loading__container">
                    <div class="full-page-bg-color">
                        <div class="vx-row no-gutter">
                            <div class="vx-col w-1/3 mx-auto self-center left-side">
                                <feather-icon svgClasses="w-16 h-16" class="mx-auto" icon="KeyIcon"/>
                            </div>
                            <div class="vx-col w-2/3 mx-auto self-center d-theme-dark-bg">
                                <div class="p-8">
                                    <div class="vx-card__title mb-4">
                                        <h3 class="mb-4 text-center">{{ $t('headers.session_locked') }}</h3>
                                        <h5 class="text-center font-normal">{{ $t('headers.enter_pin') }}</h5>
                                    </div>
                                    <masked-input
                                        @click="$ev => $ev.target.select()"
                                        style="caret-color: transparent;"
                                        autocomplete="off"
                                        id="pincode-input"
                                        class="vs-inputx vs-input--input hasValue text-2xl text-center"
                                        type="text"
                                        name="pin"
                                        :mask="[/\d/, /\d/, /\d/, /\d/]"
                                        v-model="unlockPIN"
                                        placeholder="____">
                                    </masked-input>
                                </div>
                                <div class="vx-row no-gutter" style="margin-bottom: 25px;">
                                    <a href="#" target="_self" @click="logout" class="underline w-full text-center">{{ $t('headers.forgotten_pin') }}</a> 
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import auth from "@/auth/authService"

export default {
    data () {
        return {
            unlockPIN: null
        }
    },
    methods: {
        logout () {
            auth.logOut()
            this.$store.commit('UNLOCK_SCREEN')
			this.$router.push({name: 'page-login'})
        }
    },
    watch: {
        async unlockPIN (val) {
            if(val && !val.match('_') && val.length === 4) {
                const data = {
                    pin: val
                }
                const resp = await this.requestResource('get_verifyScreenLock', {
                    data
                })
                if (resp.status === 403) {
                    this.notifyError(this.$t('notifs.pin_invalid'))
                }
                else if (resp.status === 200) {
                    this.$store.commit('UNLOCK_SCREEN')
                    this.notifySuccess(this.$t('notifs.screen_unlocked'))
                }
            }
        }
    }
}
</script>

