<template>
<vs-sidebar vs-custom-content position-right parent="body" default-index="-1" color="primary" class="add-edit-slot" :value="addSlotPrompt">
    <div class="header-sidebar" slot="header">
        <div class="flex justify-between w-full h-full bg-primary text-white">
            <h5 class="pl-4 flex self-center text-white font-light">{{ $t('headers.add_visit') }}</h5>
            <feather-icon class="pr-4 text-white cursor-pointer" svgClasses="w-6 h-6" @click="closeSidebar()" icon="XIcon"/>
        </div>
    </div>
    <laima-form
        v-if="addSlotPrompt"
        ref="lf"
        size="small"
        formScope="step-1"
        @changed="changedField"
        :formFields="slotFields"
        :onSubmit="true"
        :modelsContainer="initModelsContainer"
        :title="$t('headers.visit')" >
        <template #footer>
            <div></div>
        </template>
    </laima-form>
    <add-edit-patient ref="patient" size="small" />

    <vs-button @click="submit" class="w-full rounded-none" size="large" type="filled" color="primary">{{ $t('buttons.save') }}</vs-button>
</vs-sidebar>
</template>

<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'

export default {
    components: {
        VuePerfectScrollbar
    },
    mounted () {
        window.add = this
        const interimPatient = this.$store.state.core.interimPatient
		if (interimPatient) {
            // autocomplete the form
            this.$nextTick(() => {
                this.$refs.patient.models = Object.assign({}, interimPatient)
            })
        }
    },
    props: {
        startHour: {
            type: Date
        },
        endHour: {
            type: Date
        },
        scheduleID: {
            type: Number
        },
        doctorID: {
            type: Number
        }
    },

    data () {
        return {
            slot: {}
        }
    },

    computed: {
        slotsPerDoctor () {
            return this.$store.getters['core/allSchedulerSlotsByDoctor'](this.doctorID, this)
        },
        allSlots () {
            return Object.values(this.$store.state.core.schedulerSlots).flatMap(i => i)
        },
        hourRange () {
            const dayIndex = this.startHour.getDay() - 1
            const [minHour, maxHour] = this.$store.state.core._schedulerClosureHoursEachDoc[dayIndex].doctors[this.doctorID].scheduleDuring.map(i => (new Date(i).format('HH')))
            return [
                parseInt(minHour), 
                parseInt(maxHour)
            ]
        },
        overlapMessages () {
            return {
                main: this.$t('overlapMessages.main'),
                lunch: this.$t('overlapMessages.lunch'),
                noOffices: this.$t('overlapMessages.noOffices'),
                closedMorning: this.$t('overlapMessages.closedMorning'),
                closedEvening: this.$t('overlapMessages.closedEvening'),
            }
        },
        slotFields () {
            return [
                {
                    name: 'startTime',
                    label: this.$i18n.t('fields.startTime'),
                    type: 'time',
                    interval: this.default_visit_interval,
                    hourRange: [this.hourRange],
                    validators: {required: true}
                },
                {
                    name: 'duration',
                    label: this.$i18n.t('fields.duration'),
                    helpText: this.$i18n.t('help_texts.duration'),
                    classes: ['duration-input'],
                    type: 'fancyNumber',
                    step: this.default_visit_interval,
                    min: this.default_visit_interval,
                    max: 180
                },
                {
                    name: 'procedure_category',
                    label: this.$i18n.t('fields.procedure_category'),
                    type: 'select',
                    options: this.procCategories
                },
            ]
        },
        slotDetails () {
            const delta = this.endHour - this.startHour
            return {
                startTime: this.startHour,
                duration: delta / 6e+4
            }
        },
        initModelsContainer () {
            const delta = this.endHour - this.startHour
            return {
                duration: delta / 6e+4,
                startTime: this.startHour.format('HH:mm')
            }
        },
        addSlotPrompt () {
            return this.$store.state.core.addSlotPrompt
        },
        default_visit_interval () {
            const activeClinic = this.$store.getters['core/activeClinic']()
            return activeClinic && activeClinic.default_visit_interval
        }
    },

    methods: {
        closeSidebar () {
            this.clearForm()
            this.toggleBodyOverflow()
            this.$store.commit('core/SET_ADD_SLOT_PROMPT', false)
        },
        clearForm () {
            try {
                Object.keys(this.$refs.patient.models).forEach(key => {
                    this.$set(this.$refs.patient.models, key, '')
                })
            }
            catch (e) {}
        },
        async submit () {
            const patientValid = await this.$refs.patient.$refs.lf.$validator.validateAll('step-1')
            const visitValid = !this.$refs.lf.$validator.errors.items.length
            if (patientValid && visitValid) {
                const patientPayload = this.$refs.patient.models
                // patientPayload
                const patient_id = patientPayload.patient_id.toString()
                let [yearborn, monthBorn] =  [patient_id.slice(0, 2), patient_id.slice(2, 4)]
                if (parseInt(monthBorn) > 12) {
                    yearborn = '20' + yearborn
                }
                else {
                    yearborn = '19' + yearborn
                }
                patientPayload.year_born = yearborn
                patientPayload.country = 'PL'
                const resp = await this.requestResource('post_patientClinicRegistration', {
                    data: patientPayload,
                    scope: 'step-1',
                    vm: this.$refs.patient.$refs.lf,
                    errRespMap: {
                        year_born: 'patient_id'
                    }
                })
                if (resp.status === 200) {
                    // now submit the new visit itself
                    const patient = resp.data.actor_id
                    const [start, end] = [
                        this.slotDetails.startTime,
                        this.slotDetails.startTime.addMinutes(this.slotDetails.duration)
                    ]
                    
                    const slotPayload = {
                        patient,
                        schedule: this.scheduleID,
                        during: [
                            start.format(`YYYY-MM-DD HH:mm${this.timezoneOffset}`),
                            end.format(`YYYY-MM-DD HH:mm${this.timezoneOffset}`)
                        ]
                    }
                    if (this.slotDetails.procedure_category) {
                        slotPayload.procedure_category = this.slotDetails.procedure_category
                    }
                    const slotResp = await this.requestResource('post_scheduleslot', {
                        data: slotPayload,
                        scope: 'step-1',
                        vm: this.$refs.lf
                    })
                    if (slotResp.status === 422) {
                        const during = slotResp.data.during
                        if (during) {
                            if (during[0].match('office')) {
                                this.notifyError(this.$t('misc.no_avail_offices'))
                            }
                            else {
                                this.notifyError(slotResp.data.during[0])
                            }
                        }
                    }
                    if (slotResp.status === 200) {
                        const slotID = slotResp.data.id
                        const commitPayload = {
                            doctorID: this.doctorID,
                            start,
                            end,
                            class: 'main',
                            status: 'booked',
                            id: slotID,
                            split: parseInt(this.doctorID),
                            draggable: true,
                            title: [patientPayload.name, patientPayload.last_name].join(' ')
                        }
                        this.notifySuccess(this.$t('notifs.new_visit', {title: commitPayload.title}) )
                        this.$store.commit('core/ADD_SCHEDULER_SLOT', commitPayload)
                    }
                }
                else {
                    return this.notifyError(this.$t('notifs.server_error'))
                }
                this.$store.commit('SET_GLOBAL_MESSAGE', null)
                this.$store.commit('core/RESET_INTERIM_PATIENT')
                this.closeSidebar()
            }
        },
        changedField ([field, val]) {
            const doctorID = this.$store.state.core.interimEvents[0].split
            this.$set(this.slotDetails, field, val)
            if (field === 'procedure_category') return

            let newStartTime, newEndTime
            const newSlotDate = this.startHour.format('MM-DD')
            
            if (field === 'startTime') {
                const [hours, minutes] = val.split(':')
                newStartTime = this.startHour.clone()
                newStartTime.setHours(hours)
                newStartTime.setMinutes(minutes)
                newEndTime = newStartTime.addMinutes(this.slotDetails.duration)
                
                this.$set(this.slotDetails, field, newStartTime)
            }
            else if (field === 'duration') {
                newStartTime = this.slotDetails.startTime
                newEndTime = newStartTime.addMinutes(val)
            }

            const newSlotDateTime = newStartTime.getTime()
            const endSlotDate = newEndTime.getTime()
            const sameDaySlots = this.slotsPerDoctor.filter(slot => {
                const {start, end} = slot
                return start instanceof Date && newSlotDate === start.format('MM-DD')
                       && !slot.class.match('interim-slot|interim-overlap')
                       && start.getTime() != end.getTime()
            })
            
            const overlaps = sameDaySlots.find(slot => {
                if (doctorID != slot.doctorID) return false
                let {start, end} = slot
                start = start.getTime()
                end = end.getTime()
                return (
                    start <= newSlotDateTime && end > newSlotDateTime // within the slot
                ) || (
                    start < endSlotDate && end >= endSlotDate // within the slot
                ) || (
                    start >= newSlotDateTime && end <= endSlotDate // slot within
                )
            })

            // do we have enough office space to allow new appointment?
			const officeCount = this.$parent.clinic.office_count
			const officeOverlaps = this.allSlots.reduce((prev, slot) => {
				if (doctorID == slot.doctorID || slot.class != 'main') return prev
				let {start, end} = slot
				start = start.getTime()
				end = end.getTime()
				let res = (
                    start <= newSlotDateTime && end > newSlotDateTime // within the slot
                ) || (
                    start < endSlotDate && end >= endSlotDate // within the slot
                ) || (
                    start >= newSlotDateTime && end <= endSlotDate // slot within
                )
				return res + prev
            }, 0)
            const tooManySlots = officeOverlaps >= officeCount
            const interimClass = !overlaps && !tooManySlots ? 'interim-slot' : 'interim-overlap'
            this.$store.commit('core/UPDATE_INTERIM_SLOT', {start: newStartTime, end: newEndTime, class: interimClass})
            if (overlaps || tooManySlots) {
                setTimeout(() => {
                    this.$refs.lf.$validator.errors.add({
                        field,
                        scope: 'step-1',
                        msg: this.overlapMessages[overlaps && overlaps.class || 'noOffices']
                    })
                }, 0)
            }
            else {
                this.$refs.lf.$validator.errors.remove(field, 'step-1')
            }
        }
    },
    beforeDestroy () {
        window.removeEventListener('keydown', e => {
            if (e.key == 'Escape') {
                this.closeSidebar ()
            }
        })
    },
    watch: {
        addSlotPrompt (val) {
            if (val) {
                window.addEventListener('keydown', e => {
                    if (e.key == 'Escape') {
                        this.closeSidebar ()
                    }
                })
            }
        }
    }
}
</script>

<style lang="scss">
.add-edit-slot {
    .vs-sidebar {
        .vs-sidebar--header {
            padding: 0;
            z-index: 1;
            border: none;
        }
        .header-sidebar {
            height: 42px;
            box-shadow: 3px 2px 15px #00000038;
        }
        .vs-sidebar--items {
            padding: 0;
        }
        min-width: 470px;
    }
    .laima-form {
        box-shadow: none;
        .fields-box {
            &:last-child {
                border-bottom: 1px solid #dadada;
            }
        }
        .vx-card__header {
            border-radius: 0 !important;
        }
    }
    .vx-card {
        border-radius: 0 !important;
    }
}
</style>