import http from "@/axios.js"
import router from '@/router'
import EventBus from '@/EventBus.js'

class AuthService {

    clearLocalStorage () {
        // certain keys will need to be spared
        const leftoutKeys = ['app.laima.screenLock.isSet', 'app.laima:fngprt']

        for (let key in window.localStorage) {
            if (!leftoutKeys.includes(key)) {
                window.localStorage.removeItem(key)
            }
        }
    }

    logOut (config={}) {
        console.info("Logging out..." + config.reason)
        http.get('/api/actor/logout/').then(() => {
            const source = config.reason || {}
            const to = config.to || router.currentRoute.path
            EventBus.$emit('logout', {source, to})
            // router.push({
            //     name: 'page-login',
            //     query: {
            //         source,
            //         to: config.to || router.currentRoute.path
            //     },
            // })
            // this.clearLocalStorage()
        })
    }

    isAuthenticated () {
        // check for: user info, spec hashsum and spec itself, all 
        return (
            !!localStorage.getItem('app.laima.actorInfo') && !!localStorage.getItem('app.laima.specHashsum') && !!localStorage.getItem('app.laima.spec')
        )
    }
}

export default new AuthService()
