<template>
<div id="user-security">
    <laima-form
        formScope="step-1"
        :formFields="screenLockFields"
        :modelsContainer="screenLockPayload"
        :title="$t('headers.screen_lock')">
        <template #beforefield="{item}">
            <vs-alert class="form-alert flex primary"
                    v-if="item.name === 'screenlock' && useScreenLock"
                    active="true" icon-pack="feather"
                    icon="icon-info">
                <div class="alert-text font-normal">
                    {{ $t('headers.lockscreen_code_disclaimer') }}
                </div>
                <div class="alert-text font-normal">
                    {{ $t('headers.otp_app_cta') }}
                </div>
            </vs-alert>
        </template>

        <template #screenlock="{item, successCallback}">
            <vs-switch
                v-model="useScreenLock"
                @input="toggleScreenLock(successCallback)"
            />
        </template>        
        <template #pin="{item, hasErrors, successCallback}">
            <lm-input
                :class="hasErrors && 'input-icon-validate-danger'"
                :name="item.name"
                ref="pin"
                type="password"
                maxlength="4"
                minlength="4"
                v-validate="item.validators"
                v-debounce:1100ms="partial(changedScreenLock, item.name, successCallback)"
                @keypress.enter="changedScreenLock(item.name, successCallback)"
                @focus="$event => $event.target.select()"
                @click="$event => $event.target.select()"
                class="pin-field"
                v-model="pin" />
        </template>
        <template #defaultIdleTime="{item, hasErrors, successCallback}">
            <lm-input
                :disabled="!pin || pin.length != 4"
                :name="item.name"
                type="number"
                :data-vv-as="item.label.toLowerCase()"
                v-validate="item.validators"
                v-debounce:500ms="partial(changedScreenLock, item.name, successCallback)"
                @keypress.enter="changedScreenLock(item.name, successCallback)"
                :class="hasErrors && 'input-icon-validate-danger'"
                v-model="defaultIdleTime" />
        </template>
    </laima-form>

    <laima-form formScope="step-1" :formFields="OTPFields" :validator="$validator" 
        :title="$t('headers.otp_setup')" :modelsContainer="{}" class="mt-8">
        <template #beforefield>
            <vs-alert class="form-alert flex primary"
                        active="true" icon-pack="feather"
                        icon="icon-info">
                <div class="alert-text font-normal">
                    {{ $t('headers.lockscreen_code_disclaimer') }}
                </div>
                <div class="alert-text font-normal">
                    {{ $t('headers.otp_app_cta') }}
                </div>
            </vs-alert>
        </template>
        <template #otpSecret>
            <vs-button class="w-32 justify-end" @click="requestOTPQR">{{ $t('headers.view') }}</vs-button>
        </template>
    </laima-form>
    
    <vs-prompt
        style="z-index: 10000000"
        :title="$t('misc.totp_pin_prompt')"
        :buttonsHidden="true"
        @close="otpSecret = null"
        :active="!!otpSecret">
            <div class="text-center">
                <p>{{ $t('headers.scan_code_cta') }}</p>
            </div>
            <div id="qr-dialog" class="">
                <qrcode class="qr" ref="qr" :value="TOTPval" :options="{ size: 300 }"></qrcode>
            </div>
    </vs-prompt>
</div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import VueQrcode from '@xkeshi/vue-qrcode'
import Vue from 'vue'

Vue.component(VueQrcode.name, VueQrcode)

export default {

    data () {
        return {
            otpSecret: null,
            screenLockPayload: {}
        }
    },

    computed: {
		...mapState({
				actorContext: state => state.auth.actorContext,
				userContext: state => state.auth.userContext || {}
		}),
        ...mapGetters(['auth/getUserContext']),

        OTPFields () {
            return [
                {
                    model: 'otpSecret',
                    name: 'otpSecret',
                    type: 'button',
                    label: this.$t('fields.otp_qr'),
                    helpText: this.$t('help_texts.otp')
                }
            ]
        },

        pin: {
            get () {
                return this.screenLockPayload.pin || '0000'
            },
            set (val) {
                this.$set(this.screenLockPayload, 'pin', val)
            }
        },

        useScreenLock: {
            get () {
                return this.$store.state.screenLock.isSet
            },
            set (val) {
                if (val) {
                    this.$store.commit('ENABLE_SCREEN_LOCK')
                }
                else {
                    this.$store.commit('DISABLE_SCREEN_LOCK')
                }
            }
        },
        
        defaultIdleTime: {
            get () {
                return this.screenLockPayload.defaultIdleTime || this.$store.state.screenLock.defaultIdleTime
            },
            set (val) {
                this.$set(this.screenLockPayload, 'defaultIdleTime', val)
            }
        },

        screenLockFields () {
            return  [ 
                {
                    model: 'useScreenLock',
                    name: 'screenlock',
                    type: 'switch',
                    label: this.$t('fields.use_lock')
                },
                {
                    hidden: !this.useScreenLock,
                    name: 'pin',
                    type: 'number',
                    validators: {
                        required: true,
                        integer: true,
                        length: 4
                    },
                    label: this.$t('fields.lock_pin'),
                    helpText: this.$t('help_texts.lock_pin')
                },
                {
                    model: 'defaultIdleTime',
                    hidden: !this.useScreenLock,
                    type: 'number',
                    validators: {
                        required: true,
                        integer: true,
                        min_value: 30
                    },
                    name: 'defaultIdleTime', 
                    label: this.$t('fields.time2lock'),
                    helpText: this.$t('help_texts.time2lock')
                },

            ]
        },
		isAuthed () {
			return this.$store.state.auth.actorContext.roles.includes('Owner')
		},
		TOTPval () {
			const actorContext = this.$store.state.auth.actorContext
			return `otpauth://totp/${actorContext.email}?secret=${this.otpSecret}&issuer=laima.app`
		}
    },
    
    methods: {
		async requestOTPQR () {
			// this is a shielded resource, so can't really await its original response. 
			// Instead, listen on to vuex's variable set by the shieldDialog component.
			await this.requestResource('get_getOtpSecret', {data: {}})
        },

        async toggleScreenLock (callback) {
            
            const status = +this.useScreenLock
            const data = {
                select: { actor: this.$store.state.auth.actorContext.id },
                payload: { status }
            }

            const patch_resp = await this.requestResource('patch_screenlock', {
                data,
                vm: this
            })

            if (patch_resp.status === 422 && patch_resp.statusText === 'Update failed') {
                // the record doesn't exit for this client, create new
                await this.requestResource('post_screenlock', {
                    data: {
                        time_to_lock: this.defaultIdleTime,
                        status: 1
                    },
                    vm: this
                })
            }
            
            // const newState = status && 'on' || 'off'
            if (status) return this.notifySuccess(this.$t('notifs.lock_on'))
            this.notifySuccess(this.$t('notifs.lock_off'))
        },

        async changedScreenLock (field, callback) {
            if (!await this.$validator.validateAll('step-1')) return

            let serverField = field
            if (field === 'defaultIdleTime') {
                serverField = 'time_to_lock'
            }
            
            const data = {
                select: {
                    actor: this.$store.state.auth.actorContext.id
                },
                payload: {
                    [serverField]: this[field]
                }
            }
            const patch_resp = await this.requestResource('patch_screenlock', {
                data,
                vm: this
            })

            if (patch_resp.status === 200) {
                if (field === 'defaultIdleTime') {
                    this.$store.commit('SET_LOCK_SCREEN_IDLE_TIME', this.defaultIdleTime)
                }
                callback()
            }
        }
    },

    async mounted () {
        window.ii = this
    },
    
	watch: {
		'$store.state.auth.shieldedResponse' (val) {
			if (val.source === 'get_getOtpSecret') {
				this.otpSecret = val.data.otp_secret
			}
		}
	},
}
</script>

<style lang="scss">
#user-security {
    $break-large: 600px;
    #qr-dialog {
        text-align: center;
        margin: 19px;
    }
    .pin-field {
        input {
            max-width: 65px;
            height: 42px;
            font-size: 2rem !important;
        }
    }
}
</style>