<template>
<div class="calendar-menu-wrap">
    <div class="vuecal__menu justify-between vx-row shadow relative mb-6" style="top: -15px; max-height: 67px">
        <!-- <vs-button v-if="currentView.match('day')"
            type="flat"
            color="primary"
            icon-pack="feather"
            icon="icon-chevron-left"
            class="shadow-none"
            @click="activeCalendar.switchView('week')"
        >Go back</vs-button> -->

        <section class='action-buttons flex ml-4' style="max-height: 48px; height: 67px;"  >
            <div class="con-img flex mr-3" v-if="!currentView.match('day|month')">
                <div class="flex calendar-mode border-2 bg-white shadow-md border-solid cursor-pointer self-center" :class="calendarMode">
                    <vs-dropdown ref="dropdown" vs-custom-content vs-trigger-click class="cursor-pointer ml-4" v-tooltip="`${selectedDoctor.name} ${selectedDoctor.last_name}`">
                        <vs-avatar
                            v-if="!selectedDoctor.avatar"
                            style="font-size: 23px;"
                            size="35px"
                            color="white"
                            class="m-0 relative"
                            :text="selectedDoctorInitials"/>
                        <div v-else class="relative" style="width: 48px; height: 3rem">
                            <img :src="selectedDoctor.avatar"
                                alt="doctor-img"
                                class="m-0 rounded-full shadow-md cursor-pointer absolute z-30"
                                width="48"
                                height="48"/>
                        </div>
                                <!-- <p class="text-white tracking-wider"> {{ selectedDoctorName }}</p> -->
                        <vs-dropdown-menu
                                v-if="availableDoctors.length > 1"
                                class="calendar-menu-wrap notification-dropdown dropdown-custom"
                                style="width: auto; margin-left: -5px; margin-top: 3px">
                            <div class="notification-top text-center px-5 py-2 bg-primary text-white">
                                <h5 class="text-white tracking-wider font-light py-1 px-4" style="letter-spacing: 0; font-size: 15px;">{{ $t('misc.select_doctor') }}</h5>
                            </div>
                            <div class="flex">
                                <ul class="bordered-items w-full"> 
                                    <li v-for="doc in availableDoctors" :key="doc.name"
                                        class="flex justify-between px-4 py-4 notification cursor-pointer doc-line-wrap"
                                        :class="doc.actor === selectedDoctor.actor && 'docLineActive'"
                                        @click="selectDoctor(doc)" >
                                        <div class="flex items-center doc-line">
                                            <vs-avatar
                                                v-if="!doc.avatar"
                                                :style="{
                                                    'font-size': '17px'
                                                }"
                                                size="28px"
                                                color="#444"
                                                text-color="white"
                                                :class="doc.actor === selectedDoctor.actor && 'activeAvatar'"
                                                class="m-0"
                                                :text="`${doc.name[0]}${doc.last_name[0]}`"/>
                                            <img v-else 
                                                key="onlineImg"
                                                :src="doc.avatar"
                                                alt="doctor-img"
                                                :class="doc.actor === selectedDoctor.actor && 'activeAvatar'"
                                                class="m-0 rounded-full shadow-md cursor-pointer relative"
                                                width="30"
                                                height="30"/>
                                            <div style="font-size: 14px" class=" mx-2 ml-4" >
                                                {{ doc.name }} {{doc.last_name}}
                                            </div>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </vs-dropdown-menu>
                    </vs-dropdown>
                    <text-switch
                        inversed
                        class="transition-colors ml-10"
                        size="big"
                        style="width: 170px; background: transparent; margin-top: 0px; left: 7px; margin-left: -7px;" v-model="calendarMode"
                        ref="text"
                        :color="calendarSwitchColor"
                        :options="[
                            {label: this.$t('misc.scheduler'), val: 'scheduler'},
                            {label: this.$t('misc.planner'), val: 'planner'}
                        ]"
                    />
                </div>
            </div>
            <section class="flex ml-4" v-if="calendarMode === 'scheduler'">
                <scheduler-searchbar @on="$parent.searchbarOn=true" @off="$parent.searchbarOn=false" style="z-index: 50000000"/>
            </section>
        </section>

        <section class='flex' style="max-height: 48px">
            <section v-if="calendarMode === 'scheduler'" class='range-button flex mr-4' style="max-height: 46px">
                <vs-button 
                    class="border border-solid rounded-r-none"
                    :class="currentView == 'month' && 'active-view'"
                    @click="activeCalendar.switchView('month')">{{ $t('datetime.month') }}</vs-button>              
                <vs-button 
                    :class="[currentView == 'week' && 'active-view', currentView === 'day' && penultimateDayCtrlBtnClasses || lastDayCtrlBtnClasses]"
                    @click="activeCalendar.switchView('week')">{{ $t('datetime.week') }}</vs-button>              
                <vs-button v-if="currentView === 'day'"
                    :class="[currentView == 'day' && 'active-view', currentView === 'day' && lastDayCtrlBtnClasses || penultimateDayCtrlBtnClasses]"
                    @click="activeCalendar.switchView('day')">{{ $t('datetime.day') }}</vs-button>              
            </section>
            <vs-button type="relief" class="ctrl-btn my-auto h-full w-16" 
                    icon-pack="feather" icon="icon-arrow-left-circle text-3xl"
                    @click="getPrevTimeFrame()" />
            <vs-dropdown vs-custom-content vs-trigger-click class="heatmap-dropdown cursor-pointer hover:underline" >
                <div class="date-display inline-grid cursor-pointer" v-if="isMounted" v-html="currentDateTitle"></div>
                <vs-dropdown-menu class="notification-dropdown dropdown-custom" style="z-index: 50001">
                    <monthly-slots-heatmap
                        @timeFrameChanged="updateMonthlyPicker"
                        @dateSelected="datepickerDateChanged"
                        :doctor="$parent.selectedDoctor.actor"
                        :date="$parent.currentDate.format('YYYY-MM')" 
                        :set="currentView === 'week' ? 'docs' : 'all'" />
                </vs-dropdown-menu>
            </vs-dropdown>
            <vs-button
                class="ctrl-btn my-auto ml-2 h-full w-16"
                icon-pack="feather"
                icon="icon-arrow-right-circle text-3xl"
                type="relief"
                @click="getNextTimeFrame()" />
        </section>
        
    </div>
</div>
</template>

<script>
import MonthlySlotsHeatmap from './MonthlySlotsHeatmap.vue'
import SchedulerSearchbar from './SchedulerSearchbar.vue'

export default {

    props: {
        calendar: {},
        currentView: {
            default: 'week'
        },
        isMounted: {
            default: false
        },
        currentDate: {},
        currentViewChanged: {}
    },

    data () {
        return {
            lastDayCtrlBtnClasses: "rounded-l-none border border-solid border-l-0",
            penultimateDayCtrlBtnClasses: "rounded-r-none rounded-l-none border border-solid border-l-0"
        }
    },

    components: {
        MonthlySlotsHeatmap,
        SchedulerSearchbar
    },

    computed: {
        activeCalendar () {
            if (this.calendarMode === 'scheduler') {
                return this.$parent.$refs.vuecal                
            }
            return this.$parent.$refs.planner.$refs.vuecal
        },
        calendarMode: {
            get () {
                return this.$store.state.core.calendarMode
            },
            set (val) {
                this.$store.dispatch('core/switchCalendarMode', {mode: val, vm: this})
            }
        },
        calendarSwitchColor () {
            return this.calendarMode === 'scheduler' && 'rgba(var(--vs-primary, 1))' || 'rgba(var(--vs-success, 1))';
        },
        clinic () {
			return this.$store.getters['core/activeClinic']()
        },
        currentDateTitle () {
            const timeZone = this.$i18n.locale
			this.currentViewChanged
            let view
			try {
				view = this.activeCalendar.view
			}
			catch (e) {
				return 'MM/YYYY'
			}
			if (view.id === 'month') {
                const month = view.startDate.toLocaleString(timeZone, { month: 'long' })
				return month + ' ' + view.startDate.format('YYYY')
			}
			if (view.id === 'week') {
                const monthStart = view.startDate.toLocaleString(timeZone, { month: 'short' })
                let {startDate, endDate} = view
                let endMonth = endDate.format('MM')
                endDate = endDate.clone().subtractDays(1)
                let [startDay, endDay] = [startDate, endDate].map(i => i.format('DD'))
                if (startDate.format('MM') != endMonth) {
                    endMonth = endDate.toLocaleString(timeZone, { month: 'short' })
                    return `${startDay} ${monthStart} - ${endDay} ${endMonth} <br> ${view.startDate.format('YYYY')}`
                }
                return `${startDay}-${endDay} ${monthStart} <br> ${view.startDate.format('YYYY')}`
			}
			if (view.id === 'day') {
                let date = this.$parent.currentDate
                const weekday = date.toLocaleString(timeZone, { weekday: "long" })
                return weekday + ' ' + date.format(`DD/MM/YY`)
            }
		},
        availableDoctors () {
            return Object.values(this.$store.state.core.docs)
        },
        selectedDoctor: {
            get () {
                const store = this.$store.state.core
                return store.calendarActiveDoctor || store.docs[Object.keys(store.schedulerSlots)[0]] || store.docs[Object.keys(store.docs)[0]] || {}
            },
            set (val) {
                // this.$store.dispatch('core/recalculateOutliers', val)
                this.$store.commit("core/SET_ACTIVE_DOCTOR", val)
            }
        },
        selectedDoctorName () {
			return `${this.selectedDoctor.name} ${this.selectedDoctor.last_name}`
        },
        selectedDoctorInitials () {
			try {
				return `${this.selectedDoctor.name[0]}${this.selectedDoctor.last_name[0]}`
			}
			catch (e) {
				return ''
			}
		},
    },

    methods: {
        datepickerDateChanged (date) {
            this.$parent.currentDate = date
            const data = {
                min_date: date.toJSON().split('T')[0]
            }
            this.$store.dispatch('core/fetchSlots', {
                data,
                currentDate: date,
                bh: this.$parent.clinic.business_hours,
                vm: this.$parent
            })
        },
        getNextTimeFrame () {
            
            let min_date
            if (this.currentView === 'day') {
                min_date = this.$parent.currentDate.clone().addDays(1)
                this.$parent.currentDate = min_date
                // this.$parent.currentDate = this.$parent.currentDate.clone().addDays(1)
            }
            else {
                this.activeCalendar.next()
                min_date = this.activeCalendar.view.endDate
            }
			const data = {
				min_date: min_date.toJSON().split('T')[0]
            }
            if (this.currentView === 'month') return
            this.$store.dispatch('core/fetchSlots', {vm: this.$parent, data, bh: this.$parent.clinic.business_hours, currentDate: this.currentDate})
		},

		getPrevTimeFrame () {
            let min_date
            if (this.currentView === 'day') {
                min_date = this.$parent.currentDate.clone().addDays(-1)
                this.$parent.currentDate = min_date
            }
            else {
                this.activeCalendar.previous()
                min_date = this.activeCalendar.view.endDate
            }
			const data = {
                min_date: min_date.toJSON().split('T')[0]
            }
            if (this.currentView === 'month') return
            this.$store.dispatch('core/fetchSlots', {vm: this.$parent, data, bh: this.$parent.clinic.business_hours, currentDate: this.currentDate})
        },

        selectDoctor (doc) {
            this.selectedDoctor=doc
            document.body.click()
        },

        updateMonthlyPicker (ev) {
            const date = ev.endDate
			this.$store.dispatch('core/fetchMonthlyHeatmap', [date, this])
        }
    }
}
</script>

