<template>
<div class="bg-white" v-if="!disabled">
    <div class="loading-heatmap absolute top-0 w-full h-full" style="z-index: 50002" v-show="$store.state.core.heatMapLoading">
        <div class="flex justify-center h-full">
            <div class="spinner justify-center self-center">
                <div class="double-bounce1"></div>
                <div class="double-bounce2"></div>
            </div>
        </div>
    </div>
    <vue-cal 
        active-view="month"
        class="heatmap vuecal--blue-theme vuecal--rounded-theme"
        hide-view-selector
        small
        ref="cal"
        :disable-views="['years', 'year', 'week', 'day']"
        :events="events"
        :hide-weekdays="[7]"
        :locale="$i18n.locale"
        :min-date="minDate"
        :selected-date="date"
        :time="false"
        @cell-click="cellClicked"
        @view-change="viewChanged">
        <template #cell-content="{ cell, view, events }">
            <v-popover trigger="hover" v-if="events.length" class='month-picker-popover' popoverBaseClass="tooltip">
                <span 
                    :class="events.length && events[0].class"
                    class="h-full w-full rounded-full flex self-center items-center justify-center vuecal__cell-date heat">
                    {{ cell.content }}
                </span>
                <template slot="popover">
                    {{ hours(events[0].content) }}
                </template>
            </v-popover>
            <span v-else-if="!cell.outOfScope" class="vuecal__cell-date closed rounded-full h-full w-full">
                {{ cell.content }}
            </span>
        </template>
    </vue-cal>
</div>
</template>

<script>
export default {
    props: {
        set: {
            default: 'all'
        },
        date: {
            type: String
        },
        doctor: {
            default: null
        },
        minDate: {
            default: ''
        },
        disabled: {
            default: false
        }
    },
    computed: {
        events () {
            try {
                const date = this.localDate || this.date
                const ret = this.$store.state.core.monthlyHeatmapSlots[date][this.set]
                if (this.set === 'docs' && this.doctor) return ret[this.doctor]
                return ret
            }
            catch (e) {
                return []
            }
        }
    },
    data () {
        return {
            cells: [],
            localDate: null
        }
    },
    mounted () {
        window.month = this
    },
    methods: {
        cellClicked (ev) {
            if (!this.disabled) {
                this.$emit('dateSelected', ev)
            }
        },
        hours (val) {
            const hrs = val / 60
            if (hrs === 100) {
                return this.$t('misc.no_visits')
            }
            return `${Math.floor(hrs)} ` + this.$t('misc.hours')
        },
        viewChanged (ev) {
            if (!this.disabled) {
                this.$emit('timeFrameChanged', ev)
                this.localDate = ev.startDate.format('YYYY-MM')
            }
        }
    },
}
</script>

