		}
		@keyframes sk-bounce {
            0%, 100% { transform: scale(0.0) }
            50% { transform: scale(1.0) }
		}
		@keyframes sk-bounce {
            0%, 100% { 
                transform: scale(0.0);
            } 50% { 
                transform: scale(1.0);
            }
        }
	}
}
