import Vue from 'vue'

export default {
    async createBreak ({state, commit}, [event, timeStart, duration, vm]) {
        const {doctorID, start} = event
        const scheduleID = event.id
        const day = start.format('DD-MM')
        const end = timeStart.clone().addMinutes(duration)
        const startTime = timeStart.getTime()
        const endTime = end.getTime()
        const docBreaks = state.schedulerBreakSlots[doctorID] || []
        const sameDayBreaks = docBreaks.filter(item => item.start.format('DD-MM') === day)
        const gaps = sameDayBreaks.map(item => {
            return [item.start, item.end].map(datestr => (new Date(datestr).format('HH:mm')))
        })
        /* 
            validation block
        */
        // Overlaps with other breaks?
        const newSlotClearWithBreaks = sameDayBreaks.every(item => {
            const localStart = item.start.getTime()
            const localEnd = item.end.getTime()
            return (
                    (startTime < localStart && endTime <= localStart) || (startTime >= localEnd && endTime > localEnd)
            )
        })
        if (!newSlotClearWithBreaks) {
            return vm.notifyError(vm.$t('notifs.cannot_overlap'))
        }
        // Overlaps with appointment slots?
        const newSlotClearWithSlots = state.schedulerSlots[doctorID].filter(item => item.start.format('DD-MM') === day).every(item => {
           const localStart = item.start.getTime()
           const localEnd = item.end.getTime()
           return (
               (startTime < localStart && endTime <= localStart) || (startTime >= localEnd && endTime > localEnd)
           )
        })
        if (!newSlotClearWithSlots) {
           return vm.notifyError(vm.$t('overlapMessages.main'))
        }

        gaps.push([timeStart.format('HH:mm'), end.format('HH:mm')])
        const data = {
            select: {id: scheduleID},
            payload: {gaps}
        }
        const resp = await vm.requestResource('put_schedule', { vm, data })
        if (resp.status === 200) {
            const payload = {
                doctorID,
                scheduleID,
                end,
                class: "lunch",
                start: timeStart
            }
            commit('ADD_SCHEDULER_BREAK_SLOT', payload)
            vm.notifySuccess(vm.$t('notifs.new_schedule_break', {
                timeStart: timeStart.format('HH:mm')
            }))
            // vm.notifySuccess(`New schedule break added at <i>${timeStart.format('HH:mm')}</i>`)
        }
        else {
            vm.notifyError(vm.$t('notifs.new_schedule_break_failed'))
            // vm.notifyError('Failed to add a schedule break')
        }
    },

    async createSchedule ({state, commit}, [event, timeStart, duration, vm]) {
        const {doctorID} = event
        const midnightDate = timeStart.clone()
        midnightDate.setHours(0)
        midnightDate.setMinutes(0)
        const timeEnd = timeStart.clone().addMinutes(duration)
        const [dailyMin, dailyMax] = state.businessHours[midnightDate]

        if (timeStart < new Date()) {
            return vm.notifyError(vm.$t('notifs.no_schedules_in_past'))
        }

        if (timeStart < dailyMin) {
            return vm.notifyError(vm.$t('notifs.no_schedule_before_opening'))
        }
        if (timeEnd > dailyMax) {
            return vm.notifyError(vm.$t('notifs.no_schedule_after_closure'))
        }
        
        const data = {
            doctor: doctorID,
            date: timeStart.format('YYYY-MM-DD'),
            during: [
                timeStart.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`),
                timeEnd.format(`YYYY-MM-DD HH:mm${vm.timezoneOffset}`)
            ]
        }

        const resp = await vm.requestResource('post_schedule', { vm, data })
        if (resp.status === 200) {
            const scheduleID = resp.data.id
            vm.notifySuccess(vm.$t('notifs.new_schedule', {
                date: data.date
            }))
            commit('MAKE_SCHEDULE', {
                doctorID,
                id: scheduleID,
                class: "schedule",
                end: timeEnd,
                start: timeStart
            })
            vm.emitSchedulerUpdate()
        }
        else {
            vm.notifyError(vm.$t('notifs.new_schedule_failed'))
        }
    },

    async addNewProcedure ({commit}, [payload, vm]) {
        const {name, category} = payload
        delete payload.name
        delete payload.category
        const respTemplate = await vm.requestResource('post_proceduretemplate', {
            data: {
                name,
                category
            },
            vm,
            loadingIn: '.laima-form'
        })
        if (respTemplate.status === 200) {
            payload.template = respTemplate.data.code
            const respProc = await vm.requestResource('post_clinicprocedure', {
                data: payload,
                vm,
                loadingIn: '.laima-form'
            })
            if (respProc.status === 200) {
                payload.id = respProc.data.id
                payload.name = name
                payload.category = category
                payload.legacy = false
                payload.hidden = false
                commit('APPEND_CLINIC_PROCEDURE', [payload])
                vm.closeSideBar()
                return vm.notifySuccess(vm.$t('notifs.procedure_added', {name}))
            }
        }
        return vm.notifyError(vm.$t('notifs.procedure_added_failed'))
    }
}