<template>
	<div class="parentx">

		<vs-sidebar
			class="v-nav-menu items-no-padding"
			v-model="isVerticalNavMenuActive"
			ref="verticalNavMenu"
			default-index="-1"
			:click-not-close="clickNotClose"
			:reduce-not-rebound="reduceNotRebound"
			:parent="parent"
			:hiddenBackground="clickNotClose"
			:reduce="reduce"
			v-hammer:swipe.left="onSwipeLeft">

			<div @mouseenter="mouseEnter" @mouseleave="mouseLeave">

				<!-- Header -->
				<div class="header-sidebar flex items-end justify-between" slot="header">

					<!-- Logo -->
					<router-link tag="div" class="vx-logo cursor-pointer flex items-center" to="/">
						<img width="250" v-show="isMouseEnter || !reduce" src="@/assets/images/laima-logo-plain.png" alt="login" class="vx-logo-text absolute" style="top: -2px; left: -15px;">
						<img width="25" v-show="reduce && !isMouseEnter"  src="@/assets/images/laima-logo-symbol-only.png" class="vx-logo-text absolute top-0 mt-3 ml-1" />
					</router-link>
					<!-- /Logo -->

					<!-- Menu Buttons -->
					<div>
						<!-- Close Button -->
						<template v-if="showCloseButton">
							<feather-icon icon="XIcon" class="m-0 cursor-pointer" @click="$store.commit('TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE', false)" />
						</template>

						<!-- Toggle Buttons -->
						<template v-else-if="!showCloseButton && !verticalNavMenuItemsMin">
							<feather-icon
								id="btnVNavMenuMinToggler"
								class="mr-0 cursor-pointer"
								:icon="reduce ? 'CircleIcon' : 'DiscIcon'"
								svg-classes="stroke-current text-primary"
								@click="toggleReduce(!reduce)" />
						</template>
					</div>
					<!-- /Menu Toggle Buttons -->
				</div>
				<!-- /Header -->
				<!-- Header Shadow -->

				<!-- Menu Items -->
				<VuePerfectScrollbar ref="verticalNavMenuPs" class="scroll-area-v-nav-menu" :settings="settings" @ps-scroll-y="psSectionScroll" :key="$vs.rtl">
					<div class="sticky clinic-picker py-4 w-full top-0 g-primary text-white overflow-hidden" :class="showShadowBottom && !clinicSelectionOpen && 'bottom-shadow'">
						<div class="flex justify-start pl-4">
							<vs-avatar
								@click="clinics.length > 1 && (clinicSelectionOpen=!clinicSelectionOpen)"
								v-tooltip.bottom-end="activeWorkspace.name"
								size="large" color="white" 
								text-color="primary" 
								style="width: 40px!important; height: 40px!important;"
								class="mt-4 clinic-avatar absolute top-0  border-none shadow"
								:text="activeWorkspace.name" />
							<div class="ml-16 truncate" v-if="!isVerticalNavMenuReduced || !verticalNavMenuItemsMin">
								<div style="font-size: 1.05rem;" class="font-light text-white truncate">
									{{ activeWorkspace.name }}
								</div>
								<div class="text-sm font-light text-gray" style="color: #e2e8f0;">
									Logged in as <span class='font-medium'>{{ scope }}</span>
								</div>
							</div>
							<!-- <svg class="absolute w-8 right-0" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="#fff">
								<path fill-rule="evenodd" d="M10 3a1 1 0 01.707.293l3 3a1 1 0 01-1.414 1.414L10 5.414 7.707 7.707a1 1 0 01-1.414-1.414l3-3A1 1 0 0110 3zm-3.707 9.293a1 1 0 011.414 0L10 14.586l2.293-2.293a1 1 0 011.414 1.414l-3 3a1 1 0 01-1.414 0l-3-3a1 1 0 010-1.414z" clip-rule="evenodd" />
							</svg> -->
						</div>
					</div>
					<div class="w-full bg-white clinic-selection-wrap sticky"
						v-if="clinics.length > 1"
						:class="clinicSelectionOpen && ['active-clinic-picker', 'bottom-shadow']">
						<div v-for="clinic in clinics" :key="clinic">
							<div
								@click="selectedWorkspace=clinic"
								class="clinic-option text-base truncate pl-6 py-5 cursor-pointer"
								:style="{background: clinic == selectedWorkspace && '#eaf2f7'}" >
								{{ clinic }}
							</div>
						</div>
					</div>
					<div class="absolute top-0 w-full h-full mt-24 pl-4 pr-4">
						<template v-for="(item, index) in menuItemsUpdated">
							<!-- Group Header -->
							<span v-if="item.header && !verticalNavMenuItemsMin" class="navigation-header truncate" :key="`header-${index}`">
								{{ $t(item.i18n) }}
							</span>
							<!-- /Group Header -->

							<template v-else-if="!item.header">
								<!-- Nav-Item -->
								<v-nav-menu-item
									v-if="!item.submenu"
									:key="`item-${index}`"
									:index="index"
									:to="item.slug !== 'external' ? item.url : null"
									:href="item.slug === 'external' ? item.url : null"
									:icon="item.icon" :target="item.target"
									:isDisabled="item.isDisabled"
									:slug="item.slug">
										<span v-show="!verticalNavMenuItemsMin" class="truncate">{{ $t(item.i18n) }}</span>
										<vs-chip class="ml-auto" :color="item.tagColor" v-if="item.tag && (isMouseEnter || !reduce)">{{ item.tag }}</vs-chip>
								</v-nav-menu-item>

								<!-- Nav-Group -->
								<template v-else>
									<v-nav-menu-group
										:key="`group-${index}`"
										:openHover="openGroupHover"
										:group="item"
										:groupIndex="index"
										:open="isGroupActive(item)" />
								</template>
								<!-- /Nav-Group -->
							</template>
						</template>
					</div>
				</VuePerfectScrollbar>
				<!-- /Menu Items -->
			</div>
		</vs-sidebar>

		<!-- Swipe Gesture -->
		<div
			v-if="!isVerticalNavMenuActive"
			class="v-nav-menu-swipe-area"
			v-hammer:swipe.right="onSwipeAreaSwipeRight" />
		<!-- /Swipe Gesture -->
	</div>
</template>


<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import VNavMenuGroup from './VerticalNavMenuGroup.vue'
import VNavMenuItem from './VerticalNavMenuItem.vue'
// import {eventBus} from '@/main.js'

export default {
	name: 'v-nav-menu',
	components: {
		VNavMenuGroup,
		VNavMenuItem,
		VuePerfectScrollbar
	},
	props: {
		logo:             { type: String },
		openGroupHover:   { type: Boolean, default: false },
		parent:           { type: String },
		reduceNotRebound: { type: Boolean, default: true },
		navMenuItems:     { type: Array,   required: true },
		title:            { type: String },
	},
	data: () => ({
		clickNotClose       : false, // disable close navMenu on outside click
		isMouseEnter        : false,
		reduce              : false, // determines if navMenu is reduce - component property
		showCloseButton     : false, // show close button in smaller devices
		settings            : {      // perfectScrollbar settings
			maxScrollbarLength: 60,
			wheelSpeed        : 1,
			swipeEasing       : true
		},
		clinicSelectionOpen: false,
		showShadowBottom    : false,
	}),
	computed: {
		activeWorkspace () {
			return this.$store.state.auth.selectedWorkspace
		},
		clinics () {
			return this.$store.state.auth.availableWorkspaces.map(i => i.name)
		},
		scope () {
			let scope = this.$store.state.auth.actorContext.scope 
			scope = scope === 'Generic' ? 'Owner' : scope
			return scope 
		},
		selectedWorkspace: {
			get () {
				return this.activeWorkspace.name
			},
			set (val) {
				const workspace = this.$store.state.auth.availableWorkspaces.find(i => i.name == val)
				this.$store.dispatch('auth/changeWorkspace', workspace)
			}
		},
		isGroupActive() {
			return (item) => {
				const path        = this.$route.fullPath
				const routeParent = this.$route.meta ? this.$route.meta.parent : undefined
				let open          = false

				let func = (item) => {
					if (item.submenu) {
						item.submenu.forEach((item) => {
							if (item.url && (path === item.url || routeParent === item.slug)) { open = true }
							else if (item.submenu) { func(item) }
						})
					}
				}
				func(item)
				return open
			}
		},
		menuItemsUpdated() {
				let clone = this.navMenuItems.slice()

				for(let [index, item] of this.navMenuItems.entries()) {
					if (item.header && item.items.length && (index || 1)) {
						let i = clone.findIndex(ix => ix.header === item.header)
						for(let [subIndex, subItem] of item.items.entries()) {
							clone.splice(i + 1 + subIndex, 0, subItem)
						}
					}
				}

			const heldRoles = this.$store.state.auth.actorContext.roles
			return clone.filter(item => {
				if (heldRoles && item.roles) {
					for (let role of item.roles) {
							if (heldRoles.includes(role)) return true
						}
						return false
				}
				return true
			})
		},
		isVerticalNavMenuActive: {
			get()    { return this.$store.state.isVerticalNavMenuActive },
			set(val) { this.$store.commit('TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE', val) }
		},
		reduceButton: {
			get()    { return this.$store.state.reduceButton },
			set(val) { this.$store.commit('TOGGLE_REDUCE_BUTTON', val) }
		},
		isVerticalNavMenuReduced()   { return Boolean(this.reduce && this.reduceButton) },
		verticalNavMenuItemsMin() { return this.$store.state.verticalNavMenuItemsMin },
		windowWidth()     { return this.$store.state.windowWidth }
	},
	watch: {
		'$route'() {
			// if (this.isVerticalNavMenuActive && this.showCloseButton) {
			// 	this.$store.commit('TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE', false)
			// }
			this.reduceOnRoute()
		},
		reduce (val) {
			const verticalNavMenuWidth = val ? "reduced" : this.$store.state.windowWidth < 1200 ? "no-nav-menu" : "default"
			this.$store.dispatch('updateVerticalNavMenuWidth', verticalNavMenuWidth)
			setTimeout(function() {
				window.dispatchEvent(new Event('resize'))
			}, 100)
		},
		reduceButton() { this.setVerticalNavMenuWidth() },
		windowWidth()  { this.setVerticalNavMenuWidth() }
	},
	methods: {
		reduceOnRoute () {
			const reduceableRoutes = ['Scheduler', 'schedule-planner', 'visit-new']
			const isNarrow = this.$store.state.windowWidth < 1700
			if (reduceableRoutes.includes(this.$route.name) && isNarrow) {
				this.$store.dispatch('toggleNavbar', true)
			}
			else {
				this.$store.dispatch('toggleNavbar', false)
			}
		},
		onSwipeLeft() {
			if (this.isVerticalNavMenuActive && this.showCloseButton) this.isVerticalNavMenuActive = false
		},
		onSwipeAreaSwipeRight() {
			if (!this.isVerticalNavMenuActive && this.showCloseButton) this.isVerticalNavMenuActive = true
		},
		psSectionScroll() {
			this.showShadowBottom = this.$refs.verticalNavMenuPs.$el.scrollTop > 0 ? true : false
		},
		mouseEnter() {
			if (this.reduce) this.$store.commit('UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN', false)
			this.isMouseEnter = true
		},
		mouseLeave() {
			if (this.reduce) this.$store.commit('UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN', true)
			this.isMouseEnter = false;
		},
		setVerticalNavMenuWidth() {
			if(this.windowWidth > 1200) {
				// Set reduce
				this.reduce = this.reduceButton ? true : false

				// Open NavMenu
				this.$store.commit('TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE', true)

				// Set Menu Items Only Icon Mode
				// const verticalNavMenuItemsMin = (this.reduceButton && !this.isMouseEnter) ? true : false
				// this.$store.commit('UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN', verticalNavMenuItemsMin)

				// Menu Action buttons
				this.clickNotClose   = true
				this.showCloseButton = false

				const verticalNavMenuWidth   = this.isVerticalNavMenuReduced ? "reduced" : "default"
				this.$store.dispatch('updateVerticalNavMenuWidth', verticalNavMenuWidth)

				return
			}
			// Close NavMenu
			// this.$store.commit('TOGGLE_IS_VERTICAL_NAV_MENU_ACTIVE', false)
			// Reduce button
			if (this.reduceButton) this.reduce = false

			// Menu Action buttons
			this.showCloseButton = true
			this.clickNotClose   = false

			// Update NavMenu Width
			this.$store.dispatch('updateVerticalNavMenuWidth', 'no-nav-menu')

			// Remove Only Icon in Menu
			this.$store.commit('UPDATE_VERTICAL_NAV_MENU_ITEMS_MIN', false)
		},
		toggleReduce (val) {
			this.reduceButton = val
			this.setVerticalNavMenuWidth()
		},
	},

	mounted () {
		this.setVerticalNavMenuWidth()
		this.reduceOnRoute()
	},
}

</script>


<style lang="scss">
@import "@/assets/scss/vuexy/components/verticalNavMenu.scss";
@import "@/assets/scss/vuexy/_variables.scss";
.clinic-option {
	&:hover {
		background: #eaf2f7;
	}
}
.clinic-picker {
	min-height: 68px;
	z-index: 50001;
	border-bottom: 1px solid rgba(#fff, .2);
	background-color: $primary !important;
}
.bottom-shadow {
	box-shadow: 0 2px 23px #4f95b8;
}
.clinic-selection-wrap {
	transition: transform .3s ease-out;
	transform: translateY(-200%);
	top: 69px;
    z-index: 10000;
	
	&.active-clinic-picker {
		transform: translateY(0);
	}
	.active-clinic-option {
		width: 5px;
		height: 5px;
		background: $primary;
		left: 8px;
		margin-top: 17px;
	}
}

</style>
