<template>
<div class="laima-text-switch relative rounded-lg overflow-hidden" :class="sizeClass[size]">
    <div class="marker absolute z-0 h-full" :style="markerStyle"></div>
    <ul class="toggle-switch h-full flex p-0 m-0 list-none rounded-lg border-solid border" :style="ulStyling">
        <li class="text-center cursor-pointer z-10 h-full flex"
            v-for="(option, idx) in options" :key="option.val"
            :style="lineStyle(option.val, idx)"
            @click="!disabled && $emit('input', option.val)"
            :class="value === option.val && 'active' || 'standard'">
            <div class="self-center text-center w-full">
                {{ option.label }}
            </div>
        </li>
    </ul>
</div>
</template>
<script>
export default {
    props: {
        inversed: {
            type: Boolean,
            default: false
        },
        options: {
            type: [Array, Object],
            default () {
                return []
            }
        },
        disabled: {
            default: false
        },
        value: {
            type: String
        },
        color: {
            type: String
        },
        size: {
            type: String,
            default: 'default'
        }
    },
    data () {
        return {
            sizeClass: {
                default: 'normal-size',
                small: 'small-size',
                big: 'big-size'
            }
        }
    },
    methods: {
        lineStyle (option, idx) {
            const borders = {}
            const extra = {}
            const lastIndex = this.options.length - 1
            let color = this.color || 'rgba(var(--vs-primary))'
            if (this.inversed) {
                extra['color'] = `${this.color}`
            }
            if (this.options.length > 2) {
                borders['border-left'] = (idx > 1 && idx != lastIndex) && `1px solid ${color}` || 'none'
                borders['border-right'] = (!idx || idx != lastIndex) && `1px solid ${color}` || 'none'
            }
            return {
                width: (100 / this.options.length) + '%',
                ...borders,
                ...extra
            }
        },
    },
    computed: {
        ulStyling () {
            const themeStyle = {}
            if (this.color) {
                if (this.inversed) {
                    themeStyle['background'] = this.color
                    themeStyle['color'] = '#fff'
                    themeStyle['border-color'] = this.color
                }
                else {
                    themeStyle['border-color'] = this.color
                    themeStyle['color'] = this.color
                }
            }
            return themeStyle
        },
        markerStyle () {
            const unit = 100 / this.options.length
            const themeStyle = {}
            if (this.color) {
                themeStyle.background = this.inversed && '#fff' || this.color
            }
            return {
                width: unit + '%',
                left: unit * this.activeIndex + '%',
                ...themeStyle
            }
        },
        activeIndex () {
            return this.options.findIndex(i => i.val === this.value)
        }
    }
}
</script>
<style lang="scss">
@import "@/assets/scss/vuexy/_variables.scss";

.laima-text-switch {
    background: #fff;
    height: 2.35rem;
    .marker {
        background: $primary;
        transition: left .15s cubic-bezier(1, 1, 0.22, .1);
    }
    .toggle-switch {
        background: transparent;
        border-color: $primary;
        color: $primary;
        li {
            height: 2.35rem;
            &.standard {
                color: #fff !important;
            }
        }
    }
    &.small-size {
        height: 1.9rem !important;
        font-size: 12px;
        .toggle-switch {
            li {
                height: 1.9rem;
            }
        }
    }
    &.big-size {
        height: 3rem !important;
        font-size: 14px;
        .toggle-switch {
            li {
                height: 3rem;
            }
        }
    }
    &.danger {
        .marker {
            background: $danger;
        }
        .toggle-switch {
            border-color: $danger;
            color: $danger;
        }
    }
    &.success {
        .marker {
            background: $success;
        }
        .toggle-switch {
            border-color: $success;
            color: $success;
        }
    }
    &.warning {
        .marker {
            background: $warning;
        }
        .toggle-switch {
            border-color: $warning;
            color: $warning;
        }
    }
}
</style>