<template>

    <div class="clinic-edit">

        <laima-form formScope="step-1"
                    :title="$t('headers.basics')"
                    @updated="onUpdated"
                    :modelsContainer="editedClinic"
                    :formFields="basicFields" >
        </laima-form> 
        <laima-form formScope="step-2"
                    :title="$t('headers.contact')"
                    class="mt-8"
                    @updated="onUpdated"
                    :modelsContainer="editedClinic"
                    :formFields="contactFields" />
        <laima-form formScope="step-3"
                    ref="add"
                    :title="$t('fields.address')"
                    class="mt-8"
                    @updated="onUpdated"
                    :modelsContainer="editedClinic"
                    :formFields="addressFields">
        </laima-form>
        <laima-form formScope="step-4"
                    title="Legal"
                    class="mt-8"
                    @updated="onUpdated"
                    :modelsContainer="editedClinic"
                    :formFields="legalFields" />
    </div>
</template>

<script>
import CountryFlag from 'vue-country-flag'

export default {
    components: {
        CountryFlag,
        
    },
    mounted () {
        window.clinics = this
        if (this.$store.state.core.clinics[0]) this.parseClinics()
    },
    computed: {
        inEdit () {
            return !!Object.entries(this.editedClinic)[0]
        },
        // clinic () {

        // },
        clinicID () {
            return this.$store.state.auth.selectedWorkspace.id
        },
        basicFields () {
            return [
                {
                    name: 'name',
                    type: 'text',
                    label: this.$t('fields.clinic_name'),
                    placeholder: this.$t('placeholders.clinic_name'),
                    validators: { required: true },
                    submitters: ['blur', 'enter']
                },
                {
                    name: 'description',
                    label:  this.$t('fields.clinic_desc'),
                    placeholder: this.$t('placeholders.clinic_desc'),
                    type: 'textarea',
                    submitters: ['blur']
                },
                {
                    name: 'office_count',
                    type: 'number',
                    label: this.$t('fields.office_count'),
                    validators: {required: true, numeric: true, min_value: 1},
                    submitters: ['blur', 'enter']
                }
            ]
        },
        contactFields ()  {
            return [
                {
                    name: 'phone',
                    type: 'tel',
                    label: this.$t('fields.clinic_phone'),
                    placeholder: this.$t('placeholders.phone'),
                    validators: { required: true },
                    submitters: ['blur', 'enter', 'debounce']
                },
                {
                    name: 'email',
                    type: 'email',
                    label: this.$t('fields.clinic_email'),
                    validators: { required: true, email: true },
                    placeholder: this.$t('placeholders.email'),
                    submitters: ['blur', 'enter']
                }
            ]
        },
        addressFields ()  {
            return [
                {
                    name: 'address1',
                    label: this.$t('fields.address'),
                    type: 'text',
                    validators: { required: true },
                    placeholder: this.$t('placeholders.address'),
                    submitters: ['blur', 'enter']
                },
                {
                    name: 'country',
                    label: this.$t('headers.country'),
                    type: 'select',
                    options: this.countries,
                    validators: { required: true },
                    submitters: ['change']
                },
                {
                    name: 'zipcode',
                    label: this.$t('fields.zipcode'),
                    type: 'text',
                    validators: { required: true },
                    placeholder: this.$t('placeholders.zipcode'),
                    submitters: ['blur', 'enter']
                },
                {
                    name: 'city',
                    label: this.$t('fields.city'),
                    type: 'text',
                    validators: { required: true },
                    placeholder: this.$t('placeholders.city'),
                    submitters: ['blur', 'enter']
                },
            ]
        },
        legalFields () {
            return [
                {
                    name: 'tax_id',
                    type: 'text',
                    label: this.$t('fields.tax_id'),
                    required: true,
                    placeholder: '123345678',
                    validators: {
                        required: true,
                        numeric: true,
                        length: 10
                    },
                    submitters: ['blur', 'enter']
                },
            ]
        }
    },

    data () {
        return {
            origClinic: null,
            editedClinic: {}
        }
    },
    
    methods: {

        parseClinics () {
            const clinics = this.$store.state.core.clinics
            const clinic = clinics.find(item => item.id == this.clinicID)
            this.editedClinic = this.$clone(clinic)
            this.origWorkspaceName = clinic.name
        },
        
        async onUpdated ([payload, callback]) {
            const clinic = this.$store.state.core.clinics.find(item => item.id == this.clinicID)
            if (payload.name && this.origWorkspaceName !== payload.name) {
                let workspacePayload = {
                    select: {
                        id: clinic.id
                    },
                    payload
                }
                let workspaceUpdateResponse = await this.requestResource('patch_workspace', {vm: this, data: workspacePayload})
                if (workspaceUpdateResponse.status === 200) {
                    this.$store.commit('core/PATCH_CLINIC', {
                        clinicID: this.clinicID,
                        payload
                    })
                    const actorInfo = JSON.parse(window.localStorage.getItem('app.laima.actorInfo'))
                    actorInfo.workspaces[clinic.id] = payload.name
                    this.$store.dispatch('auth/setActorContext', [actorInfo, 'no-meta', this])
                    callback()
                }
                return
            }
            this.$store.dispatch('core/editClinic', [this, this.editedClinic.id, payload, callback])
        }
    },

    watch: {
        '$store.state.core.clinics' (val) {
            if (val) {
                this.parseClinics()
            }
        }
    }
}
</script>

