<template>
    <div class="vx-col ">
        <statistics-card-line
            hideChart
            class="mb-base"
            icon="SmileIcon"
            :statisticTitle="message"
            :statistic="$t('headers.pass_reset')"
            color='success' />
    </div>
</template>

<script>
import StatisticsCardLine from '@/components/statistics-cards/StatisticsCardLine.vue'
export default {
    components: {
        StatisticsCardLine
    },
    computed: {
        message () {
            let email = this.$store.state.auth.passResetEmail
            email = email ? email : this.$t('misc.your_address')
            return this.$t('headers.reset_pass_email_sent', {email})
        }
    }
}

</script>