<template>
<div class="time-range flex justify-around" :class="$attrs.disabled && 'disabled'">
    <div class='range-side relative'>
        <span class="field-validation">
            <v-popover
                v-show="!!leftErrors"
                popoverBaseClass="tooltip"
                popoverArrowClass="border-danger tooltip-arrow popover-arrow"
                popoverInnerClass="bg-danger rounded-full text-white tracking-normal text-sm p-1 px-2"
                ref="tooltipl"
                trigger="click">
                    <feather-icon class="text-danger animated pulse delay-1s infinite" icon="AlertCircleIcon" />
                    <template slot="popover">
                        {{ leftErrors }}
                    </template>
            </v-popover>
        </span>
        <lm-input
            autocomplete="off"
            name="from_hour"
            type="time"
            v-model="value[0]"
            :disabled="$attrs.disabled"
            @input="$emit('input', [$event, value[1]])"
            :danger="!!leftErrors"
            iconBefore="ClockIcon">
            <template #labelBefore>
                <span>From</span>
            </template>
        </lm-input>
    </div>
    <div class='range-side relative'>
        <span class="field-validation">
            <v-popover
                v-show="!!rightErrors"
                popoverBaseClass="tooltip"
                popoverArrowClass="border-danger tooltip-arrow popover-arrow"
                popoverInnerClass="bg-danger rounded-full text-white tracking-normal text-sm p-1 px-2"
                ref="tooltipr"
                trigger="click">
                    <feather-icon class="text-danger animated pulse delay-1s infinite" icon="AlertCircleIcon" />
                    <template slot="popover">
                        {{ rightErrors }}
                    </template>
            </v-popover>
        </span>
        <lm-input
            autocomplete="off"
            :label="$t(fields.to_hour)"
            name="to_hour"
            type="time"
            v-model="value[1]"
            :disabled="$attrs.disabled"
            @input="$emit('input', [value[0], $event])"
            :danger="!!rightErrors"
            iconBefore="ClockIcon">
            <template #labelBefore>
                <span>{{ $t('headers.to') }} </span>
            </template>
        </lm-input>
    </div>
</div>
</template>

<script>
export default {
    inheritAttrs: false,
    props: {
        value: {
            default: () => [],
            type: Array
        },
        errorsArray: {
            default: () => ['', ''],
            type: Array
        }
    },
    computed: {
        leftErrors () {
            return this.errorsArray[0]
        },
        rightErrors () {
            return this.errorsArray[1]
        }
    },
    mounted () {
        window.ooo = this
    },
    methods: {

    },
    watch: {
        'errorsArray' () {
            this.$nextTick(() => {
                if (this.leftErrors) {
                    this.$refs.tooltipl.show()
                }
                else {
                    this.$refs.tooltipl.hide()
                }
                if (this.rightErrors) {
                    this.$refs.tooltipr.show()
                }
                else {
                    this.$refs.tooltipr.hide()
                }
            })
        }
    }
}
</script>

<style lang="scss">
.time-range {
    &.disabled {
        filter: saturate(0);
        opacity: .3;
    }
    .field-validation {
        position: absolute;
        left: -26px;
        top: 9px;
    }
}
</style>